import React, { Component } from 'react'

import { Tabs, Tab } from 'react-bootstrap';
import { connect } from 'react-redux';
// import { getMerchantActivityList } from '../actions/index'
import { history} from './../../../history'
import RenderDataTable from '../../components/dataTable';
class Bookings extends Component {
  navigateEditBooking = (row) => {
    history.push(`/merchant/dashboard/bookingDetails/${row.bookingId}`);
  }

  render() {


    const columns = [
      {
        name: "Date Purchased",
        selector: "datePurchased",
        sortable: true,
      },
      {
        name: "Booking ID",
        selector: "bookingId",
        sortable: true
      },
      {
        name: "Activity",
        selector: "activity",
        sortable: false,

      },
      {
        name: "Outlet",
        selector: "outlet",
        sortable: false,
      },
      {
        name: "Deal Type",
        selector: "dealType",
        sortable: false,
      },
      {
        name: "Reservation Date",
        selector: "reservationDate",
        sortable: false,
      },
      {
        name: "Expiry Date",
        selector: "expiryDate",
        sortable: true,
      },
      {
        name: "Price (SGD)",
        selector: "price",
        sortable: true,
      },
      {
        name: "Qty",
        selector: "quantity",
        sortable: true,
      },


      // {
      //     name: "Actions",
      //     selector: "actions",
      //     sortable: false,

      // },

    ];


    return (


      <Tabs defaultActiveKey="activeBookings" >
        <Tab eventKey="activeBookings" title="Active Bookings">
          <RenderDataTable
            onRowClicked={this.navigateEditBooking}
            columns={columns}
            data={this.props.activitiesList || []}
            defaultSortField="datePurchased"
          />
        </Tab>
        <Tab eventKey="archievedBookings" title="Archieved Bookings">
          <RenderDataTable
            onRowClicked={this.navigateEditBooking}
            columns={columns}
            data={this.props.activitiesList || []}
            defaultSortField="datePurchased"
          />
        </Tab>
      </Tabs>



    )
  }
}
const mapStateToProps = () => {
  return {
    activitiesList: [
      {
        datePurchased: "24-Dec-2019", bookingId: 'xx-yy-zz', activity: 'Archiery', outlet: 'West coast', dealType: 'Regular Deal', reservationDate: '21-Dec-2019', expiryDate: '21-Jan-2020', price: 45.21, quantity: 21
      },
      {
        datePurchased: "21-Dec-2019", bookingId: 'xx-yy-zz', activity: 'Paragliding', outlet: 'East coast', dealType: 'Promotion', reservationDate: '18-Dec-2019', expiryDate: '21-Feb-2020', price: 11.11, quantity: 2
      },
      {
        datePurchased: "24-Dec-2019", bookingId: 'xx-yy-zz', activity: 'Archiery', outlet: 'West coast', dealType: 'Regular Deal', reservationDate: '21-Dec-2019', expiryDate: '21-Jan-2020', price: 123.11, quantity: 12
      }
    ]
  };
}
export default connect(mapStateToProps, null)(Bookings)
