import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Row, Col, Card, Tabs, Tab, Form } from 'react-bootstrap';
import { ValidationForm } from 'react-bootstrap4-form-validation';
import validator from 'validator';
import { RenderTextField, RenderPhoneField, RenderEmailField, RenderBootstrapSingleSelect } from '../../../components/commonUIComponents';
import Datetime from 'react-datetime';
import PatnerDetails from './patnerDetails';
import CoupleInfo from './coupleInfo';
import Diary from './diary';
import validations from '../../../validations';


class EditUser extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isActive: true,
            activeModaltext: "Inactive",
            showModal: false,
            selectedOutlet: null
        }
    }
    handleSubmitTandC = (event) => {
        event.preventDefault();

    }
    handleSubmitOverview = (event) => {
        event.preventDefault();

    }
    handleSubmitDeals = (event) => {
        event.preventDefault();

    }
    toggleHandler = (event) => {
        event.preventDefault();
        this.setState({
            isActive: !this.state.isActive,
            showModal: !this.state.showModal
        }, () => {
            if (this.state.isActive === true) {
                this.setState({
                    activeModaltext: "Inactive"
                })
            } else {
                this.setState({
                    activeModaltext: "Active"
                })
            }
        })
    };

    openModal = () => {
        this.setState({
            showModal: true
        })
    }

    modalClosed = () => {
        this.setState({
            showModal: false
        })
    }
    changeActivity = () => {
        this.setState({
            selectedOutlet: null
        });
    }
    changeOutlet = (input) => {
        this.setState({
            selectedOutlet: input
        })

    }
    handleSubmit = (event) => { event.preventDefault() }
    render() {
        const defaultSwitch = true
        const toggleHandler = (d) => {
            return !d
        };
        return (
            <>
                {/* <Row>
          <Col md={12}>
            <Card>

              <Card.Body >
                <span className="float-right"><i className="card-icon feather icon-edit-2 f-24" /></span>
              </Card.Body>
            </Card>


          </Col>
        </Row> */}

                <Tabs defaultActiveKey="contact"  variant="pills"  className="mb-3 ">
                    <Tab eventKey="contact" title="User Detail">

                        <Card className="box-border">
                            <Card.Header>
                                <Card.Title as="h5"> Personal Details</Card.Title>
                            </Card.Header>
                            <Card.Body >
                                <ValidationForm onSubmit={this.handleSubmit}>
                                    <Row>
                                        <Col md={6}>
                                            <Form.Group>
                                                <Form.Label>Name</Form.Label>
                                                <Row>
                                                    <Col md={6}>
                                                        <RenderTextField
                                                            name="firstname"
                                                            placeholder="First Name"
                                                            maxLength={18}
                                                            errorMessage={{ validator: validations.required }}
                                                            validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                                        />
                                                    </Col>
                                                    <Col md={6}>
                                                        <RenderTextField
                                                            name="lastname"
                                                            placeholder="Last Name"
                                                            maxLength={18}
                                                            errorMessage={{ validator: validations.required }}
                                                            validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                                        />
                                                    </Col>
                                                </Row>

                                            </Form.Group>
                                        </Col>
                                        <Col md="6">
                                            <Form.Group>
                                                <Form.Label>Country</Form.Label>
                                                <RenderTextField
                                                    name="country"
                                                    placeholder="Country"
                                                    maxLength={18}
                                                    errorMessage={{ validator: validations.required }}
                                                    validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                                />
                                            </Form.Group>
                                        </Col>
                                        <Col md="6">
                                            <Form.Group>
                                                <Form.Label>Mobile Number</Form.Label>
                                                <RenderPhoneField
                                                    name="contactNumberForSparks"
                                                    validator={(value) => ((/[1-9]{3}-[0-9]{4}-[0-9]{1}/).test(value))}
                                                    errorMessage={{ validator: "Invalid contact number" }}

                                                />
                                            </Form.Group>
                                        </Col>
                                        <Col md="6">
                                            <Form.Group>
                                                <Form.Label>DOB</Form.Label>
                                                <Datetime timeFormat={false} dateFormat={"DD MMM YYYY"} inputProps={{ placeholder: 'Select Date' }} />

                                            </Form.Group>
                                        </Col>
                                        <Col md="6">
                                            <Form.Group>
                                                <Form.Label>Email</Form.Label>
                                                <RenderEmailField
                                                    name="contactEmailForSparks"
                                                    validator={validator.isEmail}
                                                    errorMessage={{ validator: 'Invalid email' }}
                                                />
                                            </Form.Group>
                                        </Col>
                                        <Col md="6">
                                            <Form.Group>
                                                <Form.Label>Gender</Form.Label>
                                                <RenderBootstrapSingleSelect
                                                    name="gender"
                                                    options={[
                                                        { value: 'male', label: 'Male' },
                                                        { value: 'female', label: 'Female' },
                                                        { value: 'others', label: 'Others' },
                                                    ]}
                                                />
                                            </Form.Group>
                                        </Col>

                                        <Col md="6">
                                            <Form.Group>
                                                <Form.Label>Status</Form.Label>
                                                <RenderBootstrapSingleSelect
                                                    name="status"
                                                    options={[
                                                        { value: 'active', label: 'Active' },
                                                        { value: 'blocked', label: 'Blocked' },
                                                    ]}
                                                />
                                            </Form.Group>
                                        </Col>
                                        <Col md="6">
                                            <Form.Group>
                                                <Form.Label>Created Date</Form.Label>
                                                <Datetime timeFormat={false} dateFormat={"DD MMM YYYY"} inputProps={{ placeholder: 'Select Date' }} />

                                            </Form.Group>
                                        </Col>
                                        <Col md="6">
                                            <Form.Group>
                                                <Form.Label>Social Account</Form.Label>
                                                {/* <Datetime timeFormat={false} dateFormat={"DD MMM YYYY"} inputProps={{ placeholder: 'Select Date' }} /> */}
                                                <Row>
                                                    <Col md="3" className="font-30" >
                                                        <i className="feather icon-twitter"></i>
                                                    </Col>
                                                    <Col md="3" className="font-30">
                                                        <i className="feather icon-facebook"></i>
                                                    </Col>
                                                    <Col md="3" className="font-30">
                                                        <i className="feather icon-instagram"></i>
                                                    </Col>
                                                    <Col md="3" className="font-30">
                                                        <i className="feather icon-mail"></i>
                                                    </Col>
                                                </Row>
                                            </Form.Group>
                                        </Col>
                                        <Col md="6">
                                            <Form.Group>
                                                <Form.Label>Last Session</Form.Label>
                                                <Datetime timeFormat={false} dateFormat={"DD MMM YYYY"} inputProps={{ placeholder: 'Select Date' }} />
                                            </Form.Group>
                                        </Col>
                                    </Row>
                                    <Row>
                                        {/* <Col md="6">
                    <SubmitButton className="btn btn-primary shadow-2 mt-4">
                      Save
                          </SubmitButton>
                  </Col> */}
                                    </Row>
                                </ValidationForm>
                            </Card.Body>
                        </Card>
                        <Card className="box-border">
                            <Card.Header>
                                <Card.Title as="h5">App Preferences</Card.Title>
                            </Card.Header>
                            <Card.Body>
                                <ValidationForm onSubmit={this.handleSubmit}>
                                    <Row>
                                        <Col md="6">
                                            <Form.Group>
                                                <Form.Label>Privacy</Form.Label>
                                                <RenderBootstrapSingleSelect
                                                    name="privacy"
                                                    options={[
                                                        { value: 'private', label: 'Private' },
                                                        { value: 'public', label: 'Public' },
                                                    ]}
                                                />

                                            </Form.Group>
                                        </Col>
                                        <Col md="6">
                                            <Form.Group>
                                                <Form.Label>Activity Categories</Form.Label><br></br>
                                                <button key='cyc' type="btn" disabled className="category-btn" >Cycling</button>
                                                <button key='bike' type="btn" disabled className="category-btn" >Biking</button>
                                                <button key='hike' type="btn" disabled className="category-btn" >Hiking</button>
                                            </Form.Group>
                                        </Col>
                                        <Col md="6">
                                            <Row>
                                                <Col md="12">
                                                    <Form.Group>
                                                        <Form.Label>Latest Promotions and Recomendations</Form.Label>
                                                        <div className="switch d-inline m-r-10 padding-left-15">
                                                            <Form.Control type="checkbox" id="checked-default" defaultChecked={defaultSwitch} onChange={() => toggleHandler(defaultSwitch)} />
                                                            <Form.Label htmlFor="checked-default" className="cr" />
                                                        </div>
                                                    </Form.Group>
                                                </Col>
                                                <Col md="12">
                                                    <Form.Group>
                                                        <Form.Label>Reminders</Form.Label>
                                                        <div className="switch d-inline m-r-10 padding-left-15">
                                                            <Form.Control type="checkbox" id="checked-default" defaultChecked={defaultSwitch} onChange={() => toggleHandler(defaultSwitch)} />
                                                            <Form.Label htmlFor="checked-default" className="cr" />
                                                        </div>
                                                    </Form.Group>
                                                </Col>
                                                <Col md="12">
                                                    <Form.Group>
                                                        <Form.Label>Default Export Calender</Form.Label>
                                                        <RenderBootstrapSingleSelect
                                                            name="export"
                                                            options={[
                                                                { value: 'googledrive', label: 'Google Drive' },
                                                            ]}
                                                        />
                                                    </Form.Group>
                                                </Col>
                                                <Col md="12">
                                                    <Form.Group>
                                                        <Form.Label>Default Payment</Form.Label>
                                                        <RenderBootstrapSingleSelect
                                                            name="export"
                                                            options={[
                                                                { value: 'googledrive', label: 'Google Drive' },

                                                            ]}
                                                        />
                                                    </Form.Group>
                                                </Col>
                                            </Row>

                                        </Col>
                                    </Row>
                                    <Row>
                                        {/* <Col md="6">
                      <SubmitButton className="btn btn-primary shadow-2 mt-4">
                        Save
                          </SubmitButton>
                    </Col> */}
                                    </Row>
                                </ValidationForm>
                            </Card.Body>
                        </Card>

                    </Tab>

                    {/* <Tab eventKey="activtyDetails" title="Activty Details">
                       <patnerDetails handleSubmitOverview={this.handleSubmitOverview} /> =
                    </Tab> */}

                    <Tab eventKey="patnerDetail" title="Patner Detail">
                    <PatnerDetails handleSubmitOverview={this.handleSubmitOverview} />

                    </Tab>
                    <Tab eventKey="coupleInfo" title="Couple Info">
                    <CoupleInfo handleSubmitOverview={this.handleSubmitOverview}></CoupleInfo>
                        {/* <Deals handleSubmitDeals={this.handleSubmitDeals} /> */}
                    </Tab>
                    <Tab eventKey="booking" title="Bookings">
                        {/* <ReviewsScreen /> */}
                    </Tab>
                    <Tab eventKey="friends" title="Friends">
                        {/* <TermsAndConditions handleSubmitTandC={this.handleSubmitTandC} /> */}
                    </Tab>
                    <Tab eventKey="favourites" title="Favourites">
                        {/* <TermsAndConditions handleSubmitTandC={this.handleSubmitTandC} /> */}
                    </Tab>
                    <Tab eventKey="diary" title="Diary">
                    <Diary handleSubmitOverview={this.handleSubmitOverview}></Diary>
                        {/* <TermsAndConditions handleSubmitTandC={this.handleSubmitTandC} /> */}
                    </Tab>
                </Tabs>
            </>

        )
    }
}
const mapStateToProps = () => {
    const Rating = (prop) => {
        return <div> {prop.data} <i className='feather icon-star-on'></i></div>;
    };
    return {
        catergoryOptions: [
            { label: 'Sports', value: 'sports' },
            { label: 'Adventure', value: 'adventure' },
            { label: 'Racing', value: "racing" },
            { label: 'Dance', value: 'dance' },
            { label: 'Others', value: 'others' }
        ],
        typeOptions: [
            // {label: 'Free', value: 'free' },
            { label: 'General', value: 'general' },
            { label: 'Specialized', value: "specialized" }
        ],
        earnedOptions: [
            { label: 'A', value: 'a' },
            { label: 'B', value: 'b' },
            { label: 'C', value: "c" },
            { label: 'D', value: 'd' },
            { label: 'E', value: 'e' },
            { label: 'F', value: "f" },
            { label: 'G', value: 'g' },
            { label: 'H', value: 'h' },
            { label: 'I', value: "i" },
            { label: 'J', value: 'j' },
            { label: 'K', value: 'k' },
            { label: 'L', value: "l" },
            { label: 'M', value: 'm' },
            { label: 'N', value: "n" },
            { label: 'O', value: 'o' },
            { label: 'P', value: 'p' },
            { label: 'Q', value: "q" },
            { label: 'R', value: 'r' },
            { label: 'S', value: 's' },
            { label: 'T', value: "t" },
            { label: 'U', value: 'u' },
            { label: 'V', value: 'v' },
            { label: 'W', value: "w" },
            { label: 'X', value: 'x' },
            { label: 'Y', value: 'y' },
            { label: 'Z', value: "x" }
        ],
        statusOptions: [
            { label: 'Active', value: '1', isSelected: true },
            { label: 'Inactive', value: '0', isSelected: false },
        ],
        merchantActivitiesList: [
            { rank: "1", name: "A1", price: "From 22", booked: "2", status: "Active", rating: <Rating data="4.8" />, },
            { rank: "2", name: "B1", price: "$22", booked: "4", status: "Active", rating: <Rating data="4.0" />, },
            { rank: "3", name: "C1", price: "$20", booked: "12", status: "Active", rating: <Rating data="3.8" />, },
            { rank: "4", name: "D1", price: "From 10", booked: "1", status: "Inactive", rating: <Rating data="2.0" />, },
        ],
        patnerList: [
            { rank: "1", name: "A1", price: "From 22", booked: "2", status: "Active", rating: <Rating data="4.8" />, },
            { rank: "2", name: "B1", price: "$22", booked: "4", status: "Active", rating: <Rating data="4.0" />, },
            { rank: "3", name: "C1", price: "$20", booked: "12", status: "Active", rating: <Rating data="3.8" />, },
            { rank: "4", name: "D1", price: "From 10", booked: "1", status: "Inactive", rating: <Rating data="2.0" />, },
        ],
        activityOptions: [
            { value: 'act1', label: 'Activity 1' },
            { value: 'act2', label: 'Activity 2' },
            { value: 'act3', label: 'Activity 3' },
            { value: 'act4', label: 'Activity 4' },
        ],
        outletOptions: [
            { value: 'outlet1', label: 'Outlet 1' },
            { value: 'outlet2', label: 'Outlet 2' },
            { value: 'outlet3', label: 'Outlet 3' },
            { value: 'outlet4', label: 'Outlet 4' },
        ]
    };
}


export default connect(mapStateToProps, null)(EditUser)
