import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Row, Col, Form, Card } from 'react-bootstrap';
import { RenderTextField, RenderValidationDateField, RenderTextArea, SuccessButton, DeleteButton, RenderPrefixedPriceField,showInfoMsg } from '../../../components/commonUIComponents';
import { ValidationForm } from 'react-bootstrap4-form-validation';
import Datetime from 'react-datetime';
import validator from 'validator';
import { getRequestDetailsById,ProccedRequest } from '../actions/index';
import validations from '../../../validations';
import moment from 'moment';
class ActiveFeatureRequest extends Component {
  constructor(props) {
    super(props);
    this.state = {
      buttonClicked: '',
      merchant:'',
      activity:'',
      letterCollected:'',
      pcategorey:'',
      scategorey:'',
      startDate:'',
      endDate:'',
      notes:'',
      price:'',
      prefix:'',
      rejectDate:new Date(),
      adminNote:'',
      primaryCategory: false,
      secondaryCategory: false,
      letter: false,
      adminNotes: ""
    }
  }
  componentDidMount() {
    let requestId = window.location.pathname.split('/').pop();
    if (requestId && requestId !== 'add') {
      this.props.getRequestDetailsById(requestId);
    }
  }
  componentDidUpdate(prevProps, newState) {
    
    if (this.props.requestDetail && this.props.requestDetail !== prevProps.requestDetail) {
      let sDate = moment(this.props.requestDetail.startDate).format('YYYY-MM-DD H:mm:ss');
      let eDate = moment(this.props.requestDetail.endDate).format('YYYY-MM-DD H:mm:ss');
      this.setState({
        merchant: this.props.requestDetail.merchantName,
        activity: this.props.requestDetail.activityName,
        letterCollected: this.props.requestDetail.letterCollected,
        pcategorey: this.props.requestDetail.categories && this.props.requestDetail.categories[0] && this.props.requestDetail.categories[0].category_name ,
        scategorey: this.props.requestDetail.categories && this.props.requestDetail.categories[1] && this.props.requestDetail.categories[1].category_name ,
        startDate: sDate,
        endDate: eDate,
        notes: this.props.requestDetail.notes,
        price: this.props.requestDetail.price && this.props.requestDetail.price.price,
        prefix: this.props.requestDetail.price && this.props.requestDetail.price.currency,
        primaryCategory: this.props.requestDetail.primaryCategory?this.props.requestDetail.primaryCategory:false,
        secondaryCategory: this.props.requestDetail.secondaryCategory?this.props.requestDetail.secondaryCategory:false,
        letter: this.props.requestDetail.letter?this.props.requestDetail.letter:false,
        adminNotes: this.props.requestDetail.adminNotes
        
      })
    }
  }
  Accept =() =>{
    this.setState({
      buttonClicked : 'accept'
    })
  }
  acceptSubmmit = () => {
    showInfoMsg('Are you Sure To Accept this Request?', () => this.acceptRequest('1'))
  }
  acceptRequest =(e)=>{
    let sd = new Date(this.state.startDate);
    console.log(this.state.startDate)
    let payload ={
      "status":e,
      "id":this.props.requestDetail.featuredId,
      "primaryCategory": this.state.primaryCategory,
      "secondaryCategory": this.state.secondaryCategory,
      "letter": this.state.letter,
      "adminNotes": this.state.adminNotes,
      "startDate":moment(sd).format('YYYY-MM-DD H:mm:ss'),
      "endDate":moment(this.state.endDate).format('YYYY-MM-DD H:mm:ss'),
    }
    this.props.ProccedRequest(payload)
  }
  reject = () => {
    this.setState({
      buttonClicked : 'reject'
    })
  }
  rejectSubmit =() =>{
    showInfoMsg('Are you Sure To Reject this Request?', () => this.rejectRequest('0'))
  }
  
  rejectRequest =(e)=>{
    let payload ={
      "status":e,
      "id":this.props.requestDetail.featuredId,
      "primaryCategory": this.state.primaryCategory,
      "secondaryCategory": this.state.secondaryCategory,
      "letter": this.state.letter,
      "adminNotes": this.state.adminNotes,
      "startDate":moment(this.state.startDate).format('YYYY-MM-DD H:mm:ss'),
      "endDate":moment(this.state.endDate).format('YYYY-MM-DD H:mm:ss'),
    }
    this.props.ProccedRequest(payload)
  }
  handleStartDateChange = (e) =>{
    this.setState({
        startDate:e._d,
        endDate:moment(e._d).add(14, 'days'),
    })
  }
  handleEndDateChange = (e) =>{
      this.setState({
          endDate:e._d
      })
  }
  validationStartDate = (currentDate) => {
    // console.log(currentDate)
    return currentDate.isAfter(moment().add(7, 'days'));
  };
  validationEndDate = (currentDate) => {
      
      return currentDate.isAfter(moment(this.state.startDate).add(13, 'days'));
      
  };
  submit =()=>{
    console.log("done")
  }
  render() {
    return (
      <Card>
        <Card.Body>

          <Row>
            <Col md={12}>
              <h5>Pending Feature Request</h5>
              <hr />
            </Col>
          </Row>
          <ValidationForm onSubmit={this.submit}>
            <Row>

              <Col md={6}>
                <Form.Group as={Row} >
                  <Form.Label column sm={3}>
                    Merchant
                           </Form.Label>
                  <Col sm={9}>
                    <RenderTextField
                      name="merchant"
                      value={this.state.merchant}
                      disabled={true}
                    />
                  </Col>
                </Form.Group>

                <Form.Group as={Row}>
                  <Form.Label column sm={3}>
                    Activity
                  </Form.Label>
                  <Col sm={9}>
                    <RenderTextField name="activity"                    
                    value={this.state.activity}
                    disabled={true} />
                  </Col>
                </Form.Group>
                <Form.Group as={Row}>
                  <Form.Label column sm={3}>
                    Letter
                  </Form.Label>
                  <Col sm={9}>
                    <RenderTextField name="letterCollected"
                    value={this.state.letterCollected}
                    disabled={true} />
                  </Col>
                </Form.Group>
                <Form.Group as={Row}>
                  <Form.Label column sm={3}>
                    Primary Category
                  </Form.Label>
                  <Col sm={9}>
                    <RenderTextField name="pcategorey"
                    value={this.state.pcategorey}
                    disabled={true} />
                  </Col>
                </Form.Group><Form.Group as={Row}>
                <Form.Label column sm={3}>
                  Secondary Category
                </Form.Label>
                <Col sm={9}>
                  <RenderTextField name="scategorey"
                  value={this.state.scategorey}
                  disabled={true} />
                </Col>
              </Form.Group>

              </Col>


              <Col md={6}>
                <Form.Group as={Row} >
                  <Form.Label column sm={3}>
                    Request Start Date
                  </Form.Label>
                  <Col sm={9}>
                  <RenderTextField name="startDate"
                    value={this.state.startDate}
                    disabled={true} />
                  </Col>
                </Form.Group>

                <Form.Group as={Row} >
                  <Form.Label column sm={3}>
                    Request End Date
                  </Form.Label>
                  <Col sm={9}>
                  <RenderTextField name="endDate"
                  value={this.state.endDate}
                  disabled={true} />
                  </Col>
                </Form.Group>
                <Form.Group as={Row} >
                  <Form.Label column sm={3}>
                    Notes
                  </Form.Label>
                  <Col sm={9}>
                    <RenderTextArea name="notes"
                    value={this.state.notes}
                    disabled={true} 
                    // errorMessage={{ validator: validations.required }}
                    // validator={(value) => !isNaN(new Date(value).getTime())}
                    />
                  </Col>
                </Form.Group>
              </Col>


            </Row>
            {this.props.requestDetail && this.props.requestDetail.status === '2' &&
            <Row>
              <Col md={6}>
                <Form.Group as={Row}>
                  <Col sm={6}>
                    <SuccessButton onClick={this.Accept}>Accept</SuccessButton>
                    <DeleteButton onClick={this.reject} className="ml-3">Reject</DeleteButton>
                  </Col>
                </Form.Group>
              </Col>

            </Row>
            } 
          </ValidationForm>
          {this.state.buttonClicked === 'accept' ?
            <Card className="box-border">
              <Card.Header>
                <Card.Title as="h5">Accepting Feature Request</Card.Title>
              </Card.Header>
              <Card.Body>
                <ValidationForm onSubmit={this.acceptSubmmit}>
                  <Row>
                    <Col md={6}>
                      <Col sm={12}>
                        <h5>User Audience</h5>
                        <hr />
                      </Col>
                      <Col md={12}>
                        <Form.Group as={Row} >
                          <Form.Label column sm={5}>
                            Activity Category-Primary
                      </Form.Label>
                          <Col sm={5} className="category-btn-col">
                            <button type="btn" disabled className="category-btn-rectangle" >{this.state.pcategorey}</button>
                          </Col>
                          <Col sm={2}>
                            <Form.Group>
                              <div className="switch d-inline">
                                <Form.Control type="checkbox" id="primaryCategory" checked={this.state.primaryCategory} />
                                <Form.Label htmlFor="primaryCategory" className="cr" />
                              </div>
                            </Form.Group>
                          </Col>

                        </Form.Group>
                      </Col>
                      <Col md={12}>
                        <Form.Group as={Row} >
                          <Form.Label column sm={5}>
                            Activity Category-Secondary
                      </Form.Label>
                          <Col sm={5} className="category-btn-col">
                            <button type="btn" disabled className="category-btn-rectangle" >{this.state.scategorey}</button>
                          </Col>
                          <Col sm={2}>
                            <Form.Group>
                              <div className="switch d-inline">
                                <Form.Control type="checkbox" id="secondaryCategory" checked={this.state.secondaryCategory}/>
                                <Form.Label htmlFor="secondaryCategory" className="cr" />
                              </div>
                            </Form.Group>
                          </Col>

                        </Form.Group>
                      </Col>
                      <Col md={12}>
                        <Form.Group as={Row} >
                          <Form.Label column sm={5}>
                            Leter
                      </Form.Label>
                          <Col sm={5} className="category-btn-col">
                            <button type="btn" disabled className="category-btn-rectangle" >{this.state.letterCollected}</button>
                          </Col>
                          <Col sm={2}>
                            <Form.Group>
                              <div className="switch d-inline">
                                <Form.Control type="checkbox" id="letter" checked={this.state.letter} />
                                <Form.Label htmlFor="letter" className="cr" />
                              </div>
                            </Form.Group>
                          </Col>

                        </Form.Group>
                      </Col>

                    </Col>
                    <Col md={6}>
                      <Col sm={12}><h5>Actual Live Dates</h5>
                        <hr />
                      </Col>
                      <Col sm={12}>
                        <Form.Group as={Row} >
                          <Form.Label column sm={3}>
                            Start Date
                        </Form.Label>
                          <Col sm={9}>
                            <Datetime name="startDate"
                              errorMessage={{ validator: validations.required }}
                              validator={(value) => !isNaN(new Date(value).getTime())}
                              value={this.state.startDate}
                              timeFormat={false} 
                              onChange={this.handleStartDateChange} dateFormat={"YYYY-MM-DD H:mm:ss"} inputProps={{ placeholder: 'Select Date' }} />
              
                          </Col>
                        </Form.Group>
                      </Col>
                      <Col sm={12}>
                        <Form.Group as={Row} >
                          <Form.Label column sm={3}>
                            End Date
                        </Form.Label>
                          <Col sm={9}>
                          <Datetime name="endDate"
                              errorMessage={{ validator: validations.required }}
                              validator={(value) => !isNaN(new Date(value).getTime())}
                              value={this.state.endDate}
                              timeFormat={false} onChange={this.handleEndDateChange} dateFormat={"YYYY-MM-DD H:mm:ss"} inputProps={{ placeholder: 'Select Date' }} />
              
                            
                          </Col>
                        </Form.Group>
                      </Col>
                      <Col sm={12}><h5>Other Details</h5>
                        <hr />
                      </Col>
                      <Col sm={12}>
                        <Form.Group as={Row} >
                          <Form.Label column sm={3}>
                            Price
                        </Form.Label>
                          <Col sm={9}>
                          <RenderPrefixedPriceField
                          name="price"
                          placeholder="Discounted Amount"
                          disabled={true} 
                          validator={validator.isFloat}
                          prefix={this.state.prefix?this.state.prefix:'SGD'}
                          value={this.state.price?this.state.price:''}
                          errorMessage={{ validator: 'Invalid price' }}
                        />
                          </Col>
                        </Form.Group>
                      </Col>
                      <Col sm={12}>
                        <Form.Group as={Row} >
                          <Form.Label column sm={3}>
                            Admin Notes
                        </Form.Label>
                          <Col sm={9}>
                            <RenderTextArea
                              name='adminNotes'
                              value={this.state.adminNotes}
                            />
                          </Col>
                        </Form.Group>
                      </Col>


                    </Col>
                  </Row>
                  <Row >
                    <Col sm={12}>
                      <SuccessButton onClick={this.acceptSubmmit}>Submit</SuccessButton>
                    </Col>
                  </Row>

                </ValidationForm>
              </Card.Body>
            </Card> : null}
          {this.state.buttonClicked === 'reject' ?
            <Card className="box-border">
              <Card.Header>
                <Card.Title as="h5">Rejecting Feature Request</Card.Title>
              </Card.Header>
              <Card.Body>
                <ValidationForm onSubmit={this.rejectSubmit}>
                  <Row>
                    <Col sm={12}>
                      <Form.Group as={Row} >
                        <Form.Label column sm={3}>
                          Date Rejected
                        </Form.Label>
                        <Col sm={9}>
                        <Datetime name="rejectDate"
                        errorMessage={{ validator: validations.required }}
                        validator={(value) => !isNaN(new Date(value).getTime())}
                        value={this.state.rejectDate}
                        timeFormat={false} onChange={this.handleStartDateChange} dateFormat={"YYYY-MM-DD H:mm:ss"} inputProps={{ placeholder: 'Select Date' }} />
        
                        </Col>
                      </Form.Group>
                    </Col>
                    <Col sm={12}>
                      <Form.Group as={Row} >
                        <Form.Label column sm={3}>
                          Reason
                        </Form.Label>
                        <Col sm={9}>
                          <RenderTextArea
                            name='rejectionReason'
                            value={this.state.adminNotes}
                          />
                        </Col>
                      </Form.Group>
                    </Col>
                  </Row>

                  <Row >
                    <Col sm={12}>
                      <SuccessButton onClick={this.rejectSubmit}>Submit</SuccessButton>
                    </Col>
                  </Row>

                </ValidationForm>
              </Card.Body>
            </Card> : null}
        </Card.Body>
      </Card >
    )
  }
}
const mapStateToProps = (state) => {
  console.log(state.admin)
  return {
    requestDetail:state.admin.requestDetail && state.admin.requestDetail.data && state.admin.requestDetail.data.data[0] 
  }
}
export default connect(mapStateToProps, {getRequestDetailsById,ProccedRequest})(ActiveFeatureRequest)
