import React, { Component } from 'react'
import RenderDataTable from '../../../components/dataTable'
import { Row, Col, Card } from 'react-bootstrap';
import { connect } from 'react-redux';
import { getMerchantList } from '../actions/index'
import { history } from '../../../../history';
import { capitalizeFirstChar } from '../../../utilActions';
import moment from 'moment';
import {DataTable} from 'primereact/datatable';
import { TreeTable } from 'primereact/treetable';
import { InputText } from 'primereact/inputtext';
import { Column } from "primereact/column";
import DEMO from '../../../../store/constant';

class MerchantActivityManagement extends Component {
  constructor(props) {
    super(props);
    this.state = {
      sortColumn: 'merchantName',
      sortDirection: 'ASC',
      recordLimit: 10,
      recordOffset: 0,
      search:'',      
      globalFilter: null,
      merchantList:[]
    }
  }

  componentDidMount() {
    console.log("hello")
    this.loadData()
  }
  componentDidUpdate(prevProps) {
    if(prevProps.merchantList !== this.props.merchantList){
      let files = [];
        for (let i = 0; i < this.props.merchantList.length; i++) {
            let mainCurrency = this.props.merchantList[i].price && this.props.merchantList[i].price.length > 0 &&  this.props.merchantList[i].price[0].currency ?this.props.merchantList[i].price[0].currency:'SGD';
            let mainFrom = this.props.merchantList[i].price && this.props.merchantList[i].price.length > 0 && this.props.merchantList[i].price[0].totalDeals && this.props.merchantList[i].price[0].totalDeals >1 ?'From':'';
           
            let node = {
                    merchantname: this.props.merchantList[i].merchantname,
                    activity: this.props.merchantList[i].activity &&  this.props.merchantList[i].activity.title,
                    merchantActivityId:this.props.merchantList[i].merchantActivtyId,
                    category: this.props.merchantList[i].activity &&  this.props.merchantList[i].activity.activitycategories && this.props.merchantList[i].activity.activitycategories.length >0 && this.props.merchantList[i].activity.activitycategories.map((cat, index) => <button key={index} type="btn" disabled className="category-btn" >{capitalizeFirstChar(cat.category_name)}</button>),
                    price: this.props.merchantList[i].price &&  this.props.merchantList[i].price.length > 0 &&  this.props.merchantList[i].price[0].price ?((this.props.merchantList[i].price[0].price % 1 === 0)? `${mainFrom} ${mainCurrency} ${this.props.merchantList[i].price[0].price}` :`${mainFrom} ${mainCurrency} ${(Math.round(this.props.merchantList[i].price[0].price * 100) / 100).toFixed(2)}`):null,
                    created: this.props.merchantList[i].created ? moment(this.props.merchantList[i].created).format("DD MMM YYYY") : null,
                    merchantId:this.props.merchantList[i].merchantId,
                    status:<a href={DEMO.BLANK_LINK} className={`label border-round ${this.props.merchantList[i].status === 1 ? "green-bg" : 'red-bg'} f-12 text-white`}>{this.props.merchantList[i].status===1 ? "Active" : "Inactive"}</a>
                  }

            files.push(node);
        }
      this.setState({merchantList:files})
    }
  }
  loadData() {
    let searchObj =
    {
      limit: this.state.recordLimit,
      offset: this.state.recordOffset,
      orderBy: this.state.sortColumn,
      order: this.state.sortDirection,
      search: this.state.search
    }
    this.props.getMerchantList(searchObj);
  }
  handleSearch = (text)=>{
    this.setState({
      search:text
    }, () => this.loadData())
  }
  handleSorting = (column, sortDirection) => {
    this.setState({
      sortColumn: column.selector,
      sortDirection: sortDirection.toUpperCase()
    }, () => this.loadData())
  }
  handlePageChange = (pageNumber) => {
    this.setState((prevState) => ({
      recordOffset: (pageNumber - 1) * prevState.recordLimit,
    }), () => this.loadData())

  }
  onChangeRecordsLimit = (event) => {
    this.setState({
      recordLimit: event.target.value && parseInt(event.target.value, 10)
    }, () => this.loadData())
  }
  numCheck = (num) => {
    
      return num % 1 === 0;
   
  }
  navigateAddMerchant = () => {
    history.push('/admin/dashboard/merchant/add')
  }
  navigateEditMerchant = (event) => {
    let ids = event.value
    console.log(event)
    if(ids.merchantActivityId){
    history.push(`/admin/dashboard/merchant/${ids.merchantId}/${ids.merchantActivityId}`)
    
    // history.push(`/admin/dashboard/merchant/${item.merchantId}/0`)
    }else{
      
    history.push(`/admin/dashboard/merchant/${ids.merchantId}/0`)
    }
  }
  render() {

    const columns = [
      {
        name: "Merchant",
        selector: "merchantname",
        sortable: true,
        width: '30%'

      },
      {
        name: "Activity",
        selector: "activity",
        sortable: false,
        width: '15%'
      },
      {
        name: "Category",
        selector: "category",
        sortable: false,
        width: '20%'

      },
      {
        name: "Price",
        selector: "price",
        sortable: false,
        width: '15%',
      },
      {
        name: "Created",
        selector: "created",
        sortable: false,

      },
      {
        name: "Status",
        selector: "status",
        format: (row) => {
          return <a href={DEMO.BLANK_LINK} className={`label d-block ${row.status === 1 ? "green-bg" : 'red-bg'} f-12 text-white`}>{row.status===1 ? "Active" : "Inactive"}</a>
        },
        sortable: true,
        width: '5%'
      },
    ];
    let header = <div style={{'textAlign':'right'}}>
                   <InputText type="search" className="table-search" onInput={(e) => this.setState({globalFilter: e.target.value})} placeholder="Search" size="20"/>
                  </div>;
    // if(this.props.merchantList.length > 0 && this.props.merchantList.length === this.state.prevLength){
    return (
      <Row>
        <Col>
          <Card>
            <Card.Header>
              <Card.Title as="h5">Merchants</Card.Title>
              <i title="Add Merchant" className="f-28 text-c-green float-right feather icon-plus" onClick={this.navigateAddMerchant}></i>
            </Card.Header>
            <Card.Body>
            <DataTable value={this.state.merchantList} paginator={true} 
            currentPageReportTemplate="Showing {first} to {last} of {totalRecords} entries"
            paginatorTemplate="CurrentPageReport  PrevPageLink PageLinks NextPageLink   RowsPerPageDropdown"
             header={header}  rowsPerPageOptions={[10,20,30,40]} rows={10 } 
             globalFilter={this.state.globalFilter}
             selectionMode="single"
             onSelectionChange={this.navigateEditMerchant} sortMode="multiple" defaultSortOrder={-1}>
                <Column  style={{width:'25%'}} field="merchantname" header="Merchant"  sortable ></Column>
                <Column style={{width:'15%'}}  field="activity" header="Activity" sortable></Column>
                <Column style={{width:'20%'}} field="category" header="Category"></Column>
                <Column style={{width:'13%'}} field="price" header="Price" sortable></Column>
                <Column style={{width:'13%'}} field="created" header="Created" sortable></Column>
                <Column style={{width:'14%'}} field="status" header="Status" sortable></Column>
            </DataTable>
            </Card.Body>
          </Card>
        </Col>
      </Row>


    )
    // }else{
    //   return(
    //     <div></div>
    //   )
    // }
  }
}
const mapStateToProps = (state) => {
  


  let merchantList = [];
  let dataLength = 0;
  let totalRecords =0;
  if (state.admin.merchantList && state.admin.merchantList.data && state.admin.merchantList.data.data) {
    console.log(state.admin.merchantList.data.data)
    merchantList = state.admin.merchantList.data.data.map((item, index) => {

      return {
        id: index, 
        merchantname: capitalizeFirstChar(item.merchantName),
        activity: item.activity,
        created: item.createdAt,
        price:item.price,
        merchantId:item.merchantId,
        status:item.status,
        merchantActivtyId:item.merchantActivtyId
      }
    })
    dataLength = state.admin.merchantList.data.data.length;
    totalRecords = state.admin.merchantList.data.totalCount;
    
  }
  
  return {
    dataLength:dataLength,
    merchantList: merchantList,
    totalRecords: totalRecords

  } 
  
}
export default connect(mapStateToProps, { getMerchantList })(MerchantActivityManagement)