import React, { Component } from 'react'
import RenderDataTable from '../../../components/dataTable'
import { Row, Col, Card } from 'react-bootstrap';
import { connect } from 'react-redux';
import { getActivityList , clearActivityList} from '../actions/index'
import { history } from './../../../../history'
import { capitalizeFirstChar } from '../../../utilActions';

import DEMO from '../../../../store/constant';

class MerchantActivityManagement extends Component {
  constructor(props) {
    super(props);
    this.state = {
      sortColumn: 'title',
      sortDirection: 'ASC',
      recordLimit: 10,
      recordOffset: 0,
      search:'',
      pervState:false,
      prevLength:10,
      activityList:[]
    }
  }
  loadData = () => {
    let searchObj = 
    { 
      limit: this.state.recordLimit,
      offset: this.state.recordOffset,
      orderBy: this.state.sortColumn,
      order: this.state.sortDirection,
      search:this.state.search
    }

    this.props.getActivityList(searchObj);
  }
  componentDidMount() {
    this.loadData();
  }
  componentDidUpdate(prevProps) {
    if(prevProps.activityList !== this.props.activityList){
      this.setState({
        activityList:this.props.activityList
      })
    }
  }
  componentWillUnmount(){
    this.props.clearActivityList()
  }
  handleSorting = (column, sortDirection, event) => {
    this.setState({
      sortColumn: column.selector,
      sortDirection: sortDirection.toUpperCase()
    }, () => this.loadData())


  }
  handleSearch = (text)=>{
    this.setState({
      search:text
    }, () => this.loadData())
  }
  handlePageChange = (pageNumber) => {
    this.setState((prevState) => ({
      recordOffset: (pageNumber - 1) * prevState.recordLimit,
    }), () => this.loadData())

  }
  navigateAddActivity = () => {
    history.push('/admin/dashboard/activity/add');
  }
  navigateEditActivity = (row) => {
    history.push(`/admin/dashboard/activity/${row.id}`);
  }
  onChangeRecordsLimit = (event) => {
    this.setState({
      recordLimit: event.target.value && parseInt(event.target.value, 10)
    }, () => this.loadData())
  }
  numCheck = (num) => {
    
    return num % 1 === 0;
 
}
  render() {
    const columns = [
      {
        name: "Name",
        selector: "title",
        sortable: true,      
        width: "20%"
      },
      {
        name: "Category",
        selector: "category",
        sortable: false,
        minWidth: "195px",        
        width: "20%"
      },
      {
        name: "No. of Merchants",
        selector: "merchantCount",
        sortable: false,
        width: "10%"
      },
      {
        name: "Type",
        selector: "type",
        sortable: false,
        width: "15%"
      },
      {
        name: "Price",
        selector: "lowestPrice",
        // format: (row) => row.lowestPrice &&  `SGD ${Math.round(parseFloat(row.lowestPrice))}`,
        format:(row)=>row.lowestPrice?(this.numCheck(row.lowestPrice)? `${row.priceFrom} ${row.currency} ${row.lowestPrice}` :`${row.priceFrom} ${row.currency} ${(Math.round(row.lowestPrice * 100) / 100).toFixed(2)}`):'',
        sortable: false,
        width: "15%"
      },
      {
        name: "Best Seller",
        selector: "bestSeller",
        sortable: false,
        width: "10%"
      },
      {
        name: "Status",
        selector: "status",
        format: (row) => {
          return <a href={DEMO.BLANK_LINK} className={`label d-block ${row.status === 1 ? "green-bg" : 'red-bg'} f-12 text-white`}>{row.status===1 ? "Active" : "Inactive"}</a>
        },
        sortable: true,
        width: "10%"
      },
      
    ];
    // if(this.props.activityList.length > 0 && this.props.activityList.length === this.state.prevLength){
    //   console.log("list",this.props.activityList)
  //  if(!this.props.loading){
    return (
      <>
        <Row>
          <Col>
            <Card>
              <Card.Header>
                <Card.Title as="h5">Activities</Card.Title>
                <i title="Add Activity" className="f-28 text-c-green float-right feather icon-plus" onClick={() => this.navigateAddActivity()}></i>
              </Card.Header>
              <Card.Body>
                <RenderDataTable
                  onRowClicked={this.navigateEditActivity}
                  columns={columns}
                  data={this.state.activityList}
                  defaultSortField={this.state.sortColumn}
                  filterTextSearch={this.state.search}
                  sortServer
                  totalRecords={this.props.totalRecords}
                  dataLength ={this.props.dataLength}
                  recordLimit={this.state.recordLimit}
                  recordOffset={this.state.recordOffset}
                  onChangeRecordsLimit={this.onChangeRecordsLimit}
                  handlePageChange={this.handlePageChange}
                  onSort={this.handleSorting}
                  handleSearch={this.handleSearch}
                />
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </>
    )
    // }else{
    //   console.log("list2",this.props.activityList.length,this.state.prevLength)
    //   return(
    //     <div></div>
    //   )
    // }
  }
}
const mapStateToProps = (state) => {
  console.log(state.admin)
  

  let activityList = [];
  let dataLength = 10;
  let totalRecords =0;

  if (state.admin.activityList && state.admin.activityList.data) {
    const BestSeller = (prop) => {
      return <div>
        {/* <Form.Label>Regular Deal 1</Form.Label> */}
        <button type="btn" className="label  f-12 text-white status-btn best-seller">Yes</button>
      </div>
    }
    activityList = state.admin.activityList.data.map((item, index) => {
      return {
        id: item.id,
        title: item.title,
        category: item.activityCategories.map((cat, index) => <button key={index} type="btn" disabled className="category-btn" >{capitalizeFirstChar(cat.name)}</button>),
        merchantCount: item.merchantCount.length >0 && item.merchantCount[0].merchountCount,
        type: capitalizeFirstChar(item.activityType==='general'?'Generalized':(item.activityType==='special'?'Specialized':item.activityType)),
        lowestPrice: item.price ? item.price.price : null,
        currency: item.price ? (item.price.currency? item.price.currency:'SGD') : 'SGD',
        priceFrom:item.price && item.price.totalDeals && item.price.totalDeals >1?'From':'',
        bestSeller: item.isBestSeller ? <BestSeller /> : '',
        status:item.status
      }
    })
    dataLength =state.admin.activityList.data.length;
    totalRecords = state.admin.activityList.totalCount;
    
  }
  return {
    dataLength:dataLength,
    activityList: activityList,
    totalRecords: totalRecords,
    loading: state.admin.loading

  } 
}
export default connect(mapStateToProps, { getActivityList , clearActivityList})(MerchantActivityManagement)
