import React from 'react'
// import { SubmitButton } from '../../../components/commonUIComponents';
import { ValidationForm } from 'react-bootstrap4-form-validation';
import { Row, Col, Form } from 'react-bootstrap';
import moment from 'moment'

export default function Deals(props) {
  console.log(props)
  return (
    <ValidationForm onSubmit={props.handleSubmitDeals}>

      <Row>
        {props.regularDeals.length ? props.regularDeals.map((item,index) => {
          
          return <Col md={6} key={index}>
            <Col md={12}>
              <Form.Group >
                <Form.Label ><h5>Regular Deal {index+1}</h5></Form.Label>
                {item && item.status === 1 ?
                  <button type="btn"className="green-bg rounded-btn">Active</button> :
                <button type="btn" className="red-bg rounded-btn">Not Active</button>}
              </Form.Group>
            </Col>
            
            <Col md={12}>
              <Form.Group as={Row} controlId="formHorizontalTitle1">
                <Form.Label column sm={4}>Title</Form.Label>
                <Col sm="8">
                  <Form.Control plaintext readOnly value={item && item.title} />
                </Col>
              </Form.Group>
            </Col>
           
            <Col md={12}>
              <Form.Group as={Row} controlId="formHorizontalDesc1">
                <Form.Label column sm={4}>Description</Form.Label>
                <Col sm="8">
                  <Form.Control plaintext readOnly value={item && item.description} />
                </Col>
              </Form.Group>
            </Col>
           
            <Col md={12}>

              <Form.Group as={Row} controlId="formHorizontalOrigPrice">
                <Form.Label column sm={4}>Original Price</Form.Label>
                <Col sm="8">
                  <Form.Control plaintext readOnly value={item && item.originalPrice && `${item.currency?item.currency:'SGD'} ${item.originalPrice}`} />

                </Col>
              </Form.Group>
            </Col>
          
            <Col md={12} >
              <Form.Group as={Row} controlId="formHorizontalDiscPrice">
                <Form.Label column sm={4}>Discounted Price</Form.Label>
                <Col sm="8">
                  <Form.Control plaintext readOnly value={item && item.discountedPrice&& `${item.currency?item.currency:'SGD'} ${item.discountedPrice}`} />
                </Col>
              </Form.Group>
            </Col>
           
            <Col md={12}>
              <Form.Group as={Row} controlId="formHorizontalDiscAmt">
                <Form.Label column sm={4}>Discounted Amount</Form.Label>
                <Col sm="8">
                  <Form.Control plaintext readOnly value={item && item.discountAmount && `${item.currency?item.currency:'SGD'} ${item.discountAmount}`}/>
                </Col>
              </Form.Group>
            </Col>
           
            <Col md={12}>
              <Form.Group as={Row} controlId="formHorizontalDiscPer">
                <Form.Label column sm={4}>Discounted Percentage</Form.Label>
                <Col sm="8">
                  <Form.Control plaintext readOnly value={item && item.discountedPercentage} />
                </Col>
              </Form.Group>
            </Col>
       
            <Col md={12}>
              <Form.Group as={Row} controlId="formHorizontaldate">
                <Form.Label column sm={4}>Date Created</Form.Label>
                <Col sm="8">
                  <Form.Control plaintext readOnly value={item &&  moment(item.createdAt).format("DD MMM YYYY") } />
                </Col>
              </Form.Group>
            </Col>

            
            <Col md={12}>
              <Form.Group as={Row} controlId="formHorizontalDatemodi">
                <Form.Label column sm={4}>Date Modified</Form.Label>
                <Col sm="8">
                  <Form.Control plaintext readOnly value={item && moment(item.updatedAt).format("DD MMM YYYY")} />
                </Col>
              </Form.Group>
            </Col>

          </Col>
        }):<h3>No Deals Available</h3>}

      </Row>
      <Row>
        {/* <Col md={12}>
          <SubmitButton className="btn btn-primary shadow-2 mt-4">
            Submit
                    </SubmitButton>
        </Col> */}
      </Row>
    </ValidationForm>

  )
}
