import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Row, Col, Form, Card } from 'react-bootstrap';
import { RenderTextField, RenderValidationDateField, RenderTextArea, SuccessButton, DeleteButton, RenderPrefixedPriceField,showInfoMsg } from '../../../components/commonUIComponents';
import { ValidationForm } from 'react-bootstrap4-form-validation';
import Datetime from 'react-datetime';
import validator from 'validator';
import { getRequestDetailsById,ProccedRequest } from '../actions/index';
import validations from '../../../validations';
import moment from 'moment';
class ViewFeatureRequest extends Component {
  constructor(props) {
    super(props);
    this.state = {
      buttonClicked: '',
      merchant:'',
      activity:'',
      letter:'',
      pcategorey:'',
      scategorey:'',
      startDate:'',
      endDate:'',
      notes:'',
      price:'',
      prefix:'',
      rejectDate:new Date(),
      adminNote:'',
      status: '0',
      uniqueUserView:'0',
      conversion:'0',
      conversionPercent:'0'
    }
  }
  componentDidMount() {
    let requestId = window.location.pathname.split('/').pop();
    if (requestId && requestId !== 'add') {
      this.props.getRequestDetailsById(requestId);
    }
  }
  componentDidUpdate(prevProps, newState) {
    
    if (this.props.requestDetail && this.props.requestDetail !== prevProps.requestDetail) {
      let sDate = moment(this.props.requestDetail.startDate).format('DD/MM/YYYY');
      let eDate = moment(this.props.requestDetail.endDate).format('DD/MM/YYYY');
      this.setState({
        merchant: this.props.requestDetail.merchantName,
        activity: this.props.requestDetail.activityName,
        letter: this.props.requestDetail.letterCollected,
        pcategorey: this.props.requestDetail.categories && this.props.requestDetail.categories[0] && this.props.requestDetail.categories[0].category_name ,
        scategorey: this.props.requestDetail.categories && this.props.requestDetail.categories[1] && this.props.requestDetail.categories[1].category_name ,
        startDate: sDate,
        endDate: eDate,
        notes: this.props.requestDetail.notes,
        price: this.props.requestDetail.price && this.props.requestDetail.price.price,
        prefix: this.props.requestDetail.price && this.props.requestDetail.price.currency,
        status:this.props.requestDetail.status,
        uniqueUserView:'400',
        conversion:'100',
        conversionPercent:(100/400)*100
      })
    }
  }
  Accept =() =>{
    this.setState({
      buttonClicked : 'accept'
    })
  }
  acceptSubmmit = () => {
    showInfoMsg('Are you Sure To Accept this Request?', () => this.acceptRequest('1'))
  }
  acceptRequest =(e)=>{
    let payload ={
      "status":e,
      "id":this.props.requestDetail.featuredId
    }
    this.props.ProccedRequest(payload)
  }
  reject = () => {
    this.setState({
      buttonClicked : 'reject'
    })
  }
  rejectSubmit =() =>{
    showInfoMsg('Are you Sure To Reject this Request?', () => this.rejectRequest('0'))
  }
  
  rejectRequest =(e)=>{
    let payload ={
      "status":e,
      "id":this.props.requestDetail.featuredId
    }
    this.props.ProccedRequest(payload)
  }
  render() {
    return (
      <Row>
      <Col sm={12}>
      <Card>
        <Card.Body>

          <Row>
            <Col md={12}>
              <h5>Pending Feature Request</h5>
              <hr />
            </Col>
          </Row>
          <ValidationForm>
            <Row>

              <Col md={6}>
                <Form.Group as={Row} >
                  <Form.Label column sm={3}>
                    Merchant
                           </Form.Label>
                  <Col sm={9}>
                    <RenderTextField
                      name="merchant"
                      value={this.state.merchant}
                      disabled={true}
                    />
                  </Col>
                </Form.Group>

                <Form.Group as={Row}>
                  <Form.Label column sm={3}>
                    Activity
                  </Form.Label>
                  <Col sm={9}>
                    <RenderTextField name="activity"                    
                    value={this.state.activity}
                    disabled={true} />
                  </Col>
                </Form.Group>
                <Form.Group as={Row}>
                  <Form.Label column sm={3}>
                    Letter
                  </Form.Label>
                  <Col sm={9}>
                    <RenderTextField name="letter"
                    value={this.state.letter}
                    disabled={true} />
                  </Col>
                </Form.Group>
                <Form.Group as={Row}>
                  <Form.Label column sm={3}>
                    Primary Category
                  </Form.Label>
                  <Col sm={9}>
                    <RenderTextField name="pcategorey"
                    value={this.state.pcategorey}
                    disabled={true} />
                  </Col>
                </Form.Group><Form.Group as={Row}>
                <Form.Label column sm={3}>
                  Secondary Category
                </Form.Label>
                <Col sm={9}>
                  <RenderTextField name="scategorey"
                  value={this.state.scategorey}
                  disabled={true} />
                </Col>
              </Form.Group>

              </Col>


              <Col md={6}>
                <Form.Group as={Row} >
                  <Form.Label column sm={3}>
                    Request Start Date
                  </Form.Label>
                  <Col sm={9}>
                  <Datetime name="startDate"
                  disabled={true}
                  value={this.state.startDate}
                  timeFormat={false} onChange={this.handleEndDateChange} dateFormat={"DD MMM YYYY"} inputProps={{ placeholder: 'Select Date' }} />
              
                  </Col>
                </Form.Group>

                <Form.Group as={Row} >
                  <Form.Label column sm={3}>
                    Request End Date
                  </Form.Label>
                  <Col sm={9}>
                  <Datetime name="endDate"
                  disabled={true}
                  value={this.state.endDate}
                  timeFormat={false} onChange={this.handleEndDateChange} dateFormat={"DD MMM YYYY"} inputProps={{ placeholder: 'Select Date' }} />
              
                  </Col>
                </Form.Group>
                <Form.Group as={Row} >
                  <Form.Label column sm={3}>
                    Notes
                  </Form.Label>
                  <Col sm={9}>
                    <RenderTextArea name="notes"
                    value={this.state.notes}
                    disabled={true} 
                    // errorMessage={{ validator: validations.required }}
                    // validator={(value) => !isNaN(new Date(value).getTime())}
                    />
                  </Col>
                </Form.Group>
              </Col>


            </Row>
          </ValidationForm>
          
        </Card.Body>
      </Card >
      {this.props.requestDetail && this.props.requestDetail.status == '1' ?
      <Card className="box-border">
        <Card.Header>
          <Card.Title as="h5">Featured Activity Statistics</Card.Title>
        </Card.Header>
        <Card.Body>
            <Row>
                <Col sm={12}>
                  <h5>2 days live</h5>
                  <hr />
                </Col>
                <Col md={12}>
                <h6 className="align-items-center float-left">Unique user view</h6>
                <h6 className="align-items-center float-right">{this.state.uniqueUserView}</h6>
                <div className="progress m-t-30 m-b-20" style={{ height: '6px' }}>
                    <div className="progress-bar progress-c-theme" role="progressbar" style={{ width: '100%' }} aria-valuenow={this.state.uniqueUserView} aria-valuemin="0" aria-valuemax={this.state.uniqueUserView} />
                </div>
                </Col>
                <Col md={12}>
                <h6 className="align-items-center float-left">Conversion</h6>
                <h6 className="align-items-center float-right">{this.state.conversion}</h6>
                <div className="progress m-t-30 m-b-20" style={{ height: '6px' }}>
                    <div className="progress-bar progress-c-theme" role="progressbar" style={{ width: '25%' }} aria-valuenow={this.state.conversion} aria-valuemin="0" aria-valuemax={this.state.uniqueUserView} />
                </div>
                </Col>
                <Col md="12 text-right" >
                <h6 className="align-items-center">Conversion Rate</h6>
                <button  type="btn" disabled className="btn btn-primary" >{this.state.conversionPercent}%</button>
                </Col>
                <Col md={12}>
                <h6 className="align-items-center">Admin Notes</h6>
                <p>{this.state.adminNote}</p>
                </Col>
                
                <Col md={12}>
                <h6 className="align-items-center">Merchant Notes</h6>
                <p>{this.state.notes}</p>
                </Col>
            </Row>
        </Card.Body>
      </Card> : null}
      {this.props.requestDetail && this.props.requestDetail.status === '0' ?
      <Card className="box-border">
        <Card.Header>
          <Card.Title as="h5">Rejecting Feature Request</Card.Title>
        </Card.Header>
        <Card.Body>
          <Row>
            <Col md={12}>
            <h6 className="align-items-center">Admin Notes</h6>
            <p>{this.state.adminNotes}</p>
            </Col>
            
            <Col md={12}>
            <h6 className="align-items-center">Merchant Notes</h6>
            <p>{this.state.notes}</p>
            </Col>
          </Row>
        </Card.Body>
      </Card> : null}
      </Col>
    </Row>
    )
  }
}
const mapStateToProps = (state) => {
  console.log(state.admin)
  return {
    requestDetail:state.admin.requestDetail && state.admin.requestDetail.data && state.admin.requestDetail.data.data[0] 
  }
}
export default connect(mapStateToProps, {getRequestDetailsById,ProccedRequest})(ViewFeatureRequest)
