import { merchantActionTypes,CONFIRM_EMAIL_DONE,UPDATE_MERCHANT_EMAIL,UPDATE_MERCHANT_PASSWORD,GET_PRAMOTION_REQUEST,CREATE_PRAMOTION_REQUEST,GET_FETURED_ACTIVITY_REQUEST,CREATE_FETURED_ACTIVITY_REQUEST,GET_MERCHANT_ACTIVITY_BY_ID_URL,GET_REGULAR_DEALS_BY_ACTIVITY_URL,GET_OUTLET_DETAILS_BY_ID_URL,GET_OUTLET_BY_ACTIVITY_ID_URL,ALREADY_ADDED_ACTIVITIES, GET_MERCHANT_PROFILE_DETAILS,MERCHANT_LOGIN_URL,MERCHANT_RESET_FORGOT_PASSWORD_URL,MERCHANT_RESET_PASSWORD_URL, ADD_BANK_URL, DELETE_PAYMENT_URL, ACCOUNT_DETAILS_URL, MERCHANT_SIGNOUT } from "../constants";
