export const addBankDetails = (postdata) => {
  var data = Object.keys(postdata).reduce((formData, index) => {
    formData.append(index, postdata[index]);
    return formData;
  }, new FormData());

  return (dispatch, getState) => {
    dispatch(actionCreator(merchantActionTypes.signup.REQUEST));
    axios({
      method: 'post',
      url: ADD_BANK_URL,
      data: data,
      headers: privateHeaders(getState().merchant.authToken, true),
      validateStatus: function (status) {
        return status < 500; // Reject only if the status code is greater than or equal to 500
      }
    }).then(response => {

      if (response.data && response.data.data._id) {
        showSuccessMsg(response.data.message || ' Saved Successfully');
        dispatch(actionCreator(merchantActionTypes.signup.SUCCESS, response));
        // history.push('/admin/dashboard')
      } else if (response.status===401) {
        showErrorMsg((response.data && response.data.message) || SOMETHING_WENT_WRONG);
        dispatch(clearStoreAndRedirectToHome());
      } else {
        showErrorMsg(response.error)
        dispatch(actionCreator(merchantActionTypes.signup.SUCCESS, null));
      }
    }).catch(error => {
      showErrorMsg(error.message)

      dispatch(actionCreator(merchantActionTypes.signup.FAILURE));
    })
  }
}

