export const merchantSignout = () => {

  // console.log('ADMIN_SIGNOUT--', ADMIN_SIGNOUT, 'postData--', postData)

  return (dispatch, getState) => {
    console.log('MERCHANT_SIGNOUT-----', MERCHANT_SIGNOUT)
    dispatch(actionCreator(merchantActionTypes.merchantSignout.REQUEST));
    axios({
      method: 'POST',
      url: MERCHANT_SIGNOUT,
      headers: privateHeaders(getState().merchant.authToken),
      validateStatus: function (status) {
        return status < 500; // Reject only if the status code is greater than or equal to 500
      }
    }).then(response => {

      if (response.status >= 200 && response.status < 300) {
        dispatch(actionCreator(merchantActionTypes.merchantSignout.SUCCESS, response.data));
        dispatch(clearStoreAndRedirectToHome());
      } else if (response.status===301) {
        showErrorMsg((response.data && response.data.message) || SOMETHING_WENT_WRONG);
        dispatch(clearStoreAndRedirectToHome());
      } else {
        showErrorMsg(response.data.message || SOMETHING_WENT_WRONG)
        dispatch(actionCreator(merchantActionTypes.merchantSignout.FAILURE));
      }
    }).catch(error => {

      showErrorMsg(SOMETHING_WENT_WRONG)
      dispatch(actionCreator(merchantActionTypes.merchantSignout.FAILURE));

    })
  }
}
