import React from 'react'
import { Route, Redirect } from 'react-router-dom'
export default function PrivateRouter(props) {
    // console.log(props)
    return (
        // props.loginUser ?
            ((props.path.split("/")[0].toLowerCase() === 'merchant') ?
                <Redirect to={'/merchant/dashboard'} /> :
                <Route

                    path={props.path}
                    exact={props.exact}
                    // name={props.name}
                    render={renderProps => (
                        <props.component {...renderProps} />
                    )} />
                    )
            // : <Redirect to={props.role === 'admin' ? '/admin' : '/'} />
    )
}
