import React, { useState } from 'react'
import { Row, Col, Form } from 'react-bootstrap';
import { ValidationForm } from 'react-bootstrap4-form-validation';
import { RenderTextField, RenderImageUpload, RenderEmailField, RenderPrefixedUrlField, RenderTextArea, RenderPhoneField } from '../../../components/commonUIComponents';
import validator from 'validator';
import { SubmitButton, DeleteButton ,WarningButton} from '../../../components/commonUIComponents';
import validations from '../../../validations';
import ReactCrop from 'react-image-crop';
import { useDispatch } from 'react-redux';
import { convertBase64ToUrl } from '../actions';
import {API_URL, MERCHANT_PORT } from '../../../utilActions';
const imgPath = `${API_URL}:${MERCHANT_PORT}`;

let imgType = '';
let imgSendname = '';
export default function Step1(props) {
  
  if(props.merchantLogoFile !== ''){
    let imT = props.merchantLogoFile.name.split('.');
    imgType = props.merchantLogoFile.type;
    imgSendname = new Date().getTime()+'.'+imT[1];
  }
  const [imageRef, setImageRef] = useState('');
  const [croppedImageUrl, setCroppedImageUrl] = useState(null);
  const dispatch = useDispatch();
  const [crop, setCrop] = useState({
    width: 90,
    height: 50
  });
  // If you setState the crop in here you should return false.
  const onImageLoaded = image => {
    setImageRef(image)
  };

  const onCropComplete = crop => {
    makeClientCrop(crop);
  };

  const onCropChange = (crop1, percentCrop) => {

    setCrop(crop1);
  };
  const makeClientCrop = (crop) => {
    console.log(crop)
    if (imageRef && crop.width && crop.height) {
      const croppedImageUrl1 = getCroppedImg(
        imageRef,
        crop,
        imgSendname
      );
      console.log(croppedImageUrl1)
      setCroppedImageUrl(croppedImageUrl1);

      var file = dataURLtoFile(croppedImageUrl1,imgSendname);
      console.log(file);
      const formData = new FormData();
      formData.append('logo',file);
      dispatch(convertBase64ToUrl(file, 'croppedImageUrl'))
    }
  }
  const dataURLtoFile = (dataurl, filename) =>{
 
    var arr = dataurl.split(','),
        mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]), 
        n = bstr.length, 
        u8arr = new Uint8Array(n);
        
    while(n--){
        u8arr[n] = bstr.charCodeAt(n);
    }
    
    return new File([u8arr], filename, {type:mime});
}

  const getCroppedImg = (image, crop, fileName) => {
    const canvas = document.createElement('canvas');
    const scaleX = image.naturalWidth / image.width;
    const scaleY = image.naturalHeight / image.height;
    canvas.width = crop.width;
    canvas.height = crop.height;
    const ctx = canvas.getContext('2d');

    ctx.drawImage(
      image,
      crop.x * scaleX,
      crop.y * scaleY,
      crop.width * scaleX,
      crop.height * scaleY,
      0,
      0,
      crop.width,
      crop.height
    );
    
    const base64Image = canvas.toDataURL(imgType);
    return base64Image;

  }

  const merchantLogoSrc = props.merchantLogoSrc;
  return (

    <ValidationForm onSubmit={props.handleSubmitStep1}
    >
      <Row>
        <Col md={6}>
          <Form.Group>
            <Form.Label>Merchant Name</Form.Label>
            <RenderTextField
              name="merchantName"
              value={props.state.merchantName}
              onChange={props.handleOnChangeStep1}
              errorMessage={{ validator: props.state.merchantName && props.state.merchantName.trim().length ? validations.alreadyExist : validations.required }}
              validator={(str) => {
                return !validator.isEmpty(str, { ignore_whitespace: true }) && !props.isAlreadyExists(str) 
              }}
            />
          </Form.Group>
        </Col>
        <Col md={6}>
          <Form.Group>

            <Form.Label>Logo</Form.Label>
            <Row>
              <RenderImageUpload
                name="merchantLogo"
                value={props.state.merchantLogo}
                required={props.merchantDetails ? false : true}
                onChange={props.handleLogoField}
              />
            </Row>

          </Form.Group>

        </Col>
      </Row>
      <Row>

        <Col md="6">
          {merchantLogoSrc ? (
            <ReactCrop
              src={merchantLogoSrc}
              crop={crop}
              ruleOfThirds
              minWidth={90}
              minHeight={50}
              keepSelection={true}
              onImageLoaded={onImageLoaded}
              onComplete={onCropComplete}
              onChange={onCropChange}
              locked={false}
            />
          ) : ''}
        </Col>
        <Col md="6">
        {croppedImageUrl ?<span className="cropImageView">
          <div className="imgDiv" style={{backgroundImage:`url(${croppedImageUrl})`}} ></div>
          </span> : props.state.existingLogo ?
          <span className="cropImageView"><div className="imgDiv" style={{backgroundImage:`url(${props.state.existingLogo})`}} ></div></span> : ''
        }
        </Col>

      </Row>
      <Row>
        <Col md="6">
          <Form.Group>
            <Form.Label>Merchant Portal Email</Form.Label>
            <RenderEmailField
              name="merchantSignUpEmail"
              value={props.state.merchantSignUpEmail}
              onChange={props.handleOnChangeStep1}
              validator={validator.isEmail}
              errorMessage={{ validator: 'Invalid email' }}
            />
          </Form.Group>
        </Col>
        <Col md="6">
          <Form.Group>
            <Form.Label>Website</Form.Label>
            <RenderPrefixedUrlField
              placeholder=""
              name="merchantWebsite"
              value={props.state.merchantWebsite || ''}
              onChange={props.handleOnChangeStep1}
              validator={validator.isURL}
              errorMessage={{ validator: 'Invalid Url' }}
            />
          </Form.Group>
        </Col>

        <Col md="6">
          <Form.Group>
            <Form.Label>Contact Person for Sparks</Form.Label>
            <RenderTextField
              name="contactPersonForSparks"
              value={props.state.contactPersonForSparks || ''}
              onChange={props.handleOnChangeStep1}
              errorMessage={{ validator: validations.required }}
              validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
            />
          </Form.Group>
        </Col>
        <Col md="6">

          <Form.Group>
            <Form.Label>Contact Email for Sparks</Form.Label>
            <RenderEmailField
              name="contactEmail"
              value={props.state.contactEmail}
              onChange={props.handleOnChangeStep1}
              validator={validator.isEmail}
              errorMessage={{ validator: 'Invalid email' }}
            />
          </Form.Group>
        </Col>
        <Col md="6">
          <Form.Group>
            <Form.Label>Contact Number for Sparks</Form.Label>
            <RenderPhoneField
              name="mobileForSparks"
              value={props.state.mobileForSparks}
              onChange={props.handleOnChangeStep1}
              getCountryCode={props.getCountryCodeForSparks}
              required
              initialCountryCode={props.state.sparksCountryCode}
              validator={(value) => ((/[0-9]{2}-[0-9]{3}-[0-9]{3}/).test(value))}
              errorMessage={{ validator: "Invalid contact number" }}
            />

          </Form.Group>
        </Col>
        <Col md="6">

          <Form.Group>
            <Form.Label>Notes</Form.Label>
            <RenderTextArea
              name="notes"

              value={props.state.notes || ''}
              onChange={props.handleOnChangeStep1}
            />
          </Form.Group>
        </Col>
      </Row>
      <Row>
        <Col md="12 mt-5">
          <Row>
            <Col md="6">
              <SubmitButton disabled={props.loading}>
                {props.merchantDetails ? 'Update' : 'Save'}
              </SubmitButton>
            </Col>
            {props.merchantDetails && props.merchantDetails.status == 2 ? <Col md="3" className="text-right">
              <SubmitButton onClick={props.handleStatusMerchantConfirm}>
                Activate
              </SubmitButton>
            </Col> : null}
            {props.merchantDetails && props.merchantDetails.status == 1 ? <Col md="3" className="text-right">
            <WarningButton onClick={props.handleStatusMerchantConfirm}>
              Deactivate
            </WarningButton>
          </Col> : null}
            {props.merchantDetails ? <Col md="3" className="text-right">
              <DeleteButton onClick={props.handleRemoveMerchantConfirm}>
                Delete Merchant
                    </DeleteButton>
            </Col> : null}
          </Row>
        </Col>
      </Row>
    </ValidationForm>

  )

}
