import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Row, Col, Card, Form, Badge } from 'react-bootstrap';
import { ValidationForm } from 'react-bootstrap4-form-validation';
import validator from 'validator';
import Rating from 'react-rating';

import {  RenderTextArea } from '../../../components/commonUIComponents';
import Datetime from 'react-datetime';
import avatar1 from './../../../../assets/images/user/avatar-1.jpg';
import avatar2 from './../../../../assets/images/user/avatar-2.jpg';
import Slider from "react-slick";
import imgSlide1 from './../../../../assets/images/slider/img-slide-1.jpg';
import imgSlide2 from './../../../../assets/images/slider/img-slide-2.jpg';
import imgSlide3 from './../../../../assets/images/slider/img-slide-3.jpg';
import imgSlide4 from './../../../../assets/images/slider/img-slide-4.jpg';
import imgSlide5 from './../../../../assets/images/slider/img-slide-5.jpg';
import imgSlide6 from './../../../../assets/images/slider/img-slide-6.jpg';
import imgSlide7 from './../../../../assets/images/slider/img-slide-7.jpg';
import validations from '../../../validations';
class EditDiary extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isActive: true,
            activeModaltext: "Inactive",
            showModal: false,
            selectedOutlet: null
        }
    }
    handleSubmitTandC = (event) => {
        event.preventDefault();

    }
    handleSubmitOverview = (event) => {
        event.preventDefault();

    }
    handleSubmitDeals = (event) => {
        event.preventDefault();

    }
    toggleHandler = (event) => {
        event.preventDefault();
        this.setState({
            isActive: !this.state.isActive,
            showModal: !this.state.showModal
        }, () => {
            if (this.state.isActive === true) {
                this.setState({
                    activeModaltext: "Inactive"
                })
            } else {
                this.setState({
                    activeModaltext: "Active"
                })
            }
        })
    };

    openModal = () => {
        this.setState({
            showModal: true
        })
    }

    modalClosed = () => {
        this.setState({
            showModal: false
        })
    }
    changeActivity = () => {
        this.setState({
            selectedOutlet: null
        });
    }
    changeOutlet = (input) => {
        this.setState({
            selectedOutlet: input
        })

    }
    handleSubmit = (event) => { event.preventDefault() }
    render() {
        const settingsDots = {
            dots: true,
            infinite: true,
            fade: true,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1,
            arrows: false,
            autoplay: true,
            autoplaySpeed: 1000
        };
        return (
            <>
                {/* <Row>
          <Col md={12}>
            <Card>

              <Card.Body >
                <span className="float-right"><i className="card-icon feather icon-edit-2 f-24" /></span>
              </Card.Body>
            </Card>


          </Col>
        </Row> */}

                <Card className="box-border">
                    <Card.Header>
                        <Card.Title as="h5"> Diary Entry</Card.Title>
                    </Card.Header>
                    <Card.Body >
                        <ValidationForm onSubmit={this.handleSubmit}>
                            <Row>
                                {/* <Form.Label>Name</Form.Label> */}

                                <Col md="12">
                                    <Row>
                                        <Col md="3">
                                            <Form.Group>
                                                <Form.Label><i className="feather icon-calendar "></i> 28 December 2019 , 8:12</Form.Label>
                                            </Form.Group>
                                        </Col>

                                        <Col md="2">
                                            <Form.Group>
                                                <Form.Label><button type="btn" className="label f-12 text-white status-btn best-seller ">Published</button></Form.Label>
                                            </Form.Group>
                                        </Col>
                                        <Col md="2">
                                            <Form.Group>
                                                <Form.Label><i className="feather icon-users"> Friends</i></Form.Label>
                                            </Form.Group>
                                        </Col>
                                        <Col md="2">
                                            <Form.Group>
                                                <Form.Label> 20 Points Earned </Form.Label>
                                            </Form.Group>
                                        </Col>
                                        <Col md="3">
                                            <Form.Group>
                                                <Form.Label> <img className="rounded-circle" style={{ width: '30px' }} src={avatar2} alt="activity-user" /> <img className="rounded-circle" style={{ width: '30px' }} src={avatar1} alt="activity-user" /> Fab and Jessi</Form.Label>
                                            </Form.Group>
                                        </Col>
                                    </Row>

                                </Col>
                                <Col md={5}>

                                    <Slider ref={c => (this.slider = c)} {...settingsDots}>
                                        <div>
                                            <img className="d-block w-100" src={imgSlide1} alt="Slide1" />
                                        </div>
                                        <div>
                                            <img className="d-block w-100" src={imgSlide3} alt="Slide3" />
                                        </div>
                                        <div>
                                            <img className="d-block w-100" src={imgSlide2} alt="Slide2" />
                                        </div>
                                        <div>
                                            <img className="d-block w-100" src={imgSlide4} alt="Slide4" />
                                        </div>
                                        <div>
                                            <img className="d-block w-100" src={imgSlide5} alt="Slide5" />
                                        </div>
                                        <div>
                                            <img className="d-block w-100" src={imgSlide6} alt="Slide6" />
                                        </div>
                                        <div>
                                            <img className="d-block w-100" src={imgSlide7} alt="Slide7" />
                                        </div>
                                    </Slider>

                                </Col>

                                <Col md="6">
                                    <Row>
                                        <Col>
                                            <h2>Archery</h2>
                                            <h5>Salt and light archery </h5>
                                            <h6><Rating
                                                emptySymbol="fa fa-star-o fa-2x"
                                                fullSymbol="fa fa-star fa-2x"
                                                id="example-css"
                                            />
                                            </h6>
                                            <h6>
                                                <Badge variant="secondary">F</Badge> Letter Collected
                                            </h6>
                                        </Col>
                                    </Row>

                                </Col>
                                <Col md="12">
                                    <Form.Group>
                                        <Form.Label>Personal Experience</Form.Label>
                                        <RenderTextArea
                                            name="experience"
                                            id="exp"
                                            placeholder=""
                                            maxLength="20"
                                            errorMessage={{ validator: validations.required }}
                                            validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                        />

                                    </Form.Group>
                                </Col>
                                <Col md="12">
                                    <Form.Group>
                                        <Form.Label>Merchant Reviews</Form.Label>
                                        <RenderTextArea
                                            name="revuiews"
                                            id="rev"
                                            placeholder=""
                                            maxLength="20"
                                            errorMessage={{ validator: validations.required }}
                                            validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                        />

                                    </Form.Group>
                                </Col>
                                <Col md="12">
                                    <Form.Group>
                                        <br></br>
                                        <Form.Label><h5><i className="feather icon-thumbs-up"></i> 43 Likes </h5></Form.Label>
                                    </Form.Group>
                                </Col>
                                <Col md="6">
                                    <Form.Group>
                                        <Form.Label>Last modified</Form.Label>
                                        <Datetime timeFormat={false} dateFormat={"DD MMM YYYY"} inputProps={{ placeholder: 'Select Date' }} />

                                    </Form.Group>
                                </Col>

                            </Row>
                            <Row>
                                {/* <Col md="6">
                    <SubmitButton className="btn btn-primary shadow-2 mt-4">
                      Save
                          </SubmitButton>
                  </Col> */}
                            </Row>
                        </ValidationForm>
                    </Card.Body>
                </Card>

            </>

        )
    }
}
const mapStateToProps = () => {
    const Rating = (prop) => {
        return <div> {prop.data} <i className='feather icon-star-on'></i></div>;
    };
    return {
        catergoryOptions: [
            { label: 'Sports', value: 'sports' },
            { label: 'Adventure', value: 'adventure' },
            { label: 'Racing', value: "racing" },
            { label: 'Dance', value: 'dance' },
            { label: 'Others', value: 'others' }
        ],
        typeOptions: [
            // {label: 'Free', value: 'free' },
            { label: 'General', value: 'general' },
            { label: 'Specialized', value: "specialized" }
        ],
        earnedOptions: [
            { label: 'A', value: 'a' },
            { label: 'B', value: 'b' },
            { label: 'C', value: "c" },
            { label: 'D', value: 'd' },
            { label: 'E', value: 'e' },
            { label: 'F', value: "f" },
            { label: 'G', value: 'g' },
            { label: 'H', value: 'h' },
            { label: 'I', value: "i" },
            { label: 'J', value: 'j' },
            { label: 'K', value: 'k' },
            { label: 'L', value: "l" },
            { label: 'M', value: 'm' },
            { label: 'N', value: "n" },
            { label: 'O', value: 'o' },
            { label: 'P', value: 'p' },
            { label: 'Q', value: "q" },
            { label: 'R', value: 'r' },
            { label: 'S', value: 's' },
            { label: 'T', value: "t" },
            { label: 'U', value: 'u' },
            { label: 'V', value: 'v' },
            { label: 'W', value: "w" },
            { label: 'X', value: 'x' },
            { label: 'Y', value: 'y' },
            { label: 'Z', value: "x" }
        ],
        statusOptions: [
            { label: 'Active', value: '1', isSelected: true },
            { label: 'Inactive', value: '0', isSelected: false },
        ],
        merchantActivitiesList: [
            { rank: "1", name: "A1", price: "From 22", booked: "2", status: "Active", rating: <Rating data="4.8" />, },
            { rank: "2", name: "B1", price: "$22", booked: "4", status: "Active", rating: <Rating data="4.0" />, },
            { rank: "3", name: "C1", price: "$20", booked: "12", status: "Active", rating: <Rating data="3.8" />, },
            { rank: "4", name: "D1", price: "From 10", booked: "1", status: "Inactive", rating: <Rating data="2.0" />, },
        ],
        patnerList: [
            { rank: "1", name: "A1", price: "From 22", booked: "2", status: "Active", rating: <Rating data="4.8" />, },
            { rank: "2", name: "B1", price: "$22", booked: "4", status: "Active", rating: <Rating data="4.0" />, },
            { rank: "3", name: "C1", price: "$20", booked: "12", status: "Active", rating: <Rating data="3.8" />, },
            { rank: "4", name: "D1", price: "From 10", booked: "1", status: "Inactive", rating: <Rating data="2.0" />, },
        ],
        activityOptions: [
            { value: 'act1', label: 'Activity 1' },
            { value: 'act2', label: 'Activity 2' },
            { value: 'act3', label: 'Activity 3' },
            { value: 'act4', label: 'Activity 4' },
        ],
        outletOptions: [
            { value: 'outlet1', label: 'Outlet 1' },
            { value: 'outlet2', label: 'Outlet 2' },
            { value: 'outlet3', label: 'Outlet 3' },
            { value: 'outlet4', label: 'Outlet 4' },
        ]
    };
}


export default connect(mapStateToProps, null)(EditDiary)
