

export const REQUEST = "REQUEST";
export const SUCCESS = "SUCCESS";
export const FAILURE = "FAILURE";
export const API_URL = process.env.REACT_APP_API_URL;
export const ACTIVITY_PORT = process.env.REACT_APP_ACTIVITY_PORT;
export const MERCHANT_PORT = process.env.REACT_APP_MERCHANT_PORT;
export const WEB_PORT = process.env.REACT_APP_WEB_PORT;
export const ADMIN_PORT = process.env.REACT_APP_MERCHANT_PORT;
export const SOMETHING_WENT_WRONG = 'Something Went Wrong!';


export const commonHeaders = {
    "content-type": 'application/json',
    "Access-Control-Allow-Origin": "*", 
    "Access-Control-Allow-Methods": "POST, GET, PUT, OPTIONS, DELETE",
    "Access-Control-Max-Age": "3600",
    "Access-Control-Allow-Headers": "x-requested-with, content-type",
}

export const privateHeaders = (authToken, isMultiPart) => ({
    "content-type": isMultiPart ? 'multipart/form-data' : 'application/json',
    'authorization': `Bearer ${authToken}`,
    "Access-Control-Allow-Origin": "*", 
    "Access-Control-Allow-Methods": "POST, GET, PUT, OPTIONS, DELETE",
    "Access-Control-Max-Age": "3600",
    "Access-Control-Allow-Headers": "x-requested-with, content-type",
})
export const actionCreator = (type, data) => {
    return {
        type: type,
        payload: data
    }
}
export const capitalizeFirstChar = (value = '') => {


    return value && value[0].toUpperCase() + value.slice(1);
}
export const titlelize = (name = '') => {
    return name && (name.split(' ').map((word, index) => ((index < 1 ? word.slice(0, 1).toUpperCase() : word.slice(0, 1)) + word.slice(1).toLowerCase())).join(' '));
}
export const createActionTypes = (action_name) => {
    return [REQUEST, SUCCESS, FAILURE].reduce((requestObject, type) => {
        requestObject[type] = `${action_name}_${type}`;
        return requestObject;
    }, {})
}

export const maskPhoneNumber = (phoneNumber = '') => {
    let val = phoneNumber && phoneNumber.toString();
    if (val.length > 3 && val.length <= 7) {
        return val = val.substring(0, 3) + "-" + val.substring(3)
    } else if (val.length > 7) {
        return val = val.substring(0, 3) + "-" + val.substring(3, 7) + "-" + val.substring(7)
    }
    return val;
}

export const showHoursMinutes =(inputMinutes='')=>{

    let hr = (inputMinutes / 60);
      let rhr = Math.floor(hr);
      let min = (hr - rhr) * 60;
      let rmin = Math.round(min);


    return rhr +' Hours ' + rmin+' Minutes'
}
