import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import config from '../../../../../../config';
import navigation from '../../../../../../merchant-menu-items';
import DEMO from "../../../../../../store/constant";
import { NavLink } from 'react-router-dom';

class Breadcrumb extends Component {
    constructor(props) {
        super(props);
        this.state = {
            main: [],
            item: []
        };
    }

    componentDidMount() {
        (navigation.items).map((item, index) => {

            // if (item.children) {
            return this.getCollapse(item, index);
            // }
            // return false;
        });
    };

    componentWillReceiveProps() {
        (navigation.items).map((item, index) => {
            // if (item.children) {
            return this.getCollapse(item, index);
            // }
            // return false;
        });
    };


    getCollapse = (item) => {
        // 

        if (item.children) {
            (item.children).filter(collapse => {
                if (collapse.type && collapse.type === 'collapse') {
                    this.getCollapse(collapse);
                } else if (collapse.type && collapse.type === 'item') {

                    if (document.location.pathname === config.basename + collapse.url) {
                        this.setState({ item: collapse, main: item });
                    }
                }
                return false;
            });
        } else {
            if (document.location.pathname === config.basename + item.url) {

                this.setState({ item: item, main: item });


            }
        }
    };

    render() {
        let main, item;
        let breadcrumb = '';
        let title = 'Welcome';
        if (this.state.main && this.state.main.type === 'collapse') {
            main = (
                <li className="breadcrumb-item">
                    <a href={DEMO.BLANK_LINK}>{this.state.main.title}</a>
                </li>
            );
        }

        if (this.state.item && this.state.item.type === 'item') {
            title = this.state.item.title;
            item = (
                <li className="breadcrumb-item">
                    {this.state.item.url ? <NavLink to={this.state.item.url}>{title}</NavLink> : <a href={DEMO.BLANK_LINK}>{title}</a>}
                </li>
            );

            if (this.state.item.breadcrumbs !== false) {
                breadcrumb = (
                    <div className="page-header">
                        <div className="page-block">
                            <div className="row align-items-center">
                                <div className="col-md-12">
                                    <div className="page-header-title">
                                        <h5 className="m-b-10">{title}</h5>
                                    </div>
                                    <ul className="breadcrumb">
                                        <li className="breadcrumb-item">
                                            <Link to={DEMO.MERCHANT_DASHBOARD}><i className="feather icon-home" /></Link>
                                        </li>
                                        {main}
                                        {item}
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                );
            }

        }

        document.title = title + ' | Sparks';

        return (

            breadcrumb

        );
    }
}

export default Breadcrumb;