import React from 'react'
import { Row, Col, Form } from 'react-bootstrap';
import { ValidationForm } from 'react-bootstrap4-form-validation';
import { RenderTextField, RenderEmailField, RenderPhoneField, RenderSingleSelect, RenderBootstrapSingleSelect, RenderValidationTimeField, SuccessButton, } from '../../../components/commonUIComponents';
import validator from 'validator';
import { SubmitButton, DeleteButton } from '../../../components/commonUIComponents';
import validations from '../../../validations';
import GooglePlacesAutocomplete from 'react-google-places-autocomplete';
// If you want to use the provided css
import 'react-google-places-autocomplete/dist/index.min.css';
export default function Step2(props) {
  console.log(props)
  return (

    <ValidationForm onSubmit={props.handleSubmitStep2}>
      <Row>
        {props.outletById ?
          <Col md={4}>
            <SuccessButton onClick={() => props.getOutletData()}>Add New Outlet</SuccessButton>
          </Col> : ''}

        <Col md={{ span: 4, offset: props.outletById ? 4 : 8 }}>
          <Form.Group>
            <Form.Label>Added Outlets</Form.Label>
            <RenderBootstrapSingleSelect
              name="outlets"
              isClearable
              options={props.merchantOutlets}
              onChange={props.getOutletData}
              value={props.state.selectedOutlet}
            />
          </Form.Group>
        </Col>
      </Row>
      <Row>
        <Col md={12}>
          <h5>Outlet Details</h5>
          <hr />
        </Col>
        <Col md={6}>

          <Form.Group>
            <Form.Label>Name</Form.Label>
            <RenderTextField
              name="outletName"
              value={props.state.outletName || ''}
              onChange={props.handleOnChangeStep2}
              validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
              errorMessage={{ validator: validations.required }}
            />
          </Form.Group>
        </Col>

        <Col md="6">
          <Form.Group>
            <Form.Label>Contact Email</Form.Label>
            <RenderEmailField
              name="outletEmail"
              value={props.state.outletEmail || ''}
              onChange={props.handleOnChangeStep2}
              validator={validator.isEmail}
              errorMessage={{ validator: 'Invalid email' }}
            />
          </Form.Group>
        </Col>
        <Col md="6">
          <Form.Group>
            <Form.Label>Address</Form.Label>
            <RenderTextField
              name="streetName"
              placeholder="Street Name"
              className="mb-2"
              value={props.state.streetName || ''}
              onChange={props.handleOnChangeStep2}
              validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
              errorMessage={{ validator: validations.required }}
            />
            
            {props.gmapsLoaded && <GooglePlacesAutocomplete
              name="fullAddress"
              value={props.state.fullAddress || ''}
              onSelect={props.getLocation}
              onChange={props.handleOnChangeStep2}
              initialValue={props.state.fullAddress || ''}
              placeholder="Full Address"
              className="form-control mb-2"
              validator={(str) => !validator.isEmpty(str.description, { ignore_whitespace: true })}
              errorMessage={{ validator: validations.required }}
            />}
            
            
            <RenderTextField
              name="postalCode"
              placeholder="Postal Code"
              value={props.state.postalCode || ''}
              onChange={props.handleOnChangeStep2}
              validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
              errorMessage={{ validator: validations.required }}

            />
          </Form.Group>
        </Col>
        <Col md="6">
          <Form.Group>
            <Form.Label>Contact Number</Form.Label>
            <RenderPhoneField
              name="outletMobile"
              getCountryCode={props.getCountryCodeForOutlet}
              initialCountryCode={props.state.outletCountryCode}
              validator={(value) => ((/[0-9]{2}-[0-9]{3}-[0-9]{3}/).test(value))}
              errorMessage={{ validator: "Invalid contact number" }}
              value={props.state.outletMobile || ''}
              onChange={props.handleOnChangeStep2}
            />

          </Form.Group>
        </Col>
        <Col md={6}>
          <Form.Group>
            <Form.Label>Location</Form.Label>
            <RenderSingleSelect
              name="region"
              selectKey="region"
              required
              options={[
                { value: 'North', label: 'North' },
                { value: 'South', label: 'South' },
                { value: 'East', label: 'East' },
                { value: 'West', label: 'West' },
                { value: 'North East', label: 'North East' },

              ]}
              value={props.state.region}
              onChange={props.handleOnChangeStep2}

            />
          </Form.Group>
        </Col>
        <Col md={6}>
          <Form.Group>
            <Form.Label>MRT Station</Form.Label>
            <RenderTextField
              name="station"
              value={props.state.station || ''}
              onChange={props.handleOnChangeStep2}
              validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
              errorMessage={{ validator: validations.required }}
            />
          </Form.Group>
        </Col>

        <Col md="6">
          <Form.Label>Opening Hours</Form.Label>
          {props.openingHours.map((day, index) => {
            return index === props.openingHours.length - 1 ?
              <React.Fragment key={index}>
                <Form.Label>Opening Hours During Public Holidays</Form.Label>
                <Form.Group key={index}>
                  <Row className="align-items-center mt-3">
                    <Col md="1" ><Form.Label>{day.day}</Form.Label></Col>
                    <Col md="4" >
                    {props.openingHours[index] && props.openingHours[index].status === 'close'? <Form.Label style={{width: '100%',
                    textAlign: 'center'}}>Closed</Form.Label> :<RenderValidationTimeField
                        name={`openingHours[${index}].startTime`}
                        className="px-2"
                        onChange={(moment) => props.getTime(moment, index, 'startTime')}
                        value={props.openingHours[index].startTime || ''}
                        errorMessage={{ validator: validations.required }}
                        validator={(value) => (props.openingHours[index].endTime ? value.length <= 5 && (/([0-1][0-9]|2[0-3]):[0-5][0-9]/).test(value) && (props.openingHours[index].endTime ? new Date("2020", "1", "1", ...props.openingHours[index].endTime.split(":")).getTime() > new Date("2020", "1", "1", ...value.split(":")).getTime() : true) : true)}
                      />}
                    </Col>
                    <Col md="1" >-</Col>
                    <Col md="4" >
                    {props.openingHours[index] && props.openingHours[index].status === 'close'? <Form.Label style={{width: '100%',
                    textAlign: 'center'}}>Closed</Form.Label> :<RenderValidationTimeField
                        name={`openingHours[${index}].endTime`}
                        onChange={(moment) => props.getTime(moment, index, 'endTime')}
                        value={props.openingHours[index].endTime || ''}
                        errorMessage={{ validator: validations.required }}
                        validator={(value) => (props.openingHours[index].startTime ? value.length <= 5 && (/([0-1][0-9]|2[0-3]):[0-5][0-9]/).test(value) && (props.openingHours[index].startTime ? new Date("2020", "1", "1", ...props.openingHours[index].startTime.split(":")).getTime() < new Date("2020", "1", "1", ...value.split(":")).getTime() : true): true)}
                      />}
                    </Col>
                    <Col md="2">
                      {props.openingHours[index].status === 'open'?                       
                      <DeleteButton disabled={props.loading} onClick={(e) => props.handleCloseOpen(e,index,'close')}  >Closed</DeleteButton>:
                      <SubmitButton onClick={(e) => props.handleCloseOpen(e,index,'open')}  disabled={props.loading}>Open</SubmitButton>
                      }
                    </Col>
                  </Row>
                </Form.Group>
              </React.Fragment>
              : <Form.Group key={index}>
                <Row className="align-items-center mt-3">
                  <Col md="1" ><Form.Label>{day.day}</Form.Label></Col>
                  <Col md="4" >
                    {props.openingHours[index] && props.openingHours[index].status === 'close'? <Form.Label style={{width: '100%',
                      textAlign: 'center'}}>Closed</Form.Label> :<RenderValidationTimeField
                      name={`openingHours[${index}].startTime`}
                      className="px-2"
                      onChange={(moment) => props.getTime(moment, index, 'startTime')}
                      value={props.openingHours[index] ? props.openingHours[index].startTime : ''}
                      errorMessage={{ validator: validations.required }}
                      validator={(value) => (props.openingHours[index].endTime ? value.length <= 5 && (/([0-1][0-9]|2[0-3]):[0-5][0-9]/).test(value) && (props.openingHours[index].endTime ? new Date("2020", "1", "1", ...props.openingHours[index].endTime.split(":")).getTime() > new Date("2020", "1", "1", ...value.split(":")).getTime() : true) : true)}
                    />}
                  </Col>
                  <Col md="1" >-</Col>
                  <Col md="4" >
                  {props.openingHours[index] && props.openingHours[index].status === 'close'?<Form.Label style={{width: '100%',
                  textAlign: 'center'}}>Closed</Form.Label> :<RenderValidationTimeField
                      name={`openingHours[${index}].endTime`}
                      onChange={(moment) => props.getTime(moment, index, 'endTime')}
                      value={props.openingHours[index] ? props.openingHours[index].endTime : ''}
                      errorMessage={{ validator: validations.required }}
                      validator={(value) => (props.openingHours[index].startTime ? value.length <= 5 && (/([0-1][0-9]|2[0-3]):[0-5][0-9]/).test(value) && (props.openingHours[index].startTime ? new Date("2020", "1", "1", ...props.openingHours[index].startTime.split(":")).getTime() < new Date("2020", "1", "1", ...value.split(":")).getTime() : true): true)}
                    />}
                  </Col>
                  <Col md="2">
                    {props.openingHours[index].status === 'open'?                       
                    <DeleteButton disabled={props.loading} onClick={(e) => props.handleCloseOpen(e,index,'close')}  >Closed</DeleteButton>:
                    <SubmitButton onClick={(e) => props.handleCloseOpen(e,index,'open')}  disabled={props.loading}>Open</SubmitButton>
                    }
                  </Col>
                </Row>
              </Form.Group>
          }
          )}

        </Col>
      </Row>
      <Row>
        <Col md="12">
          <Row>
            <Col md="6">
              <SubmitButton disabled={props.loading}>
                {props.outletById ? 'Update Outlet' : 'Add Outlet'}
              </SubmitButton>
            </Col>
            <Col md="6" className="text-right">
              {!props.state.selectedOutlet ? null : <DeleteButton disabled={props.loading} onClick={props.handleRemoveSuggestionConfirm}>
                Delete Outlet
              </DeleteButton>}

            </Col>
          </Row>

        </Col>
      </Row>

    </ValidationForm>

  )

}
