import React from 'react'
import { Row, Col, Form } from 'react-bootstrap';

import { capitalizeFirstChar, showHoursMinutes } from '../../../utilActions';

import moment from 'moment';

export default function ActivityDetailsTab(props) {
  // console.log(props)
  return (
    <>
      <Row>
        <Col md="6">
          <Form.Group as={Row} controlId="formHorizontalType">
            <Form.Label column sm={4}>Activity name</Form.Label>
            <Col md="6">
              <Form.Control key="letter-earned" plaintext readOnly value={(props.activityDetails && props.activityDetails.activityName) || ''} />
            </Col>
          </Form.Group>
        </Col>
      </Row>
      <Row>
        <Col md="6">
          <Form.Group as={Row} controlId="formHorizontalType">
            <Form.Label column sm={4}>Type</Form.Label>
            <Col md="6">
              <Form.Control plaintext readOnly value={(props.activityDetails && capitalizeFirstChar(props.activityDetails.activityType === 'general' ? 'Generalized' : (props.activityDetails.activityType === 'special' ? 'Specialized' : props.activityDetails.activityType)))|| ''} />
            </Col>
          </Form.Group>
        </Col>
      </Row>
      <Row>
        <Col md="6">
          <Form.Group as={Row} controlId="formHorizontalCategory">
            <Form.Label column sm={4}>Category</Form.Label>
            <Col md="6">
              {props.activityDetails && props.activityDetails.activitycategories && props.activityDetails.activitycategories.map((item, index) => {
                return <React.Fragment key={index} > <button type="btn" disabled className="category-btn" >{item.category_name}</button>   </React.Fragment>
              })}
            </Col>
          </Form.Group>
        </Col>
      </Row>

      <Row>
        <Col md="6">
          <Form.Group as={Row} controlId="formHorizontalDuration">
            <Form.Label column sm={4}>Duration</Form.Label>
            <Col md="8">
              <Form.Control plaintext key="duration-minutes" readOnly value={(props.activityDetails && props.activityDetails.activityDuration && showHoursMinutes(props.activityDetails.activityDuration))|| ''} />
            </Col>
          </Form.Group>
        </Col>
      </Row>
      <Row>
        <Col md={6} >
          <Form.Group as={Row} controlId="formHorizontalBestSeller">
            <Form.Label column sm={4}>Best Seller</Form.Label>
            <Col md="6">
              {props.activityDetails && <button type="btn" className="label  f-12 text-white status-btn best-seller">{props.activityDetails ? (props.activityDetails.isBestSeller ? 'YES' : 'NO') : ''}</button>
              }
            </Col>
          </Form.Group>
        </Col>
      </Row>

      <Row>
        <Col md="6" >
          <Form.Group as={Row} controlId="formHorizontalLetterEarned">
            <Form.Label column sm={4}>Letter Earned</Form.Label>
            <Col md="6">
              <Form.Control   key="letter-earned" plaintext readOnly value={(props.activityDetails && props.activityDetails.letterCollected) || ''} />
            </Col>
          </Form.Group>
        </Col>
      </Row>
      <Row>
        <Col md="6">
          <Form.Group as={Row} controlId="formHorizontalAnniversary">
            <Form.Label column sm={4}>Anniversary Special</Form.Label>
            <Col md="6">
              {props.activityDetails && <Form.Control plaintext key="anniversary-special" readOnly value={(props.activityDetails && props.activityDetails.isAnniversarySpecial === 'true' ? 'YES' : 'NO') || ''} />}

            </Col>
          </Form.Group>
        </Col>
      </Row>
      <Row>
        <Col md="6">
          <Form.Group as={Row} controlId="formHorizontalAnniversary">
            <Form.Label column sm={4}>Validity of Regular Deal</Form.Label>
            <Col md="6">
              {props.activityDetails && <Form.Control plaintext key="valiidity" readOnly value={(props.activityDetails && props.activityDetails.validityPeriod && `${props.activityDetails.validityPeriod} Days`) || ''} />}

            </Col>
          </Form.Group>
        </Col>
      </Row>
      <Row>
        <Col md="6">
          <Form.Group as={Row} controlId="formHorizontalAnniversary">
            <Form.Label column sm={4}>Reservation Required</Form.Label>
            <Col md="6">
              {props.activityDetails && <Form.Control plaintext key="anniversary-special" readOnly value={(props.activityDetails && props.activityDetails.isReservationRequired === 'true'   ? 'YES' : 'NO') || ''} />}

            </Col>
          </Form.Group>
        </Col>
      </Row>
      <Row>
        <Col md="6"  >
          <Form.Group as={Row} controlId="formHorizontalStatus">
            <Form.Label column sm={4}>Merchant Activity Status</Form.Label>
            {props.activityDetails ? <Col md="6">
              {props.activityDetails && props.activityDetails.status === 1 ?
                <button type="btn" disabled={props.loading} onClick={props.showStatusConfirm} className="green-bg rounded-btn">Active</button> :
                <button type="btn" disabled={props.loading} onClick={props.showStatusConfirm} className="red-bg rounded-btn">Inactive</button>}
            </Col> : null}
          </Form.Group>
        </Col>
      </Row>
      <Row>
        <Col md="6"  >
        <Form.Group as={Row} controlId="formHorizontalAnniversary">
          <Form.Label column sm={4}>Commision Percentage</Form.Label>
          <Col md="6">
            {props.activityDetails && <Form.Control plaintext key="commisionPercentage" readOnly value={(props.activityDetails && props.activityDetails.commisionPercentage) || ''} />}

          </Col>
        </Form.Group>
        </Col>
      </Row>
      <Row>
        <Col md="6"  >
        <Form.Group as={Row} controlId="formHorizontalAnniversary">
          <Form.Label column sm={4}>Sparks Commission Last Updated</Form.Label>
          <Col md="6">
            {props.activityDetails && <Form.Control plaintext key="updatedAt" readOnly value={(props.activityDetails && props.activityDetails.updatedAt && moment(props.activityDetails.updatedAt).format('DD/MMM/YYYY')) || ''} />}

          </Col>
        </Form.Group>
        </Col>
      </Row>
      <Row>
      </Row>
    </>
  )
}
