import React, { Component } from 'react'
import { ValidationForm } from 'react-bootstrap4-form-validation';
import { Card, Tabs, Tab,Row, Col, Form, } from 'react-bootstrap';
import { RenderBootstrapSingleSelect,RenderPrefixedPriceField,RenderAppendTextField, SubmitButton,SuccessButton, DeleteButton,RenderTextField,RenderNumberField, RenderTextArea, RenderSearchableMultiSelect } from './../../../components/commonUIComponents';

import { connect } from 'react-redux';
import RenderDataTable from '../../../components/dataTable';
import { history } from '../../../../history';
import { getMerchantSelectList,getAlreadyAddedActivities,getOutletsByActivityId,createPramotionRequest,getActivePramotionsList, getPendingPramotionsList, getArchiveRequestList } from '../actions/index';
import { capitalizeFirstChar } from '../../../utilActions';
import moment from 'moment';
import validator from 'validator';
import Datetime from 'react-datetime';
import validations from '../../../validations';
class Pramotions extends Component {
  constructor(props) {
    super(props);
    this.state = {
      "merchantId":'',
      "activityId":'',
      "merchantActivityId":'',
      "startDate":moment().format(),
      "endDate":'',
      "notes":'',
      "outletId":[],
      title:'',
      selectedOutletName: [],
      description:'',
      pramotion_ticket:'',
      startMoment:'',
      originalPrice:'',
      discountedPercentage: '',
      discountedPrice : '',
      discountAmount : '',
      terms:[
            {point:'Pramotion can only be claimed unitl start date & end date'},
            {point:'Reservation is subjected to availability. please check availability with merchant before going down for activity'},
            {point:'Points are non refundable once user unlock the pramotions'}
          ],
      activeSortColumn: 'createdAt',
      activeSortDirection: 'DESC',
      activeRecordLimit: 10,
      activeRecordOffset: 0,
      activeSsearch:'',
      activeSstatus:'1',  
      pendingSortColumn: 'createdAt',
      pendingSortDirection: 'DESC',
      pendingRecordLimit: 10,
      pendingRecordOffset: 0,
      pendingSsearch:'',
      pendingSstatus:'2', 
        
      archiveSortColumn: 'createdAt',
      archiveSortDirection: 'DESC',
      archiveRecordLimit: 10,
      archiveRecordOffset: 0,
      archiveSsearch:'',     
      globalFilter: null,
      merchantList:[]
    }
  }
  componentDidMount(){
    this.props.getMerchantSelectList()
    this.loadActiveData();
    this.loadPendingData();
    this.loadArchiveData();
  }
  loadActiveData = ()=>{
   let payload = {
      "status":this.state.activeSstatus,
      "search":this.state.activeSsearch,
      "limit": this.state.activeRecordLimit,
      "offset": this.state.activeRecordOffset,
      "orderBy": this.state.activeSortColumn,
      "order": this.state.activeSortDirection
    }
    this.props.getActivePramotionsList(payload);
  }
  loadPendingData = ()=>{
    let payload = {
       "status":this.state.pendingSstatus,
       "search":this.state.pendingSsearch,
       "limit": this.state.pendingRecordLimit,
       "offset": this.state.pendingRecordOffset,
       "orderBy": this.state.pendingSortColumn,
       "order": this.state.pendingSortDirection
     }
     this.props.getPendingPramotionsList(payload);
   }
  loadArchiveData = ()=>{
    let payload = {
       "search":this.state.archiveSsearch,
       "limit": this.state.archiveRecordLimit,
       "offset": this.state.archiveRecordOffset,
       "orderBy": this.state.archiveSortColumn,
       "order": this.state.archiveSortDirection
     }
     this.props.getArchiveRequestList(payload);
   }

  handlePendingSorting = (column, sortDirection, event) => {
    this.setState({
      pendingSortColumn: column.selector,
      pendingSortDirection: sortDirection.toUpperCase()
    }, () => this.loadPendingData())


  }
  handlePendingSearch = (text)=>{
    this.setState({
      pendingSearch:text
    }, () => this.loadPendingData())
  }
  handlePendingPageChange = (pageNumber) => {
    this.setState((prevState) => ({
      pendingRecordOffset: (pageNumber - 1) * prevState.recordLimit,
    }), () => this.loadPendingData())

  }
  onChangePendingRecordsLimit = (event) => {
    this.setState({
      pendingRecordLimit: event.target.value && parseInt(event.target.value, 10)
    }, () => this.loadPendingData())
  }

  handleActiveSorting = (column, sortDirection, event) => {
    this.setState({
      activeSortColumn: column.selector,
      activeSortDirection: sortDirection.toUpperCase()
    }, () => this.loadActiveData())


  }
  handleActiveSearch = (text)=>{
    this.setState({
      activeSearch:text
    }, () => this.loadActiveData())
  }
  handleActivePageChange = (pageNumber) => {
    this.setState((prevState) => ({
      activeRecordOffset: (pageNumber - 1) * prevState.recordLimit,
    }), () => this.loadActiveData())

  }
  onChangeActiveRecordsLimit = (event) => {
    this.setState({
      activeRecordLimit: event.target.value && parseInt(event.target.value, 10)
    }, () => this.loadActiveData())
  }

  handleArchiveSorting = (column, sortDirection, event) => {
    this.setState({
      archiveSortColumn: column.selector,
      archiveSortDirection: sortDirection.toUpperCase()
    }, () => this.loadArchiveData())


  }
  handleArchiveSearch = (text)=>{
    this.setState({
      archiveSearch:text
    }, () => this.loadArchiveData())
  }
  handleArchivePageChange = (pageNumber) => {
    this.setState((prevState) => ({
      archiveRecordOffset: (pageNumber - 1) * prevState.recordLimit,
    }), () => this.loadArchiveData())

  }
  onChangeArchiveRecordsLimit = (event) => {
    this.setState({
      archiveRecordLimit: event.target.value && parseInt(event.target.value, 10)
    }, () => this.loadArchiveData())
  }
  getAllActivities = (id) => {
    this.props.getAlreadyAddedActivities(id)

  }


  editPendingFeatureRequest = (row) => {
    history.push(`/admin/dashboard/merchant/activePramotionRequest/${row.id}`)
  }
  viewFeatureRequest = (row) => {
    history.push(`/admin/dashboard/merchant/viewPramotionRequest/${row.id}`)
  }
  numCheck = (num) => {
    
    return num % 1 === 0;
 
}

handleChange = (event) => {
  console.log(event.target.name,event.target.value)
  
  this.setState({
      [event.target.name]: event.target.value
    })
  
      if (event.target.name === 'discountedPercentage') {
      this.setState(
          {
              discountAmount : ((parseFloat(event.target.value) / 100) * this.state.originalPrice).toFixed(2),
              discountedPrice : (this.state.originalPrice - ((parseFloat(event.target.value) / 100) * this.state.originalPrice)).toFixed(2),
              
          }
      )
      
      } else if (event.target.name === 'discountedPrice') {
      this.setState(
          {
              discountedPercentage : (((this.state.originalPrice - parseFloat(event.target.value)) / this.state.originalPrice) * 100).toFixed(2),
              discountAmount :(this.state.originalPrice - parseFloat(event.target.value)).toFixed(2),
              
          },()=>{
              console.log(this.state.discountedPercentage,this.state.discountAmount)
          }
      )
      
      } else if (event.target.name === 'discountAmount') {
      this.setState(
          {
              discountedPercentage : (parseFloat(event.target.value) / this.state.originalPrice * 100).toFixed(2),
              discountedPrice :(this.state.originalPrice - parseFloat(event.target.value)).toFixed(2),
              
          }
      )
      
  }
  
   
  if (event.target.name === 'originalPrice' && !event.target.value) {
    this.setState({
      discountedPercentage: '',
      discountedPrice : '',
      discountAmount : '',
    })
  }
}
handleStartDateChange = (e) =>{
  this.setState({
      startDate:e._d,
      endDate:moment(e._d).add(14, 'days'),
      startMoment:e.isAfter(moment().add(14, 'days'))
  })
}
handleEndDateChange = (e) =>{
  this.setState({
      endDate:e._d
  })
}
handleActivityChange =  (e) => {
  console.log(e)
  this.setState({
      activityId:e.activityId,
      merchantActivityId:e.value
  })
  this.getActivityOutlets(e.value);
}
handleMerchantChange = (e) => {
  this.setState({
    merchantId:e.value,
    activityId:'',
    merchantActivityId:'',
    selectedActivity:'',
    selectedOutletName:[],
    outletId:[]
  })
  this.getAllActivities(e.value)
}
getActivityOutlets = (id) => {
  this.props.getOutletsByActivityId(id, this.props.merchantLoginId)
}
handleOutletOnChange = selectedOutletName => {
  this.setState({ selectedOutletName: selectedOutletName && selectedOutletName.length ? selectedOutletName : [] }
  )
}
handleSubmit = (ev, formData) => {
  ev.preventDefault();

  let postObj = {
      "adminId":this.props.adminId,
      "merchantId":this.state.merchantId,
      // "activityId":this.state.activityId,
      "merchantActivityId":this.state.merchantActivityId,
      "startDate":moment(this.state.startDate).format('YYYY-MM-DD H:mm:ss'),
      "endDate":moment(this.state.endDate).format('YYYY-MM-DD H:mm:ss'),   
      "outletId":this.state.selectedOutletName,         
      "title":this.state.title,
      "description":this.state.description,
      "originalPrice":this.state.originalPrice,
      "discountedPrice":this.state.discountedPrice,
      "discountedPercentage":this.state.discountedPercentage,
      "discountAmount":this.state.discountAmount,
      "promotionalTerms":this.state.terms,
      "quantity":this.state.pramotion_ticket,
      "quantityPaidFor":0,
  };
  // create new activity
  this.props.createPramotionRequest(postObj);

}
validationStartDate = (currentDate) => {
  // console.log(currentDate)
  return currentDate.isAfter(moment().add(7, 'days'));
};
validationEndDate = (currentDate) => {
  
  return currentDate.isAfter(moment(this.state.startDate).add(13, 'days'));
  
};

handleTermsOnChange = (input,index) => {
  let term = this.state.terms;
  term[index].point = input.target.value
  this.setState({
      terms:term
  })
}
handleAddTerms = () => {
  let term = this.state.terms;
  term.push({point:''})
  this.setState({
      terms:term
  })
}
handleRemoveConfirm = (event, index) => {

  event.preventDefault();
  let term = this.state.terms;
  term.splice(index, 1);
  this.setState({
      terms:term
  })
}
  render() {
    const archivedColumns = [
      {
        name: "Be Featured ID",
        selector: "id",
        sortable: false,
      },
      {
        name: "Merchant",
        selector: "merchant",
        sortable: false
      },
      {
        name: "Activity",
        selector: "activity",
        sortable: false,

      },
      {
        name: "Letter",
        selector: "letter",
        sortable: false,
      },
      {
        name: "Activity categories",
        selector: "categories",
        sortable: false,
      },
      {
        name: "Start date",
        selector: "startDate",
        sortable: true,
        format: (row) => row.startDate?moment(row.startDate).format('DD MMM YYYY'):'',
      },
      {
        name: "End date",
        selector: "endDate",
        sortable: true,
        format: (row) => row.endDate?moment(row.endDate).format('DD MMM YYYY'):'',
      },
      {
        name: "Original price",
        selector: "oprice",
        sortable: false,
      },
      {
          name: "Discounted price",
          selector: "dprice",
          sortable: false,
      },
      {
        name: "Claimed Tickets",
        selector: "claimedTickets",
        sortable: false, 
        format: (row) => {
          return `${row.claimedTickets}/${row.totalTicket}`
        },
      },

    ];
    const ActiveColumns = [
      {
        name: "Be Featured ID",
        selector: "id",
        sortable: false,
      },
      {
        name: "Merchant",
        selector: "merchant",
        sortable: false
      },
      {
        name: "Activity",
        selector: "activity",
        sortable: false,

      },
      {
        name: "Letter",
        selector: "letter",
        sortable: false,
      },
      {
        name: "Activity categories",
        selector: "categories",
        sortable: false,
      },
      {
        name: "Start date",
        selector: "startDate",
        sortable: true,
        format: (row) => row.startDate?moment(row.startDate).format('DD MMM YYYY'):'',
      },
      {
        name: "End date",
        selector: "endDate",
        sortable: true,
        format: (row) => row.endDate?moment(row.endDate).format('DD MMM YYYY'):'',
      },
      {
        name: "Original price",
        selector: "oprice",
        sortable: false,
      },
      {
          name: "Discounted price",
          selector: "dprice",
          sortable: false,
      },
      {
        name: "Claimed Tickets",
        selector: "claimedTickets",
        sortable: false, 
        format: (row) => {
          return `${row.claimedTickets}/${row.totalTicket}`
        },
      },

    ];
    const PendingColumns = [
      {
        name: "Be Featured ID",
        selector: "id",
        sortable: false,
      },
      {
        name: "Merchant",
        selector: "merchant",
        sortable: false
      },
      {
        name: "Activity",
        selector: "activity",
        sortable: false,

      },
      {
        name: "Letter",
        selector: "letter",
        sortable: false,
      },
      {
        name: "Activity categories",
        selector: "categories",
        sortable: false,
      },
      {
        name: "Date submitted",
        selector: "createdAt",
        sortable: false,
        format: (row) => row.createdAt?moment(row.createdAt).format('DD MMM YYYY'):'',
      },
      {
        name: "Start date",
        selector: "startDate",
        sortable: true,
        format: (row) => row.startDate?moment(row.startDate).format('DD MMM YYYY'):'',
      },
      {
        name: "End date",
        selector: "endDate",
        sortable: true,
        format: (row) => row.endDate?moment(row.endDate).format('DD MMM YYYY'):'',
      },
      {
          name: "Original price (SGD)",
          selector: "oPrice",
          sortable: false,
      },
      {
          name: "Discounted price (SGD)",
          selector: "dPrice",
          sortable: false,
      },
      {
        name: "No of tickets",
        selector: "totalTicket",
        sortable: true,
      },
      

    ];
    return (
      <Tabs variant="pills" className="mb-3" defaultActiveKey="pending/approved" >
        <Tab eventKey="pending/approved" title="Pending/Approved">

          <Card className="box-border">
            <Card.Header>
              <Card.Title as="h5">Pending Pramotions Requests</Card.Title>
            </Card.Header>
            <Card.Body >
              <RenderDataTable
                columns={PendingColumns}
                onRowClicked={this.editPendingFeatureRequest}
                data={this.props.pendingPramotionList || []}                
                defaultSortField={this.state.pendingSortColumn}
                filterTextSearch={this.state.pendingSearch}
                sortServer
                totalRecords={this.props.pendingTotalCount}
                dataLength ={this.props.pendingLenght}
                recordLimit={this.state.pendingRecordLimit}
                recordOffset={this.state.pendingRecordOffset}
                onChangeRecordsLimit={this.onChangePendingRecordsLimit}
                handlePageChange={this.handlePendingPageChange}
                onSort={this.handlePendingSorting}
                handleSearch={this.handlePendingSearch}
              />
            </Card.Body>
          </Card>
          <Card className="box-border">
            <Card.Header>
              <Card.Title as="h5">Approved Pramotion Requests</Card.Title>
            </Card.Header>
            <Card.Body>
              <RenderDataTable
                columns={ActiveColumns}
                onRowClicked={this.viewFeatureRequest}
                data={this.props.activePramotionList || []}                
                defaultSortField={this.state.activeSortColumn}
                filterTextSearch={this.state.activeSearch}
                sortServer
                totalRecords={this.props.activeTotalCount}
                dataLength ={this.props.activeLength}
                recordLimit={this.state.activeRecordLimit}
                recordOffset={this.state.activeRecordOffset}
                onChangeRecordsLimit={this.onChangeActiveRecordsLimit}
                handlePageChange={this.handleActivePageChange}
                onSort={this.handleActiveSorting}
                handleSearch={this.handleActiveSearch}
              />
            </Card.Body>
          </Card>

        </Tab>
        <Tab eventKey="Archived" title="Archived">
          <Card className="box-border">
            <Card.Header>
              <Card.Title as="h5">Archived Featured Merchants</Card.Title>
            </Card.Header>
            <Card.Body>
              <RenderDataTable
                columns={archivedColumns}
                data={this.props.archivedList || []}               
                onRowClicked={this.viewFeatureRequest}               
                defaultSortField={this.state.archiveSortColumn}
                filterTextSearch={this.state.archiveSearch}
                sortServer
                totalRecords={this.props.archiveTotalCount}
                dataLength ={this.props.archiveLength}
                recordLimit={this.state.archiveRecordLimit}
                recordOffset={this.state.archiveRecordOffset}
                onChangeRecordsLimit={this.onChangeArchiveRecordsLimit}
                handlePageChange={this.handleArchivePageChange}
                onSort={this.handleArchiveSorting}
                handleSearch={this.handleArchiveSearch}
              />
            </Card.Body>
          </Card>

        </Tab>
        <Tab eventKey="Create" title="Create pramotions">
          <Row>
            <Col>
                {/* <p>Raw denim you probably haven't heard of them jean shorts Austin. Nesciunt tofu stumptown aliqua, retro synth master cleanse. Mustache cliche tempor, williamsburg carles vegan helvetica. Reprehenderit butcher retro keffiyeh dreamcatcher synth. Cosby sweater eu banh mi, qui irure terry richardson ex squid. Aliquip placeat salvia cillum iphone. Seitan aliquip quis cardigan american apparel, butcher voluptate nisi qui.</p> */}
                <ValidationForm onSubmit={this.handleSubmit}>
                    <Row>
                        <Col md="6">
                            <Form.Group>
                                <Form.Label>Select Merchant</Form.Label>
                                <RenderBootstrapSingleSelect
                                    options={this.props.merchantList}
                                    placeholder={"Please Select Merchant"}
                                    name="merchant"
                                    value={this.state.selectedMerchant}
                                    required
                                    errorMessage={{ validator: validations.required }}
                                    onChange={this.handleMerchantChange} />
                            </Form.Group>
                        </Col>
                        <Col md={6}>
                            <Form.Group>
                            <Form.Label>Title</Form.Label>
                            <RenderTextField
                                name="title"
                                placeholder=""
                                maxLength={25}
                                onChange={this.handleChange}
                                value={this.state.title || ''}
                                validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                errorMessage={{ validator: validations.required }}
                            />
                            </Form.Group>
                        </Col>
                    </Row>
                    <Row>
                        <Col md="6">
                            <Form.Group>
                                <Form.Label>Select Activity</Form.Label>
                                <RenderBootstrapSingleSelect
                                    options={this.props.alreadyAddedActivity}
                                    placeholder={"Please Select Activity"}
                                    name="alreadyAddedActivities"
                                    value={this.state.selectedActivity}
                                    required
                                    errorMessage={{ validator: validations.required }}
                                    onChange={this.handleActivityChange} />
                            </Form.Group>
                        </Col>
                        <Col md={6}>
                        <Form.Group>
                            <Form.Label>Outlet Name</Form.Label>
                            <RenderSearchableMultiSelect
                            name="outletName"
                            value={this.state.selectedOutletName}  
                            onChange={this.handleOutletOnChange}
                            placeholder="Select outlet name .."
                            required
                            options={this.props.activityOutlets}
                            />
                        </Form.Group>
                        </Col>
                    </Row>
                    <Row>
                        <Col md={6}>
                            <Form.Group>
                            <Form.Label>Pramotion Description</Form.Label>
                            <RenderTextField
                                name="description"
                                placeholder=""
                                maxLength={25}
                                onChange={this.handleChange}
                                value={this.state.description || ''}
                                validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                errorMessage={{ validator: validations.required }}
                            />
                            </Form.Group>
                        </Col>
                        <Col md={6}>
                            <Form.Label>
                                Number of Pramotion Tickets 
                            </Form.Label>
                            <span data-toggle="tooltip" className="InfoTooltip" data-placement="top" title="Number of couples that can redeem the pramotion is equivalent to the number of pramotinal tickets divided by 2">
                                <i class="feather icon-alert-circle"></i>
                            </span>
                            <RenderNumberField
                            name="pramotion_ticket"
                            placeholder=""
                            validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                            errorMessage={{ validator: 'Invalid hours' }}
                            onChange={this.handleChange}
                            value={this.state.pramotion_ticket}
                            />
                        </Col>
                    </Row>
                    <Row>
                        <Col md={6}>
                            <Form.Group>
                            <Form.Label>Original Price</Form.Label>
                            <RenderPrefixedPriceField
                                name="originalPrice"
                                placeholder="Original Price"
                                required
                                prefix="SGD"
                                validator={validator.isFloat}
                                onChange={this.handleChange}
                                value={this.state.originalPrice}
                                errorMessage={{ validator: 'Invalid Price' }}
                            />
                            </Form.Group>
                        </Col>
                        <Col md="6">
                            <Form.Group>
                                <Form.Label>Start Date</Form.Label>
                                <Datetime name="startDate" 
                                isValidDate={this.validationStartDate}
                                errorMessage={{ validator: validations.required }}
                                timeFormat={false} onChange={this.handleStartDateChange}  dateFormat={"DD MMM YYYY"} inputProps={{ placeholder: 'Select Date' }} />
                            </Form.Group>
                        </Col>
                    </Row>
                    <Row>
                        <Col md="6">
                            <Form.Group>
                            <Form.Label>Discounted Price</Form.Label>
                            <RenderPrefixedPriceField
                                name="discountedPrice"
                                placeholder="Discounted Price"
                                required
                                prefix="SGD"
                                disabled={this.state.originalPrice ? false : true}
                                validator={validator.isFloat}
                                onChange={this.handleChange}
                                value={this.state.discountedPrice}
                                errorMessage={{ validator: 'Invalid price' }}
                            />
                            </Form.Group>
                        </Col>
                        <Col md="6">
                            <Form.Group>
                                <Form.Label>End Date</Form.Label>
                                <Datetime name="endDate"                                                    
                                validator={(str) => !validator.isEmpty(str)}
                                isValidDate={this.validationEndDate}
                                errorMessage={{ validator: validations.required }}
                                timeFormat={false} onChange={this.handleEndDateChange}
                                dateFormat={"DD MMM YYYY"}
                                inputProps={{ placeholder: 'Select Date' }}
                                value={this.state.endDate} />
                            </Form.Group>
                        </Col>
                    </Row>
                    <Row>
                        
                        <Col md="6">
                            <Form.Group>
                            <Form.Label>Discounted Percentage</Form.Label>
                            <RenderAppendTextField
                                name="discountedPercentage"
                                placeholder="Discounted %"
                                validator={(str) => validator.isFloat(str) && parseFloat(str, 10) <= 100}
                                errorMessage={{ validator: 'Invalid percentage' }}
                                onChange={this.handleChange}
                                disabled={this.state.originalPrice ? false : true}
                                value={this.state.discountedPercentage}
                                appendText="%" />
                            </Form.Group>
                        </Col>
                    </Row>
                    <Row>
                        <Col md="6">
                            <Form.Group>
                            <Form.Label>Discounted Amount</Form.Label>
                            <RenderPrefixedPriceField
                                name="discountAmount"
                                placeholder="Discounted Amount"
                                validator={validator.isFloat}
                                prefix="SGD"
                                disabled={this.state.originalPrice ? false : true}
                                onChange={this.handleChange}
                                value={(this.state.discountAmount) || ''}
                                errorMessage={{ validator: 'Invalid price' }}
                            />
                            </Form.Group>
                        </Col>
                    </Row>
                    <Row>
                    
                        <Col md="12">
                            
                            { (this.state.terms).map((term, index) => 
                                <Row>
                                <Col md="6">
                                    <Form.Group>
                                        <Form.Label>Terms point {index+1}</Form.Label>
                                        <RenderTextField
                                        name={`term[${index}]`}
                                        disabled={index > 2 ? false : true}
                                        placeholder=""
                                        onChange={(input) => this.handleTermsOnChange(input, index )}
                                        value={this.state.terms && this.state.terms[index] &&  this.state.terms[index].point}
                                        validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                        errorMessage={{ validator: validations.required }}
                                        />
                                    </Form.Group>
                                </Col>
                                <Col md={6}>
                                    <Form.Group>
                                        {index > 2 ?
                                        <DeleteButton onClick={(event) => this.handleRemoveConfirm(event, index)}> Remove term</DeleteButton> : null
                                        }
                                        
                                    </Form.Group>
                                </Col>
                            </Row>
                            )}
                            
                            <SuccessButton onClick={this.handleAddTerms}>Add Terms point</SuccessButton> 
                        </Col>
                    </Row>
                    <hr></hr>
                    <Row>
                        <Col md="6">
                            <Row>
                                <Col>
                                    <Form.Group>
                                        <SubmitButton className="mt-10" >
                                            Submit
                                        </SubmitButton>
                                    </Form.Group>
                                </Col>
                                <Col md="6">
                                    <Form.Group>
                                        <DeleteButton className="mt-10">
                                            Cancel
                                        </DeleteButton>
                                    </Form.Group>
                                </Col>
                            </Row>

                        </Col>
                    </Row>
                </ValidationForm>
            </Col>
        </Row>
        
        </Tab>
      </Tabs>
    )
  }
}
