import React, { Component } from 'react'

import { Tabs, Tab } from 'react-bootstrap';
import { connect } from 'react-redux';
// import { getMerchantActivityList } from '../actions/index'
import { history} from './../../../../history';
import RenderDataTable from '../../../components/dataTable';
class Bookings extends Component {
  navigateEditBooking = (row) => {
    history.push(`/admin/dashboard/merchant/pendingBookings/${row.bookingId}`);
  }

  render() {


    const columns = [
      
      {
        name: "Booking ID",
        selector: "bookingId",
        sortable: true
      },
      {
        name: "Activity",
        selector: "activity",
        sortable: false,

      },
      {
        name: "Merchant",
        selector: "merchant",
        sortable: false,
      },
      {
        name: "Price (SGD)",
        selector: "price",
        sortable: true,
      },
      {
        name: "Qty",
        selector: "quantity",
        sortable: true,
      },
      {
        name: "Pramotion",
        selector: "pramotion",
        sortable: true,
      },
      {
        name: "Coupen Code",
        selector: "coupenCode",
        sortable: false,
      },
      {
        name: "Date Purchased",
        selector: "datePurchased",
        sortable: true,
      },
      {
        name: "Expiry Date",
        selector: "expiryDate",
        sortable: true,
      },


    ];


    return (


      <Tabs defaultActiveKey="activeBookings" >
        <Tab eventKey="activeBookings" title="Active Bookings">
          <RenderDataTable
            onRowClicked={this.navigateEditBooking}
            columns={columns}
            data={this.props.activitiesList || []}
            defaultSortField="datePurchased"
          />
        </Tab>
        <Tab eventKey="archievedBookings" title="Archieved Bookings">
          <RenderDataTable
            columns={columns}
            onRowClicked={this.navigateEditBooking}
            data={this.props.activitiesList || []}
            defaultSortField="datePurchased"
          />
        </Tab>
      </Tabs>



    )
  }
}
const mapStateToProps = () => {
  return {
    activitiesList: [
      {
        datePurchased: "24-Dec-2019", bookingId: 'xx-yy-zz', activity: 'Archiery', merchant: 'John', pramotion: 'Yes', coupenCode: 'NA', expiryDate: '21-Jan-2020', price: 45.21, quantity: 21
      },
      {
        datePurchased: "21-Dec-2019", bookingId: 'xx-yy-zz', activity: 'Paragliding', merchant: 'Michale', pramotion: 'No', coupenCode: '34x56(10% off)', expiryDate: '21-Feb-2020', price: 11.11, quantity: 2
      },
      {
        datePurchased: "24-Dec-2019", bookingId: 'xx-yy-zz', activity: 'Archiery', merchant: 'Wcoast', pramotion: 'Yes', coupenCode: 'NA', expiryDate: '21-Jan-2020', price: 123.11, quantity: 12
      }
    ]
  };
}
export default connect(mapStateToProps, null)(Bookings)
