import React from "react";
import DataTable from "react-data-table-component";
import { Pagination } from "react-bootstrap";
import PropTypes from "prop-types";
import FilterComponent from "./filterComponentForDataTable";


const sortIcon =
  <i className="feather icon-arrow-up"></i>;
export default function RenderDataTable({ totalRecords, dataLength,recordLimit,filterTextSearch, recordOffset, ...props }) {
  let AllData =[];
  let [activePage, setActivePage] = React.useState(1);
  let items = [];
  const totalPages = Math.ceil(totalRecords / recordLimit);
  const from = recordOffset ? recordOffset + 1 : 1;
  const limitRecord = recordLimit
  const to = recordLimit && totalRecords && (from + recordLimit >= totalRecords ? totalRecords : from + (recordLimit - 1));
  AllData = props.data;
  
  const onSerach = (text)=>{
    
    props.handleSearch(text)
  }
  const handlePageChange = (pageNumber) => {
    setActivePage(pageNumber);
    if (props.handlePageChange) {
      props.handlePageChange(pageNumber)
    }

  }
  const handleNextButton = (event) => {
    if (activePage < totalPages) {
      setActivePage(activePage + 1);
      if (props.handlePageChange) {
        props.handlePageChange(activePage + 1)
      }
    }

  }
  
  const handleBackButton = (event) => {
    if (activePage > 1) {
      setActivePage(activePage - 1);
      if (props.handlePageChange) {
        props.handlePageChange(activePage - 1)
      }
    }
  }
  const onChangeRecordsLimit = (event) => {
    if (props.onChangeRecordsLimit) {
      props.onChangeRecordsLimit(event)
      
      
    }
  }
  
  if (totalPages === 1 && activePage !== 1) {
    
    handlePageChange(1)
  }
  for (let page = 1; page <= totalPages; page++) {
    // console.log("totalpage",totalPages);
    items.push(
      <Pagination.Item key={page} onClick={() => handlePageChange(page)} active={totalPages <= 1 ? page === 1 : page === activePage}>
        {page}
      </Pagination.Item>
    );
  }
  const handleClear = () => {
    if (filterTextSearch !== '') {
      setResetPaginationToggle(!resetPaginationToggle);
      setFilterText("");
      filterTextSearch = '';
      onSerach(filterTextSearch)
    }
  };

  const [filterText, setFilterText] = React.useState(filterTextSearch);
  const [resetPaginationToggle, setResetPaginationToggle] = React.useState(
    false
  );

  const subHeaderComponentMemo = React.useMemo(() => {
   
    
    return (
      <>
        {!props.noPagination && <div className="recordsPerPage">
          <span>Show</span>
          <select name="rowsPerPage" value={recordLimit} onChange={onChangeRecordsLimit}>
            <option>10</option>
            <option>20</option>
            <option>30</option>
            <option>40</option>
          </select>
          <span>entries</span>
        </div>
        }
        {!props.noPagination && 
        <FilterComponent
          onFilter={e => onSerach(e.target.value)}
          onClear={handleClear}
          filterText={filterTextSearch}
        />
        }
       
      </>
    );
  }, [filterTextSearch, resetPaginationToggle, recordLimit, props]);
    
    
  console.log(AllData.length)
  return (
    <DataTable
      {...props}
      pagination={props.noPagination ? false : true}
      paginationComponent={() => (
        <Pagination>
          {" "}
          <span className="number">Showing {from} to {to} of {totalRecords} entries</span>
          <Pagination.Prev disabled={totalPages <= 1 || activePage === 1} onClick={handleBackButton} />
          {items}
          <Pagination.Next disabled={totalPages <= 1 || activePage === totalPages} onClick={handleNextButton} />
        </Pagination>
      )}
      data={AllData}
      paginationPerPage={ AllData.length || recordLimit}
      selectableRows={props.selectableRows || false}
      highlightOnHover
      paginationServer
      sortIcon={sortIcon}
      subHeader
      subHeaderWrap={false}
      subHeaderAlign={"left"}
      noHeader={true}
      subHeaderComponent={subHeaderComponentMemo}
      paginationResetDefaultPage={resetPaginationToggle} // optionally, a hook to reset pagination to page 1

    />
  );

}
RenderDataTable.propTypes = {
  data: PropTypes.array.isRequired,
  columns: PropTypes.array.isRequired,

  // title: PropTypes.string.isRequired
};

