import React from 'react';
import ReactSelect from 'react-select';
import { Form, Button, InputGroup } from 'react-bootstrap';
import { TextInput, SelectGroup, FileInput } from 'react-bootstrap4-form-validation';
import MaskedDateFieldWithValidation from './maskedDateFieldWithValidation';

import { toast } from 'react-toastify';
import MaskedPhoneFieldWithValidation from './maskedPhoneFieldWithValidation';
import MaskedTimeFieldWithValidation from './maskedTimeFieldWithValidation';
export const showWarningMsg = (msg) => {
  return toast.warn(msg, {
    position: toast.POSITION.TOP_CENTER,
    autoClose: 1 * 1000
  });
}
export const showInfoMsg = (msg, callBackfunction) => {
  const Msg = ({ closeToast }) => (
    <div>
      <div className="d-block pb-2">{msg}</div>

      <div className="d-block">
        <SuccessButton className="mr-3" onClick={callBackfunction}>Proceed</SuccessButton>
        <DeleteButton onClick={closeToast}>Close</DeleteButton>
      </div>

    </div>
  )
  return toast.info(<Msg />, {
    position: toast.POSITION.BOTTOM_RIGHT,
    autoClose: 5 * 1000
  });
}
export const showAlertMsg = (msg, callBackfunction) => {
  const Msg = ({ closeToast }) => (
    <div>
      <div className="d-block pb-2">{msg}</div>

      <div className="d-block">
        <SuccessButton className="mr-3" onClick={closeToast}>Ok</SuccessButton>
      </div>

    </div>
  )
  return toast.info(<Msg />, {
    position: toast.POSITION.BOTTOM_RIGHT,
    autoClose: 5 * 1000
  });
}
export const showSuccessMsg = (msg) => {
  return toast.success(msg, {
    position: toast.POSITION.TOP_CENTER,
    autoClose: 1 * 1000
  });
}
export const showErrorMsg = (msg) => {
  return toast.error(msg, {
    position: toast.POSITION.TOP_CENTER,
    autoClose: 1 * 1000
  });
}



export const RenderButton = (props) => {
  return <Button {...props} className={props.className} type="button">{props.children}</Button>
}
export const SubmitButton = (props) => {
  return <Button {...props} className={`green-bg shadow-2 m-1 ${props.className || ''}`} type="submit">{props.children}</Button>
}
export const SuccessButton = (props) => {
  return <Button {...props} className={`green-bg shadow-2 m-1 ${props.className || ''}`} type="button">{props.children}</Button>
}
export const DeleteButton = (props) => {
  return <Button {...props} className={`shadow-2 m-1 ${props.className || ''}`} >{props.children}</Button>
}
export const WarningButton = (props) => {
  return <Button {...props} className={`orange-bg shadow-2 m-1 ${props.className || ''}`} >{props.children}</Button>
}
export const RenderTextField = React.forwardRef((props, ref) => {


  // console.log(ref)
  // let value = ref && ref.current && ref.current.infputRef && ref.current.infputRef.target && ref.current.infputRef.target.value
  // ;
  // console.log('value----', value);
  // console.log('props.maxLength && ref && ref.current && !ref.current.state.isPristine---', props.maxLength && ref && ref.current && !ref.current.state.isPristine)
  const [inputValue, setValue] = React.useState('');
  const [isDirty, setDirty] = React.useState(false);
  const handleOnChange = (event) => {

    setDirty(true)
    setValue(event.target.value)
    if (props.onChange) {
      props.onChange(event)
    }
  }
  return <>
    <TextInput
      {...props}
      type="text"
      ref={ref}
      onChange={handleOnChange}
      className={`form-control ${props.className || ''}`}
      autoComplete="off"
    />
    {props.maxLength && isDirty && <div className="char_limit"><span>{inputValue ? inputValue.length : 0}/{props.maxLength}</span></div>}
  </>
})

