export const RenderTextArea = (props) => {
  const [inputValue, setValue] = React.useState('');
  const [isDirty, setDirty] = React.useState(false);
  const handleOnChange = (event) => {

    setDirty(true)
    setValue(event.target.value)
    if (props.onChange) {
      props.onChange(event)
    }
  }
  return (
    <>
      <TextInput
        {...props}
        multiline
        onChange={handleOnChange}
        rows="3"
        autoComplete="off"
      />
      {props.maxLength && isDirty && <div className="char_limit"><span>{inputValue ? inputValue.length : 0}/{props.maxLength}</span></div>}
    </>
  )
}
export const RenderEmailField = (props) => {
  return <TextInput
    {...props}
    type="email"
    autoComplete="off"
  />
}

export const RenderPasswordField = (props) => {
  return <TextInput
    {...props}
    type="password"
    autoComplete="off"
  />
}
export const RenderCheckbox = (props) => {
  return <input

    {...props}
    type="checkbox"
  />
}
export const RenderSingleSelect = React.forwardRef(({ options, selectKey, ...props }, ref) => {
  const renderOptions = (options) => {
    return options && options.length ? options.map((item, index) => <option key={index} value={item.value}>{item.label}</option>) : ''
  }
  return <SelectGroup
    {...props}
    ref={ref}
    multiple={false}

  >
    <option value="">{`Please Select  ${selectKey || 'option'}`}</option>


    {renderOptions(options)}


  </SelectGroup>
})
export const RenderSearchableMultiSelect = (props) => {

  // <MaskedMultiSelectWithValidation {...props}/>
 
  return <ReactSelect
    {...props}
    isSearchable={true}
    isClearable
    className={"basic-multi-select m-2"}

    classNamePrefix="select"
    isMulti={true}
  />
}

export const RenderSearchableSingleSelect = (props) => {

  return <>
    <ReactSelect
      {...props}
      isSearchable={true}
      isClearable
      className={"basic-single m-2"}
      classNamePrefix="select"
      isMulti={false}
    />

  </>
}

export const RenderFileUpload = () => {
  return (
    <InputGroup className="mb-3 cust-file-button">
      <div className="custom-file">
        <Form.Control
          aria-describedby="custom-addons8"
          type="file"
          className="custom-file-input"
          id="validatedCustomFile4"
        />
        <Form.Label className="custom-file-label" htmlFor="validatedCustomFile4">Choose file</Form.Label>
      </div>
      <InputGroup.Append>
        <Button id="custom-addons8">Button</Button>
      </InputGroup.Append>
    </InputGroup>

  )
}
export const RenderPrefixFieldWithoutValidation = (props) => {
  return (
    <InputGroup>
      <InputGroup.Prepend>
        <InputGroup.Text id="inputGroupPrepend">{props.prefix}</InputGroup.Text>
      </InputGroup.Prepend>
      <Form.Control
        type="text"
        placeholder={props.placeholder}
        aria-describedby="inputGroupPrepend"
        required
      />
      <Form.Control.Feedback type="invalid">
        {props.placeholder}
      </Form.Control.Feedback>
    </InputGroup>
  )
}
export const RenderPrefixedUrlField = (props) => {
  return (

    <div className="input-group mb-3">
      <div className="input-group-prepend">
        <span className="input-group-text">{props.prefix || 'https://'}</span>
      </div>
      <RenderTextField className="form-control"
        {...props} />
    </div>

  )
}
export const RenderPrefixedPriceField = (props) => {
  return (

    <div className="input-group mb-3">
      <div className="input-group-prepend">
        <span className="input-group-text">{props.prefix}</span>
      </div>
      <RenderTextField className="form-control"
        {...props} />
    </div>

  )
}


export const RenderAppendTextField = ({ appendText, ...props }) => {
  return (

    <div className="input-group mb-3">
      <RenderTextField className="form-control"
        {...props} />
      <div className="input-group-append">
        <span className="input-group-text">{appendText}</span>
      </div>
    </div>

  )
}
export const RenderPhoneField = (props) => {
  return (<MaskedPhoneFieldWithValidation
    {...props}
    mask={[/[0-9]/, /[0-9]/, '-', /[0-9]/, /[0-9]/, /[0-9]/, '-', /[0-9]/, /[0-9]/, /[0-9]/]}
    autoComplete="off"
    className={`form-control ${props.className || ''}`}

  />
  )
}
export const RenderImageUpload = (props) => {
  return (
    <div className="custom-file">
      <FileInput
        {...props}
        fileType={["png", "jpg", "jpeg"]}
        // maxFileSize="100 kb"
        errorMessage={
          {
            required: "Please upload a file",
            fileType: "Only .png or .jpg file is allowed",
            // maxFileSize: "Max file size is 100 kb"
          }
        } />
    </div>

  )
}
export const RenderValidationTimeField = (props) => {
  return <MaskedTimeFieldWithValidation
    {...props}
  />
}
export const RenderValidationDateField = (props) => {
  return <MaskedDateFieldWithValidation
    {...props}
  />
}

/**
 * 
 * Reactjs bootstrap components for outsideuse of forms
 * 
 */
export const RenderBootstrapSingleSelect = (props) => {

  return (
    <ReactSelect
      {...props}
      className="basic-single m-2"
      classNamePrefix="select"
      placeholder={props.placeholder || 'select'}
      isMulti={false}
    />)
}


export const RenderBootstrapTextField = (props) => {
  return (<Form.Control {...props}
    type="text"
    autoComplete="off" />)

}
export const RenderBootstrapEmailField = (props) => {
  return (<Form.Control
    {...props}
    type="email"
    autoComplete="off"
  />)

}
export const RenderBootstrapTextArea = (props) => {
  return (<Form.Control {...props}
    as="textarea"
    rows="3"
    autoComplete="off"
  />)
}
