import React, { Component } from 'react'
import { ValidationForm } from 'react-bootstrap4-form-validation';
import { RenderPhoneField, RenderValidationDateField, RenderSingleSelect, RenderTextField, SubmitButton, DeleteButton, showInfoMsg } from './../../components/commonUIComponents';
import { Row, Col, Form, Card } from 'react-bootstrap';
import { connect } from 'react-redux';
import validator from 'validator';
import validations from '../../validations';
import MainCard from "../../../App/components/MainCard";
import DropzoneComponent from 'react-dropzone-component';
import { addBankDetails, deletePaymentAccount, getAccountDetails } from './actions/index'
import { injectStripe } from 'react-stripe-elements';
import { capitalizeFirstChar } from './../../utilActions';

class SettlementsRemittance extends Component {
    constructor(props) {
        super(props);

        let subHeaderObj = {
            subHeader: '',
            description: ''
        };
        let suggetionsObj = {
            title: '',
            images: '',
            subTitleType: '',
            subTitle: '',
            subHeaders: [{ ...subHeaderObj }],

        }

        this.state = {
            type: '',
            letterValue: '',
            suggestionsArray: [{ ...suggetionsObj }],
            bank_id: null,
            bank_token: null,
            mobileNumber: null

        }
        this.djsConfig = {
            addRemoveLinks: false,
            acceptedFiles: "image/jpeg,image/png,image/gif",
            autoProcessQueue: false,
            uploadprogress: 100
        };

        this.componentConfig = {
            iconFiletypes: ['.jpg', '.png', '.gif'],
            showFiletypeIcon: true,
            postUrl: 'no-url'
        };
    }

    componentDidMount() {
        this.props.getAccountDetails()
    }

    handleRemoveConfirm = (event) => {
        event.preventDefault();
        showInfoMsg('Are you Sure To Delete?', () => this.deleteAccount())

    }
    deleteAccount = () => {
        if (this.state.bank_id || this.props.accountDetails.id) {
            if (this.props.accountDetails.id) {
                this.setState({
                    bank_id: this.props.accountDetails.id
                }, () => {
                    this.props.deletePaymentAccount(this.state.bank_id)
                })
            }
        }
    }
    handleFileAdded = (file, name) => {
        this.setState({
            [name]: file
        })
    }
    openStatusChangeModal = (event) => {
        this.setState({
            showActivityChangeModal: true,

        })
        event.preventDefault();
    }
    modalClosed = () => {
        this.setState({
            showActivityChangeModal: false,
        })
    }
    handlePhone = (ev) => {
        var ph = ev.target.value.replace(/-/g, '')
        this.setState({
            mobileNumber: ph
        })

    }
    handleSubmit = (event, formData) => {
        event.preventDefault()
        var obj = {}
        obj.firstName = formData.firstName
        obj.lastName = formData.lastName
        obj.countryCode = 'SG'
        obj.city = formData.city
        obj.url = 'http://google.com'
        obj.mcc = '4722'
        obj.state = formData.state
        obj.line1 = formData.addres
        obj.line2 = ''
        obj.postaladdress = formData.postal
        obj.postal = formData.postal
        obj.dob = formData.dob
        obj.gender = formData.gender
        obj.phone = this.state.mobileNumber
        obj.bankId = formData.accno
        obj.front = this.state.front
        obj.back = this.state.back
        obj.additionalFront = this.state.additionalFront
        obj.additionalBack = this.state.additionalBack

        this.props.stripe.createToken('bank_account', {
            country: 'SG',
            currency: 'SGD',
            routing_number: '1100-000',
            account_number: '000123456',
            account_holder_name: formData.firstName,
            account_holder_type: 'individual',
        }).then(token => {
            this.setState({
                bank_token: token.token.id,
                bank_id: token.token.bank_account.id
            })
            obj.token = token.token.id
            this.props.addBankDetails(obj)

        }).catch(error => console.log(error))




    }
    render() {
        const config = this.componentConfig;
        const djsConfig = this.djsConfig;

        const eventHandlers = (name) => ({
            addedfile: (file) => this.handleFileAdded(file, name)
        });
        return (
            <>
                <Card>
                    <Card.Body>
                        <Row>
                            <Col>
                                <MainCard isOption title="Stripe (For existing stripe account holder)" cardClass="tour-1 card-test">
                                    <Card.Text>Stripe (For existing stripe account holder)</Card.Text>
                                </MainCard>

                            </Col>
                        </Row>

                        {!this.props.accountDetails || !this.props.accountDetails.id ? <Row>
                            <Col><MainCard isOption title="Bank Details (For non stripe account holders)" cardClass="tour-1 card-test">
                                <Row>
                                    <Col>
                                        <Row>
                                            <Col>
                                                {/* <p>Raw denim you probably haven't heard of them jean shorts Austin. Nesciunt tofu stumptown aliqua, retro synth master cleanse. Mustache cliche tempor, williamsburg carles vegan helvetica. Reprehenderit butcher retro keffiyeh dreamcatcher synth. Cosby sweater eu banh mi, qui irure terry richardson ex squid. Aliquip placeat salvia cillum iphone. Seitan aliquip quis cardigan american apparel, butcher voluptate nisi qui.</p> */}
                                                <ValidationForm onSubmit={this.handleSubmit}>
                                                    {/* <h6>Bank Details</h6>
                                                        <hr /> */}
                                                    <Row>
                                                        <Col md="6">
                                                            <Form.Group>
                                                                <Form.Label>Firstname</Form.Label>
                                                                <RenderTextField
                                                                    name="firstName"
                                                                    placeholder="First Name"
                                                                    errorMessage={{ validator: validations.required }}
                                                                    validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                                                />
                                                            </Form.Group>
                                                        </Col>
                                                        <Col md="6">
                                                            <Form.Group>
                                                                <Form.Label>Lastname</Form.Label>
                                                                <RenderTextField
                                                                    name="lastName"
                                                                    placeholder="Last Name"
                                                                    errorMessage={{ validator: validations.required }}
                                                                    validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                                                />
                                                            </Form.Group>
                                                        </Col>
                                                    </Row>
                                                    <Row>
                                                        <Col md="6">
                                                            <Form.Group>
                                                                <Form.Label>Account Number</Form.Label>
                                                                <RenderTextField
                                                                    name="accno"
                                                                    placeholder="Account Number"
                                                                    errorMessage={{ validator: validations.required }}
                                                                    validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                                                />
                                                            </Form.Group>
                                                        </Col>
                                                        <Col md="6">
                                                            <Form.Group>
                                                                <Form.Label>Routing Number</Form.Label>
                                                                <RenderTextField
                                                                    name="rno"
                                                                    placeholder="Routing Number"
                                                                    errorMessage={{ validator: validations.required }}
                                                                    validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                                                />
                                                            </Form.Group>
                                                        </Col>
                                                    </Row>
                                                    <Row>
                                                        <Col md="6">
                                                            <Form.Group>
                                                                <Form.Label>Date Of Birth</Form.Label>

                                                                <RenderValidationDateField name='dob' />
                                                            </Form.Group>
                                                        </Col>
                                                        <Col md="6">
                                                            <Form.Group>
                                                                <Form.Label>Mobile</Form.Label>
                                                                <RenderPhoneField
                                                                    name="phone"
                                                                    placeholder="Mobile Number"
                                                                    // onChange={this.handlePhone}
                                                                    errorMessage={{ validator: validations.required }}
                                                                    validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                                                />
                                                            </Form.Group>Mobile
                                                            </Col>
                                                    </Row>
                                                    <Row>
                                                        <Col md={6}>
                                                            <Form.Group>
                                                                <Form.Label>Gender</Form.Label>
                                                                <RenderSingleSelect
                                                                    name="gender"
                                                                    value={this.state.gender}
                                                                    required
                                                                    validator={validations.required}
                                                                    options={this.props.genderOptions}
                                                                />
                                                            </Form.Group>
                                                        </Col>
                                                        <Col md="6">
                                                            <Form.Group>
                                                                <Form.Label>City</Form.Label>
                                                                <RenderTextField
                                                                    name="city"
                                                                    placeholder="City"
                                                                    errorMessage={{ validator: validations.required }}
                                                                    validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                                                />
                                                            </Form.Group>
                                                        </Col>
                                                    </Row>
                                                    <Row>
                                                        <Col md="6">
                                                            <Form.Group>
                                                                <Form.Label>Address</Form.Label>
                                                                <RenderTextField
                                                                    name="addres"
                                                                    placeholder="Address"
                                                                    errorMessage={{ validator: validations.required }}
                                                                    validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                                                />
                                                            </Form.Group>
                                                        </Col>
                                                        <Col md="6">
                                                            <Form.Group>
                                                                <Form.Label>Zipcode</Form.Label>
                                                                <RenderTextField
                                                                    name="postal"
                                                                    placeholder="Zipcode"
                                                                    errorMessage={{ validator: validations.required }}
                                                                    validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                                                />
                                                            </Form.Group>
                                                        </Col>
                                                    </Row>
                                                    <Row>

                                                        <Col md="6">
                                                            <Form.Group>
                                                                <Form.Label>State</Form.Label>
                                                                <RenderTextField
                                                                    name="state"
                                                                    placeholder="State"
                                                                    errorMessage={{ validator: validations.required }}
                                                                    validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                                                />
                                                            </Form.Group>
                                                        </Col>
                                                        <Col md="6">
                                                            <Form.Group>
                                                                <Form.Label>Country</Form.Label>
                                                                <RenderTextField
                                                                    name="country"
                                                                    placeholder="Country"
                                                                    errorMessage={{ validator: validations.required }}
                                                                    validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                                                />
                                                            </Form.Group>
                                                        </Col>

                                                    </Row>

                                                    <Col md="12">
                                                        <Row>
                                                            <Col md="5">
                                                                <Form.Group>
                                                                    <Form.Label>Beneficial/Owner Front Image</Form.Label>
                                                                    <DropzoneComponent config={config} eventHandlers={eventHandlers("front")} djsConfig={djsConfig} />
                                                                </Form.Group>
                                                            </Col>
                                                            <Col md="5">
                                                                <Form.Group>
                                                                    <Form.Label>Beneficial/Owner Back Image</Form.Label>
                                                                    <DropzoneComponent config={config} eventHandlers={eventHandlers("back")} djsConfig={djsConfig} />
                                                                </Form.Group>
                                                            </Col>
                                                        </Row>
                                                    </Col>
                                                    <Col md="12">
                                                        <Row>
                                                            <Col md="5">
                                                                <Form.Group>
                                                                    <Form.Label>Representative Front Image</Form.Label>
                                                                    <DropzoneComponent config={config} eventHandlers={eventHandlers("additionalFront")} djsConfig={djsConfig} />
                                                                </Form.Group>
                                                            </Col>
                                                            <Col md="5">
                                                                <Form.Group>
                                                                    <Form.Label>Representative Back Image</Form.Label>
                                                                    <DropzoneComponent config={config} eventHandlers={eventHandlers("additionalBack")} djsConfig={djsConfig} />
                                                                </Form.Group>
                                                            </Col>
                                                        </Row>
                                                    </Col>

                                                    <Row>
                                                        <Col md="12">
                                                            <Row>
                                                                <Col md="4">
                                                                    <Form.Group>
                                                                        <SubmitButton className="mt-10">
                                                                            Submit
                                                                             </SubmitButton>
                                                                    </Form.Group>
                                                                </Col>
                                                                <Col md="4">
                                                                    <Form.Group>
                                                                        <DeleteButton className="mt-10">
                                                                            Cancel
                                                                            </DeleteButton>
                                                                    </Form.Group>
                                                                </Col>

                                                            </Row>

                                                        </Col>
                                                    </Row>
                                                </ValidationForm>
                                            </Col>
                                        </Row>
                                        {/* <AnimatedModal showActivityChangeModal={this.state.showActivityChangeModal} modalClosed={this.modalClosed}>
                                                <div className="text-center">
                                                    <h4 className="modal-class">Thanks you for your interest !<br></br> Our team will contact you shortly with our pricing structure.</h4>
                                                    <SubmitButton className="modal-button text-center" onClick={this.modalClosed}>OK</SubmitButton>

                                                </div>

                                            </AnimatedModal> */}
                                    </Col>
                                </Row>
                            </MainCard>  </Col>
                        </Row> : null}


                        {this.props.accountDetails && this.props.accountDetails.id ? <Row>
                            <Col>
                                <MainCard isOption title="View Bank Details" cardClass="tour-1 card-test">
                                    <Row>
                                        <Col>
                                            <Row>
                                                <Col>

                                                    {/* <h6>Bank Details</h6>
                                                        <hr /> */}
                                                    <Row>
                                                        <Col md="6">
                                                            <Form.Group as={Row} controlId="formHorizontalLetterEarned">
                                                                <Form.Label column sm={4}>Name</Form.Label>
                                                                <Col md="6">
                                                                    <Form.Control key="letter-earned" plaintext readOnly value={this.props.accountDetails && capitalizeFirstChar(this.props.accountDetails.account_holder_name)} />
                                                                </Col>
                                                            </Form.Group>
                                                        </Col>
                                                    </Row>
                                                    <Row>
                                                        <Col md="6">
                                                            <Form.Group as={Row} controlId="formHorizontalLetterEarned">
                                                                <Form.Label column sm={4}>Account Type</Form.Label>
                                                                <Col md="6">
                                                                    <Form.Control key="letter-earned" plaintext readOnly value={this.props.accountDetails && capitalizeFirstChar(this.props.accountDetails.account_holder_type)} />
                                                                </Col>
                                                            </Form.Group>
                                                        </Col>
                                                    </Row>
                                                    <Row>
                                                        <Col md="6">
                                                            <Form.Group as={Row} controlId="formHorizontalLetterEarned">
                                                                <Form.Label column sm={4}>Bank Name</Form.Label>
                                                                <Col md="6">
                                                                    <Form.Control key="letter-earned" plaintext readOnly value={this.props.accountDetails && capitalizeFirstChar(this.props.accountDetails.bank_name
                                                                    )} />
                                                                </Col>
                                                            </Form.Group>
                                                        </Col>
                                                    </Row>
                                                    <Row>
                                                        <Col md="6">
                                                            <Form.Group as={Row} controlId="formHorizontalLetterEarned">
                                                                <Form.Label column sm={4}>Currency</Form.Label>
                                                                <Col md="6">
                                                                    <Form.Control key="letter-earned" plaintext readOnly value={this.props.accountDetails && capitalizeFirstChar(this.props.accountDetails.currency).toUpperCase()} />
                                                                </Col>
                                                            </Form.Group>
                                                        </Col>
                                                    </Row>
                                                    <Row>
                                                        <Col md="6">
                                                            <Form.Group as={Row} controlId="formHorizontalLetterEarned">
                                                                <Form.Label column sm={4}>Routing number</Form.Label>
                                                                <Col md="6">
                                                                    <Form.Control key="letter-earned" plaintext readOnly value={this.props.accountDetails && capitalizeFirstChar(this.props.accountDetails.routing_number)} />
                                                                </Col>
                                                            </Form.Group>
                                                        </Col>
                                                    </Row>
                                                    <Row>
                                                        <Col md={{ offset: 10, span: 2 }}>
                                                            <Form.Group>
                                                                <DeleteButton className="mt-10" onClick={this.handleRemoveConfirm}>
                                                                    Delete
                                  </DeleteButton>
                                                            </Form.Group>
                                                        </Col>
                                                    </Row>
                                                </Col>
                                            </Row>
                                            {/* <AnimatedModal showActivityChangeModal={this.state.showActivityChangeModal} modalClosed={this.modalClosed}>
                                                <div className="text-center">
                                                    <h4 className="modal-class">Thanks you for your interest !<br></br> Our team will contact you shortly with our pricing structure.</h4>
                                                    <SubmitButton className="modal-button text-center" onClick={this.modalClosed}>OK</SubmitButton>

                                                </div>

                                            </AnimatedModal> */}

                                        </Col>
                                    </Row>
                                </MainCard>

                            </Col>
                        </Row>
                            : null}
                    </Card.Body>
                </Card>
            </>

        )
    }
}
const mapStateToProps = (state) => {
    return {
        accountDetails: state.merchant && state.merchant.accountDetails && state.merchant.accountDetails.data && state.merchant.accountDetails.data.data && state.merchant.accountDetails.data.data[0],

        genderOptions: [
            { value: 'male', label: 'Male' },
            { value: 'female', label: 'Female' }
        ],
    };

}


export default connect(mapStateToProps, { addBankDetails, deletePaymentAccount, getAccountDetails })(injectStripe(SettlementsRemittance))
