import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Row, Col, Form, Card } from 'react-bootstrap';
import { RenderTextField, RenderValidationDateField, RenderTextArea, SuccessButton, DeleteButton, RenderPrefixedPriceField,showInfoMsg } from '../../../components/commonUIComponents';
import { ValidationForm } from 'react-bootstrap4-form-validation';
import Datetime from 'react-datetime';
import validator from 'validator';
import { getPramotionDetailsById,ProccedRequest } from '../actions/index';
import validations from '../../../validations';
import moment from 'moment';
let outlets = []
class ViewPramotionRequest extends Component {
  constructor(props) {
    super(props);
    this.state = {
      buttonClicked: '',
      pramotionDetail:[],
      outlets:[],
    }
  }
  componentDidMount() {
    let requestId = window.location.pathname.split('/').pop();
    if (requestId && requestId !== 'add') {
      this.props.getPramotionDetailsById(requestId);
    }
  }
  componentDidUpdate(prevProps, newState) {
    
    if (this.props.requestDetail && this.props.requestDetail !== prevProps.requestDetail) {
      this.setState({
        pramotionDetail:this.props.requestDetail
      })
    }
  }
  Accept =() =>{
    this.setState({
      buttonClicked : 'accept'
    })
  }
  acceptSubmmit = () => {
    showInfoMsg('Are you Sure To Accept this Request?', () => this.acceptRequest('1'))
  }
  acceptRequest =(e)=>{
    let payload ={
      "status":e,
      "id":this.props.requestDetail.featuredId
    }
    this.props.ProccedRequest(payload)
  }
  reject = () => {
    this.setState({
      buttonClicked : 'reject'
    })
  }
  rejectSubmit =() =>{
    showInfoMsg('Are you Sure To Reject this Request?', () => this.rejectRequest('0'))
  }
  
  rejectRequest =(e)=>{
    let payload ={
      "status":e,
      "id":this.props.requestDetail.featuredId
    }
    this.props.ProccedRequest(payload)
  }
  render() {
    return (
      <Row>
      <Col sm={12}>
      <Card>
        <Card.Body>

          <Row>
            <Col md={12}>
            {this.props.requestDetail && this.props.requestDetail.status == '1' ?
              <h5>Active Pramotion Request</h5>:<h5>Archived Pramotion Request</h5>
            }
              <hr />
            </Col>
          </Row>
          <Form.Group as={Row} >
            <Form.Label column sm={2}>
              Pramotion Id
            </Form.Label>
            <Col sm={4}>
              <span className="DetailSpan">{this.props.requestDetail && this.props.requestDetail.id}</span>
            </Col>
            {this.props.requestDetail && this.props.requestDetail.status == '1' ?'':
              <span>
                <Form.Label column sm={2}>
                  Status
                </Form.Label>
                <Col sm={4}>
                <span className="DetailSpan">EXPIRED</span>
                </Col>
              </span>
            }
            
          </Form.Group>
          <Form.Group as={Row} >
            <Form.Label column sm={2}>
              Merchant name
            </Form.Label>
            <Col sm={4}>
              <span className="DetailSpan">{this.props.requestDetail && this.props.requestDetail.merchantName}</span>
            </Col>
            <Form.Label column sm={2}>
              Activity
            </Form.Label>
            <Col sm={4}>
            <span className="DetailSpan">{this.props.requestDetail && this.props.requestDetail.activityName}</span>
            </Col>
          </Form.Group>
          <Form.Group as={Row} >
            <Form.Label column sm={2}>
              Outlet Name
            </Form.Label>
            <Col sm={4}>
           {this.props.requestDetail.outletId &&  this.props.requestDetail.outletId.length >0 && this.props.requestDetail.outletId.map((outlet, index) => <button key={index} type="btn" disabled className="category-btn" >{outlet.label}</button>)}
            </Col>
            <Form.Label column sm={2}>
              Descripiton
            </Form.Label>
            <Col sm={4}>
            <span className="DetailSpan">{this.props.requestDetail && this.props.requestDetail.description}</span>
            </Col>
          </Form.Group>
          <Form.Group as={Row} >
            <Form.Label column sm={2}>
              Original Price
            </Form.Label>
            <Col sm={4}>
              <span className="DetailSpan">{this.props.requestDetail && this.props.requestDetail.originalPrice}</span>
            </Col>
            <Form.Label column sm={2}>
              No of pramotional tickets
            </Form.Label>
            <Col sm={4}>
            <span className="DetailSpan">{this.props.requestDetail && this.props.requestDetail.quantity}</span>
            </Col>
          </Form.Group>
          <Form.Group as={Row} >
            <Form.Label column sm={2}>
              Discounted Price
            </Form.Label>
            <Col sm={4}>
              <span className="DetailSpan">{this.props.requestDetail && this.props.requestDetail.discountedPrice}</span>
            </Col>
            <Form.Label column sm={2}>
              Requested start date
            </Form.Label>
            <Col sm={4}>
            <span className="DetailSpan">{this.props.requestDetail && moment(this.props.requestDetail.startDate).format('YYYY-MM-DD H:mm:ss')}</span>
            </Col>
          </Form.Group>
          <Form.Group as={Row} >
            <Form.Label column sm={2}>
              Discounted Percentage
            </Form.Label>
            <Col sm={4}>
              <span className="DetailSpan">{this.props.requestDetail && this.props.requestDetail.discountedPercentage}</span>
            </Col>
            <Form.Label column sm={2}>
              Requested end date
            </Form.Label>
            <Col sm={4}>
            <span className="DetailSpan">{this.props.requestDetail && moment(this.props.requestDetail.endDate).format('YYYY-MM-DD H:mm:ss')}</span>
            </Col>
          </Form.Group>
          <Form.Group as={Row} >
            <Form.Label column sm={12}>
              Pramotional Terms
            </Form.Label>
            <Col sm={12}>
              <ul className="term-list">
                { this.props.requestDetail && this.props.requestDetail.promotionalTerms && this.props.requestDetail.promotionalTerms.map((term, index) => 
                <li key={index}>
                  {term.point}
                </li>
                
                )}
            </ul>
            </Col>
          </Form.Group>
          <Form.Group as={Row} >
            <Form.Label column sm={12}>
              Admin Notes
            </Form.Label>
            <Col sm={12}>
              <p>{this.props.requestDetail && this.props.requestDetail.adminNotes}</p>
            </Col>
          </Form.Group>
        </Card.Body>
      </Card >
      
     
      </Col>
    </Row>
    )
  }
}
const mapStateToProps = (state) => {
  console.log(state.admin)
  return {
    requestDetail:state.admin.pramotionDetail && state.admin.pramotionDetail.data && state.admin.pramotionDetail.data.data
  }
}
export default connect(mapStateToProps, {getPramotionDetailsById,ProccedRequest})(ViewPramotionRequest)
