import React from 'react'
import { Col, Form } from 'react-bootstrap';
import { RenderBootstrapTextArea } from '../../components/commonUIComponents';

export default function Overview(props) {
    return (
        <Form >
            <Form.Group as={Col} sm="12">
                <Form.Label htmlFor="expect">What to Expect</Form.Label>
                <RenderBootstrapTextArea
                    name="expect"
                    id="expect"
                    placeholder=""

                />
            </Form.Group>
            <Form.Group as={Col} sm="12">
                <Form.Label htmlFor="info">Additional Information</Form.Label>
                <RenderBootstrapTextArea
                    name="info"
                    id="info"
                    placeholder=""

                />
            </Form.Group>
            {/* <Form.Group as={Col} sm="12">

                <SubmitButton>
                    Submit
                    </SubmitButton>
            </Form.Group> */}

        </Form>
    )
}
