import React from 'react';
import { RenderButton } from './commonUIComponents';
const FilterComponent = ({ filterText, onFilter, onClear }) => (
    <>
      <div className="search-input-table">
        <input
          type="text"
          name="search"
          className="data-table-search-field"
          placeholder="Search"
          value={filterText}
          onChange={onFilter}
        />
        <RenderButton className="data-table-clear-button" onClick={onClear}>
          X
      </RenderButton>
      </div>
    </>
  );
  export default FilterComponent;