import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Row, Col, Form, Card } from 'react-bootstrap';
import { RenderTextField, RenderValidationDateField, RenderTextArea,RenderAppendTextField, SuccessButton, DeleteButton, RenderPrefixedPriceField,showInfoMsg } from '../../../components/commonUIComponents';
import { ValidationForm } from 'react-bootstrap4-form-validation';
import Datetime from 'react-datetime';
import validator from 'validator';
import { getPramotionDetailsById,ProccedPramotionRequest } from '../actions/index';
import validations from '../../../validations';
import moment from 'moment';
class ActivePramotionRequest extends Component {
  constructor(props) {
    super(props);
    this.state = {
      buttonClicked: '',
      title:'',
      selectedActivity:'',
      activityId:'',
      merchant:'',
      merchantId:'',
      selectedOutletName:'',
      description:'',
      pramotion_ticket:'',
      originalPrice:'',
      discountedPrice:'',
      discountedPercentage:'',
      startDate:'',
      endDate:'',
      discountAmount:'',
      terms:[],
      rejectDate:new Date(),
      adminNote:'',
      adminNotes: ""
    }
  }
  componentDidMount() {
    let requestId = window.location.pathname.split('/').pop();
    if (requestId && requestId !== 'add') {
      this.props.getPramotionDetailsById(requestId);
    }
  }
  componentDidUpdate(prevProps, newState) {
    
    if (this.props.requestDetail && this.props.requestDetail !== prevProps.requestDetail) {
      let sDate = moment(this.props.requestDetail.startDate).format('YYYY-MM-DD H:mm:ss');
      let eDate = moment(this.props.requestDetail.endDate).format('YYYY-MM-DD H:mm:ss');
      this.setState({
        title:this.props.requestDetail.title,
        selectedActivity:this.props.requestDetail.activityName,
        activityId:this.props.requestDetail.activityId,
        merchant:this.props.requestDetail.merchantName,
        merchantId:this.props.requestDetail.merchantId,
        selectedOutletName:(this.props.requestDetail.outletId && this.props.requestDetail.outletId.length > 0 && this.props.requestDetail.outletId.map((item)=>item.label)).toString(),
        description:this.props.requestDetail.description,
        pramotion_ticket:this.props.requestDetail.quantity,
        originalPrice:this.props.requestDetail.originalPrice,
        discountedPrice:this.props.requestDetail.discountedPrice,
        discountedPercentage:this.props.requestDetail.discountedPercentage,
        startDate:sDate,
        endDate:eDate,
        discountAmount:this.props.requestDetail.discountAmount,
        terms:this.props.requestDetail.promotionalTerms,
        
      })
    }
  }
  Accept =() =>{
    this.setState({
      buttonClicked : 'accept'
    })
  }
  acceptSubmmit = () => {
    showInfoMsg('Are you Sure To Accept this Request?', () => this.acceptRequest('1'))
  }
  acceptRequest =(e)=>{
    let sd = new Date(this.state.startDate);
    console.log(this.state.startDate)
    let payload ={
      "status":e,
      "id":this.props.requestDetail.id,
      "adminNotes": this.state.adminNotes,
    }
    this.props.ProccedPramotionRequest(payload)
  }
  reject = () => {
    this.setState({
      buttonClicked : 'reject'
    })
  }
  rejectSubmit =() =>{
    showInfoMsg('Are you Sure To Reject this Request?', () => this.rejectRequest('0'))
  }
  
  rejectRequest =(e)=>{
    let payload ={
      "status":e,
      "id":this.props.requestDetail.id,
      "adminNotes": this.state.adminNotes,
    }
    this.props.ProccedPramotionRequest(payload)
  }
  submit =()=>{
    console.log("done")
  }
  handleChange = (event) => {
    this.setState({
      [event.target.name]: event.target.value
    })
  }
  handleStartDateChange = (e) =>{
    this.setState({
        startDate:e._d
    })
  }
  render() {
    return (
      <Card>
        <Card.Body>

          <Row>
            <Col md={12}>
              <h5>Pending Pramotion Request</h5>
              <hr />
            </Col>
            
          </Row>
          <ValidationForm onSubmit={this.handleSubmit}>
            <Row>
                <Col md={6}>
                    <Form.Group>
                    <Form.Label>Title</Form.Label>
                    <RenderTextField
                        name="title"
                        disabled={true}
                        value={this.state.title || ''}
                    />
                    </Form.Group>
                </Col>
            </Row>
            <Row>
                <Col md="6">
                    <Form.Group>
                        <Form.Label>Select Activity</Form.Label>
                        <RenderTextField
                          name="selectedActivity"
                          disabled={true}
                          value={this.state.selectedActivity || ''}
                      />
                    </Form.Group>
                </Col>
                <Col md={6}>
                  <Form.Group>
                      <Form.Label>Outlet Name</Form.Label>
                      <RenderTextField
                        name="outletName"
                        disabled={true}
                        value={this.state.selectedOutletName || ''}
                    />
                  </Form.Group>
                </Col>
            </Row>
            <Row>
                <Col md={6}>
                    <Form.Group>
                    <Form.Label>Pramotion Description</Form.Label>
                    <RenderTextField
                        name="description"
                        disabled={true}
                        value={this.state.description || ''}
                    />
                    </Form.Group>
                </Col>
                <Col md={6}>
                    <Form.Label>
                        Number of Pramotion Tickets 
                    </Form.Label>
                    <span data-toggle="tooltip" className="InfoTooltip" data-placement="top" title="Number of couples that can redeem the pramotion is equivalent to the number of pramotinal tickets divided by 2">
                        <i class="feather icon-alert-circle"></i>
                    </span>
                    <RenderTextField
                        name="description"
                        disabled={true}
                        value={this.state.pramotion_ticket || ''}
                    />
                </Col>
            </Row>
            <Row>
                <Col md={6}>
                    <Form.Group>
                    <Form.Label>Original Price</Form.Label>
                    <RenderPrefixedPriceField
                        name="originalPrice"
                        prefix="SGD"
                        disabled={true}
                        value={this.state.originalPrice}
                    />
                    </Form.Group>
                </Col>
                <Col md="6">
                    <Form.Group>
                        <Form.Label>Start Date</Form.Label>
                        <RenderTextField
                          name="startDate"
                          disabled={true}
                          value={this.state.startDate || ''}
                      />
                    </Form.Group>
                </Col>
            </Row>
            <Row>
                <Col md="6">
                    <Form.Group>
                    <Form.Label>Discounted Price</Form.Label>
                    <RenderPrefixedPriceField
                        name="discountedPrice"
                        placeholder="Discounted Price"
                        required
                        prefix="SGD"
                        disabled={true}
                        value={this.state.discountedPrice}
                    />
                    </Form.Group>
                </Col>
                <Col md="6">
                    <Form.Group>
                        <Form.Label>End Date</Form.Label>
                        <RenderTextField
                            name="description"
                            disabled={true}
                            value={this.state.endDate || ''}
                        />
                    </Form.Group>
                </Col>
            </Row>
            <Row>
                
                <Col md="6">
                    <Form.Group>
                    <Form.Label>Discounted Percentage</Form.Label>
                    <RenderAppendTextField
                        name="discountedPercentage"
                        disabled={true}
                        value={this.state.discountedPercentage}
                        appendText="%" />
                    </Form.Group>
                </Col>
            </Row>
            <Row>
                <Col md="6">
                    <Form.Group>
                    <Form.Label>Discounted Amount</Form.Label>
                    <RenderPrefixedPriceField
                        name="discountAmount"
                        placeholder="Discounted Amount"
                        prefix="SGD"
                        disabled={true}
                        value={(this.state.discountAmount) || ''}
                    />
                    </Form.Group>
                </Col>
            </Row>
            <Row>
            
                <Col md="12">
                      <Row>
                        <Col md="6">
                        <Form.Group>
                        <Form.Label>Terms</Form.Label>
                            <ul className="term-list">
                             { this.state.terms && this.state.terms.map((term, index) => 
                              <li key={index}>
                                {term.point}
                              </li>
                              
                              )}
                            </ul>
                            
                        </Form.Group>
                        </Col>
                    </Row>
                </Col>
            </Row>
            <hr></hr>
            <Row>
                <Col md="6">
                      <Form.Group as={Row}>
                        <Col sm={6}>
                          <SuccessButton onClick={this.Accept}>Accept</SuccessButton>
                          <DeleteButton onClick={this.reject} className="ml-3">Reject</DeleteButton>
                        </Col>
                      </Form.Group>
                    </Col>
            </Row>
        </ValidationForm>
          {this.state.buttonClicked === 'accept' ?
            <Card className="box-border">
              <Card.Header>
                <Card.Title as="h5">Accepting Pramotion Request</Card.Title>
              </Card.Header>
              <Card.Body>
                <ValidationForm onSubmit={this.acceptSubmmit}>
                  <Row>
                      <Col sm={12}>
                        <Form.Group as={Row} >
                          <Form.Label column sm={3}>
                            Admin Notes
                        </Form.Label>
                          <Col sm={9}>
                            <RenderTextArea
                              name='adminNotes'
                              onChange={this.handleChange}
                              value={this.state.adminNotes}
                            />
                          </Col>
                        </Form.Group>
                      </Col>
                  </Row>
                  <Row >
                    <Col sm={12}>
                      <SuccessButton onClick={this.acceptSubmmit}>Submit</SuccessButton>
                    </Col>
                  </Row>

                </ValidationForm>
              </Card.Body>
            </Card> : null}
          {this.state.buttonClicked === 'reject' ?
            <Card className="box-border">
              <Card.Header>
                <Card.Title as="h5">Rejecting Pramotion Request</Card.Title>
              </Card.Header>
              <Card.Body>
                <ValidationForm onSubmit={this.rejectSubmit}>
                  <Row>
                    <Col sm={12}>
                      <Form.Group as={Row} >
                        <Form.Label column sm={3}>
                          Date Rejected
                        </Form.Label>
                        <Col sm={9}>
                        <Datetime name="rejectDate"
                        errorMessage={{ validator: validations.required }}
                        validator={(value) => !isNaN(new Date(value).getTime())}
                        value={this.state.rejectDate}
                        timeFormat={false} onChange={this.handleStartDateChange} 
                        dateFormat={"YYYY-MM-DD H:mm:ss"} inputProps={{ placeholder: 'Select Date' }} />
        
                        </Col>
                      </Form.Group>
                    </Col>
                    <Col sm={12}>
                      <Form.Group as={Row} >
                        <Form.Label column sm={3}>
                          Reason
                        </Form.Label>
                        <Col sm={9}>
                          <RenderTextArea
                            name='rejectionReason'
                            value={this.state.handleChange}
                          />
                        </Col>
                      </Form.Group>
                    </Col>
                  </Row>

                  <Row >
                    <Col sm={12}>
                      <SuccessButton onClick={this.rejectSubmit}>Submit</SuccessButton>
                    </Col>
                  </Row>

                </ValidationForm>
              </Card.Body>
            </Card> : null}
        </Card.Body>
      </Card >
    )
  }
}
const mapStateToProps = (state) => {
  console.log(state.admin)
  return {
    requestDetail:state.admin.pramotionDetail && state.admin.pramotionDetail.data && state.admin.pramotionDetail.data.data
  }
}
export default connect(mapStateToProps, {getPramotionDetailsById,ProccedPramotionRequest})(ActivePramotionRequest)
