import React from 'react'
import { Route, Redirect } from 'react-router-dom'
export default function PublicRouter(props) {
    
    // console.log(props)
    return (
        !props.loginUser ?
            <Route
                key={props.index}
                path={props.path}
                exact={props.exact}
                // name={props.name}
                render={renderProps => (
                    <props.component {...renderProps} />
                )} />
            : (props.role ==='admin' ? <Redirect key={props.index} to="/admin/dashboard" /> : <Redirect key={props.index}  to="/merchant/dashboard" />)
    )
}
