import React, { Component } from 'react'
import RenderDataTable from '../../../components/dataTable'
import { Row, Col, Card } from 'react-bootstrap';
import { connect } from 'react-redux';
import { getMerchantList } from '../actions/index'
import { history } from './../../../../history'
import { titlelize } from '../../../utilActions';

class UserList extends Component {

  navigateAddActivity = () => {
    history.push('/admin/dashboard/activity/add');
  }
  navigateEditActivity = (id) => {
    history.push('/admin/dashboard/user/123');
  }
  render() {

    const columns = [
      {
        name: "No",
        selector: "no",
        sortable: true,
        // width: "5%"
      },
      {
        name: "Name",
        selector: "name",
        sortable: true,
        // width: "13%"

      },
      {
        name: "Account Linked",
        selector: "accountlinked",
        sortable: false

      },
      {
        name: "Patner Name",
        selector: "patnername",
        sortable: true,
        // width:"13%"
      },
      {
        name: "Email",
        selector: "email",
        sortable: true,
        // width:"17%"
      },
      {
        name: "Email Verification",
        selector: "emailverified",
        sortable: true
      },
      {
        name: "Last seen",
        selector: "lastseen",
        sortable: true,
        // width:"18%"
      },
      {
        name: "Last booked",
        selector: "lastbooked",
        sortable: true,
        // width:"18%"
      },
      {
        name: "Activity Category",
        selector: "activitycategory",
        sortable: true,
        // width:"18%"
      },
      {
        name: "Average Spending for Paid Activities",
        selector: "avgspending",
        sortable: true,
        // width:"20%"
      },
    ];


    return (
      <>
        <Row>
          <Col>
            <Card>
              <Card.Header>
                <Card.Title as="h5">Users</Card.Title>
                {/* <i title="Users" className="f-28 text-c-green float-right feather icon-plus" onClick={() => this.navigateAddActivity()}></i> */}
              </Card.Header>
              <Card.Body>
                <RenderDataTable
                  onRowClicked={this.navigateEditActivity  }
                  columns={columns}
                  data={this.props.merchantActivitiesList || []}
                  defaultSortField="name"
                />
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </>
    )
  }
}
