import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Row, Col, Card, Form } from 'react-bootstrap';
import { ValidationForm } from 'react-bootstrap4-form-validation';
import validator from 'validator';
import RenderDataTable from '../../../components/dataTable'

import { RenderTextField, RenderPrefixedPriceField } from '../../../components/commonUIComponents';
import Datetime from 'react-datetime';
import AmChartStatistics2 from '../../../components/charts/AmChartStatistics2';
import avatar1 from './../../../../assets/images/user/avatar-1.jpg';
import avatar2 from './../../../../assets/images/user/avatar-2.jpg';
import validations from '../../../validations';


class CoupleInfo extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isActive: true,
            activeModaltext: "Inactive",
            showModal: false,
            selectedOutlet: null
        }
    }
    handleSubmitTandC = (event) => {
        event.preventDefault();

    }
    handleSubmitOverview = (event) => {
        event.preventDefault();

    }
    handleSubmitDeals = (event) => {
        event.preventDefault();

    }
    toggleHandler = (event) => {
        event.preventDefault();
        this.setState({
            isActive: !this.state.isActive,
            showModal: !this.state.showModal
        }, () => {
            if (this.state.isActive === true) {
                this.setState({
                    activeModaltext: "Inactive"
                })
            } else {
                this.setState({
                    activeModaltext: "Active"
                })
            }
        })
    };

    openModal = () => {
        this.setState({
            showModal: true
        })
    }

    modalClosed = () => {
        this.setState({
            showModal: false
        })
    }
    changeActivity = () => {
        this.setState({
            selectedOutlet: null
        });
    }
    changeOutlet = (input) => {
        this.setState({
            selectedOutlet: input
        })

    }
    handleSubmit = (event) => { event.preventDefault() }
    render() {
        const columns = [
            {
                name: "Activity",
                selector: "activity",
                sortable: true,
                width: '45%'

            },
            {
                name: "Date",
                selector: "date",
                sortable: true
            },
            {
                name: "Patner",
                selector: "patner",
                sortable: true,
                width: "20%"
            },
            {
                name: "Points",
                selector: "points",
                sortable: true
            }
        ];
        const badgeColumns = [
            {
                name: "Badge",
                selector: "badge",
                sortable:false
            },
            {
                name: "Title",
                selector: "title",
                sortable:true
            },
            {
                name: "Date",
                selector: "date",
                sortable: true
            },
            {
                name: "Description",
                selector: "desc",
                sortable: false,
                width: "40%"
            },
            {
                name: "Points",
                selector: "points",
                sortable: true
            }
        ];

        return (
            <>
                <Card className="box-border">
                    <Card.Header>
                        <Card.Title as="h5"> Couple Details</Card.Title>
                    </Card.Header>
                    <Card.Body >
                        <ValidationForm onSubmit={this.handleSubmit}>
                            <Row>
                                <Col md={6}>
                                    <Form.Group>
                                        <Form.Label>Couple Name</Form.Label>
                                        <RenderTextField
                                            name="couplename"
                                            placeholder="Couple Name"
                                            maxLength={18}
                                            errorMessage={{ validator: validations.required }}
                                            validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                        />
                                    </Form.Group>
                                </Col>
                                <Col md="6">
                                    <Form.Group>
                                        <Form.Label>Anniversary Date</Form.Label>
                                        <Datetime timeFormat={false} dateFormat={"DD MMM YYYY"} inputProps={{ placeholder: 'Select Date' }} />
                                    </Form.Group>
                                </Col>
                                <Col md="6">
                                    <Form.Group>
                                        <Form.Label>Date Connected</Form.Label>
                                        <Datetime timeFormat={false} dateFormat={"DD MMM YYYY"} inputProps={{ placeholder: 'Select Date' }} />

                                    </Form.Group>
                                </Col>
                                <Col md={6}>
                                    <Form.Group>
                                        <Form.Label>Couple Code Used To Link</Form.Label>
                                        <RenderTextField
                                            name="couplecode"
                                            placeholder="Couple Code"
                                            maxLength={18}
                                            errorMessage={{ validator: validations.required }}
                                            validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                        />
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group>
                                        <Form.Label>Hours Bonded</Form.Label>
                                        <RenderTextField
                                            name="couplename"
                                            placeholder="Couple Name"
                                            maxLength={18}
                                            errorMessage={{ validator: validations.required }}
                                            validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                        />
                                    </Form.Group>
                                </Col>
                                <Col md={6}>
                                    <Form.Group>
                                        <Form.Label>Collective Savings</Form.Label>
                                        <RenderPrefixedPriceField
                                            name="hoursbonded"
                                            placeholder="Hours Bonded"
                                            validator={validator.isInt}
                                            errorMessage={{ validator: 'Invalid hours' }}
                                        />
                                    </Form.Group>
                                </Col>
                                <Col md={6}>
                                    <Form.Group>
                                        <Form.Label>Letters Collected</Form.Label>
                                        <RenderTextField
                                            name="letters"
                                            placeholder="Letters Collected"
                                            maxLength={18}
                                            errorMessage={{ validator: validations.required }}
                                            validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                        />
                                    </Form.Group>
                                </Col>
                            </Row>
                            <Row>
                                {/* <Col md="6">
                    <SubmitButton className="btn btn-primary shadow-2 mt-4">
                      Save
                          </SubmitButton>
                  </Col> */}
                            </Row>
                        </ValidationForm>
                    </Card.Body>
                </Card>
                <Card>
                    <Row>
                        <Col xl={4}>

                            <Card className='green-bg gradientcolor h-100'>
                                <Card.Header className='borderless'>
                                    <Card.Title as='h5' className='text-white'>Points Summary</Card.Title>
                                </Card.Header>
                                <Card.Body className='p-0'>
                                    {/* <div className="p-2 text-center">
                                        <a href={DEMO.BLANK_LINK} className="text-white text-uppercase f-w-400">Month</a>
                                        <a href={DEMO.BLANK_LINK} className="btn btn-round bg-white text-uppercase mx-3 px-4 f-w-400">Week</a>
                                        <a href={DEMO.BLANK_LINK} className="text-white text-uppercase f-w-400">Day</a>
                                    </div> */}
                                    {/* <div className="my-3 text-center text-white">
                        <a href={DEMO.BLANK_LINK} className=" d-block mb-1">$78.89 <span className="feather icon-arrow-up" /></a>
                        <span>Week2 +15.44</span>
                    </div> */}

                                    <AmChartStatistics2
                                        categoryField="Year"
                                        dataProvider={[{
                                            "Year": "M-4",
                                            "value": 35
                                        }, {
                                            "Year": "M-3",
                                            "value": 62
                                        }, {
                                            "Year": "M-2",
                                            "value": 55
                                        }, {
                                            "Year": "M-1",
                                            "value": 62
                                        },
                                        {
                                            "Year": "Current",
                                            "value": 80
                                        }]} />


                                </Card.Body>
                            </Card>
                        </Col>
                        <Col xl={8}>
                            <Card>
                                <Card.Header>
                                    <Card.Title as="h5">Points</Card.Title>
                                </Card.Header>
                                <Card.Body>
                                    <RenderDataTable
                                        onRowClicked={this.navigateEditActivity}
                                        columns={columns}
                                        noPagination="true"
                                        data={this.props.patnerList || []}
                                        defaultSortField="name"
                                    />
                                </Card.Body>
                            </Card>
                        </Col>
                    </Row>
                </Card>
                <Card>
                    <Col xl={12}>
                        <Card>
                            <Card.Header>
                                <Card.Title as="h5">Badges Earned</Card.Title>
                            </Card.Header>
                            <Card.Body>
                                <RenderDataTable
                                    onRowClicked={this.navigateEditActivity}
                                    columns={badgeColumns}
                                    noPagination="true"
                                    data={this.props.badgeList || []}
                                    defaultSortField="name"
                                />
                            </Card.Body>
                        </Card>
                    </Col>
                </Card>
            </>
        )
    }
}
const mapStateToProps = () => {
    return {
        patnerList: [
            { title:'Adventure',date: '29-12-2013', activity: 'Completed Review', patner: <button key="patner1" type="btn" disabled className="category-btn" >Both</button>, points: '10' },
            { title:'Outdoor',date: '29-12-2013', activity: 'Purchased tickets for activity', patner: <button key="patner1" type="btn" disabled className="category-btn" >Both</button>, points: '+60' },
            { title:'Outdoor',date: '29-12-2013', activity: 'Unlocked promotions', patner: <button key="patner1" type="btn" disabled className="category-btn" >Jessica</button>, points: '+20' },
            { title:'Adventure',date: '29-12-2013', activity: 'Unloack boadge', patner: <button key="patner1" type="btn" disabled className="category-btn" >Febb</button>, points: '30' },
        ],
        badgeList: [
            { title:'Adventure',badge:<span><img className="rounded-circle" style={{ width: '40px' }} src={avatar2} alt="activity-user" /> </span>,date: '29-12-2013', desc: 'Completed Review', patner: <button key="patner1" type="btn" disabled className="category-btn" >Both</button>, points: '10' },
            { title:'Outdoor',badge:<img className="rounded-circle" style={{ width: '40px' }} src={avatar1} alt="activity-user" />,date: '29-12-2013', desc: 'Purchased tickets for activity', patner: <button key="patner1" type="btn" disabled className="category-btn" >Both</button>, points: '+60' },
            { title:'Outdoor',badge:<img className="rounded-circle" style={{ width: '40px' }} src={avatar2} alt="activity-user" />,date: '29-12-2013', desc: 'Unlocked promotions', patner: <button key="patner1" type="btn" disabled className="category-btn" >Jessica</button>, points: '+20' },
            { title:'Adventure',badge:<img className="rounded-circle" style={{ width: '40px' }} src={avatar1} alt="activity-user" />,date: '29-12-2013', desc: 'Unloack boadge', patner: <button key="patner1" type="btn" disabled className="category-btn" >Febb</button>, points: '30' },
        ]
    };
}


export default connect(mapStateToProps, null)(CoupleInfo)
