import React from 'react';
import { NavLink } from 'react-router-dom';
import logo from '../../../assets/images/sparks-logo.svg';
import Breadcrumb from "../../../App/layout/AdminLayout/Breadcrumb";
import { ValidationForm } from 'react-bootstrap4-form-validation';
import { login } from './actions/index';
import { SubmitButton } from '../../components/commonUIComponents';
import validator from 'validator';
import { connect } from 'react-redux'
import { RenderEmailField, RenderPasswordField } from '../../components/commonUIComponents';

class AdminLogin extends React.Component {
  handleSubmit = (event, formData, formControl) => {

    event.preventDefault();
    this.props.login(formData);
  }
  render() {
    return (
      // <Aux>
      <>

        <Breadcrumb />
        <div className="auth-wrapper">
          <div className="auth-content subscribe">
            <div className="card">
              <div className="row no-gutters">
                <div className="col-md-4 col-lg-6 d-md-flex d-lg-flex theme-bg-new align-items-center justify-content-center">
                  <img src={logo} alt="lock images" className="img-fluid" />
                </div>
                <div className="col-md-8 col-lg-6">
                  <div className="card-body text-center">
                    <div className="row justify-content-center">
                      <div className="col-sm-10">
                        <ValidationForm onSubmit={this.handleSubmit}>

                          <h3 className="mb-4">Admin Login</h3>
                          <div className="input-group mb-3">
                            <RenderEmailField
                              name="email"
                              placeholder="Email"
                              validator={validator.isEmail}
                              errorMessage={{ validator: "Please enter a valid email" }}

                            />
                          </div>
                          <div className="input-group mb-4">
                            <RenderPasswordField
                              name="password"
                              placeholder="Password"
                              minLength={6}
                            />
                          </div>
                          {/* <div className="form-group text-left">
                            <div className="checkbox checkbox-fill d-inline">
                              <Field
                                name="saveCredentials"
                                id="checkbox-fill-a1"
                                component={RenderCheckbox}
                              />
                             
                              <label htmlFor="checkbox-fill-a1" className="cr"> Save credentials</label>
                            </div>
                          </div> */}
                          <SubmitButton >Login</SubmitButton>                          
                          <p className="mb-2 mt-3 text-danger"><NavLink className="text-danger" to="/admin/reset-password">Forgot your password?</NavLink></p>
                          <p className="mb-2 mt-3 text-dark"><NavLink className="text-dark" to="/merchant">Merchant Login</NavLink></p>
                          {/* <p className="mb-2 text-muted">Forgot password? <NavLink to="/auth/reset-password-4">Reset</NavLink></p>
                                                <p className="mb-0 text-muted">Don’t have an account? <NavLink to="/auth/signup-4">Signup</NavLink></p> */}
                        </ValidationForm>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

      </>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    initialValues: {
      email: '',
      password: '',
      saveCredentials: false,
    }
  }
}
export default connect(mapStateToProps, { login })(AdminLogin);