import React from 'react';
import { NavLink } from 'react-router-dom';
import Breadcrumb from "../../../App/layout/AdminLayout/Breadcrumb";
import logo from '../../../assets/images/sparks-logo.svg';

import { emailConfirmed } from './actions/index';
import { connect } from 'react-redux'
class EmailConfirmLink extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email:'',
      code:'',
      success:false,
      message:''
    }
    let urlProp = window.location.pathname.split("/");
    console.log(urlProp)
    
    this.state.email = urlProp[4]
    this.state.code = urlProp[3]

  }
  componentDidMount(){
    console.log(this.state.email)
    if(this.state.email){
      let postData = {
        "email":this.state.email,
        "code":this.state.code
      }
      this.props.emailConfirmed( postData);
 
    }
  }
  componentDidUpdate(prevProps){
    console.log(prevProps.confirmResponse)
    if(this.props.confirmResponse &&  this.props.confirmResponse !== prevProps.confirmResponse){
      console.log(this.props.confirmResponse.message)
      this.setState({
        success:this.props.confirmResponse.success,
        message:this.props.confirmResponse.message
      })
    }
  }
  render() {

    return (
      <>
        <Breadcrumb />
        <div className="auth-wrapper">
          <div className="auth-content subscribe">
            <div className="card">
              <div className="row no-gutters">
                <div className="col-md-4 col-lg-6 d-md-flex d-lg-flex theme-bg-new align-items-center justify-content-center">
                  <img src={logo} alt="lock images" className="img-fluid" />
                </div>
                <div className="col-md-8 col-lg-6">
                  <div className="card-body text-center">
                    <div className="row justify-content-center">
                    {
                      this.state.sucess?
                      <div className="col-sm-10">
                        
                          <div className="mb-4">
                            <i className="feather icon-check auth-icon" />
                          </div>
                          <h3 className="mb-4">Email Confirmed</h3>
                          <div className="input-group mb-3">
                          <p className="mb-2 mt-3 text-black-50">{this.state.message}</p>
                          </div>
                          <NavLink to="/"  className="btn green-bg shadow-2 m-1">Back to Login</NavLink>
                        
                      </div>:
                      <div className="col-sm-10">
                        
                          <div className="mb-4">
                            <i className="feather icon-close auth-icon" />
                          </div>
                          <h3 className="mb-4">Oops!</h3>
                          <div className="input-group mb-3">
                          <p className="mb-2 mt-3 text-black-50">{this.state.message}</p>
                          </div>
                          <NavLink to="/"  className="btn green-bg shadow-2 m-1">Back to Login</NavLink>
                        
                      </div>
                    }
                      
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}
const mapStateToProps = (state) => {
  
  return {
    loading:state.merchant.loading,
    confirmResponse:state.merchant.confirmEmailDone && state.merchant.confirmEmailDone.data,
    initialValues: {
      email: '',
      saveCredentials: false,
    }
  }
}
export default connect(mapStateToProps, { emailConfirmed })(EmailConfirmLink);