export const login = (postdata) => { 
console.log(ADMIN_LOGIN_URL)
  return (dispatch, getState) => {
    dispatch(actionCreator(adminActionTypes.login.REQUEST));
    axios({
      method: 'POST',
      url: ADMIN_LOGIN_URL,
      data: postdata,
      headers: commonHeaders,
    }).then(response => {
      console.log(response)
      if (response.data.statusCode >= 400) {
        showErrorMsg((response.data && response.data.message) || SOMETHING_WENT_WRONG);
        dispatch(actionCreator(adminActionTypes.login.FAILURE, response.data));
      } else {
        response.data['uType']='admin';
        dispatch(actionCreator(adminActionTypes.login.SUCCESS, response.data));

        if (response.data && response.data.data && response.data.data.id) {
          history.push('/admin/dashboard');
          // dispatch(getS3BucketDynamicImageUrl())
        }
      }
    }).catch(error => {

      dispatch(actionCreator(adminActionTypes.login.FAILURE));
    })
  }
}
export const forgotPassword = (postdata) => { 
    return (dispatch, getState) => {
      dispatch(actionCreator(adminActionTypes.forgot.REQUEST));
      axios({
        method: 'POST',
        url: ADMIN_RESET_PASSWORD_URL,
        data: postdata,
        headers: commonHeaders,
      }).then(response => {
        console.log(response)
        if (response.status >= 400 && response.status < 410) {
          showErrorMsg((response.data && response.data.message) || SOMETHING_WENT_WRONG);
          dispatch(actionCreator(adminActionTypes.forgot.FAILURE, response.data));
        } else {
          if (response.data.statusCode >= 400 && response.data.statusCode < 410) {
            showErrorMsg((response.data && response.data.message) || SOMETHING_WENT_WRONG);
            dispatch(actionCreator(adminActionTypes.activityList.FAILURE));
          } else{
          
          history.push('/admin/confirm-link');
          showSuccessMsg((response.data && response.data.message) || 'Please check your mail for password update link');
          dispatch(actionCreator(adminActionTypes.forgot.SUCCESS, response.data));
          }
          
        }
      }).catch(error => {
  
        dispatch(actionCreator(adminActionTypes.forgot.FAILURE));
      })
    }
  }
