export const getActivityList = (searchObj) => {
  return (dispatch, getState) => {
    dispatch(actionCreator(adminActionTypes.activityList.REQUEST));
    axios({
      method: 'post',
      url: ACTIVITY_LIST_URL,
      headers: privateHeaders(getState().admin.authToken),
      data: searchObj,
      validateStatus: function (status) {
        return status < 500; // Reject only if the status code is greater than or equal to 500
      }
    }).then(response => {
      console.log(response)
      if (response.status >= 200 && response.status < 300) {
        if (response.data.statusCode >= 400 && response.data.statusCode < 410) {
          showErrorMsg((response.data && response.data.message) || SOMETHING_WENT_WRONG);
          dispatch(actionCreator(adminActionTypes.activityList.FAILURE));
        } else   
        if(response.data.statusCode === 301){
          showErrorMsg((response.data && response.data.message) || 'Token expire please login again');
          dispatch(clearStoreAndRedirectToHome());
        }else{
          dispatch(actionCreator(adminActionTypes.activityList.SUCCESS, response.data));
        }
      }
       else if (response.status === 401) {
        showErrorMsg((response.data && response.data.message) || SOMETHING_WENT_WRONG);
        dispatch(clearStoreAndRedirectToHome());
      }
      else {
        dispatch(actionCreator(adminActionTypes.activityList.SUCCESS, null));
      }

    }).catch(error => {
      console.error('error:', error);

      dispatch(actionCreator(adminActionTypes.activityList.FAILURE));
      showErrorMsg(SOMETHING_WENT_WRONG)
    })
  }
}
