import React, { Component } from 'react'
import { Card, Tabs, Tab } from 'react-bootstrap';
import { connect } from 'react-redux';
import RenderDataTable from '../../../components/dataTable';
import { history } from '../../../../history';
import { getActiveRequestList, getPendingRequestList, getArchiveRequestList } from '../actions/index';
import { capitalizeFirstChar } from '../../../utilActions';
import moment from 'moment';
class BeFeatured extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeSortColumn: 'createdAt',
      activeSortDirection: 'DESC',
      activeRecordLimit: 10,
      activeRecordOffset: 0,
      activeSsearch:'',
      activeSstatus:'1',  
      pendingSortColumn: 'createdAt',
      pendingSortDirection: 'DESC',
      pendingRecordLimit: 10,
      pendingRecordOffset: 0,
      pendingSsearch:'',
      pendingSstatus:'2', 
        
      archiveSortColumn: 'createdAt',
      archiveSortDirection: 'DESC',
      archiveRecordLimit: 10,
      archiveRecordOffset: 0,
      archiveSsearch:'',     
      globalFilter: null,
      merchantList:[]
    }
  }
  componentDidMount(){
    this.loadActiveData();
    this.loadPendingData();
    this.loadArchiveData();
  }
  loadActiveData = ()=>{
   let payload = {
      "status":this.state.activeSstatus,
      "search":this.state.activeSsearch,
      "limit": this.state.activeRecordLimit,
      "offset": this.state.activeRecordOffset,
      "orderBy": this.state.activeSortColumn,
      "order": this.state.activeSortDirection
    }
    this.props.getActiveRequestList(payload);
  }
  loadPendingData = ()=>{
    let payload = {
       "status":this.state.pendingSstatus,
       "search":this.state.pendingSsearch,
       "limit": this.state.pendingRecordLimit,
       "offset": this.state.pendingRecordOffset,
       "orderBy": this.state.pendingSortColumn,
       "order": this.state.pendingSortDirection
     }
     this.props.getPendingRequestList(payload);
   }
  loadArchiveData = ()=>{
    let payload = {
       "search":this.state.archiveSsearch,
       "limit": this.state.archiveRecordLimit,
       "offset": this.state.archiveRecordOffset,
       "orderBy": this.state.archiveSortColumn,
       "order": this.state.archiveSortDirection
     }
     this.props.getArchiveRequestList(payload);
   }

  handlePendingSorting = (column, sortDirection, event) => {
    this.setState({
      pendingSortColumn: column.selector,
      pendingSortDirection: sortDirection.toUpperCase()
    }, () => this.loadPendingData())


  }
  handlePendingSearch = (text)=>{
    this.setState({
      pendingSearch:text
    }, () => this.loadPendingData())
  }
  handlePendingPageChange = (pageNumber) => {
    this.setState((prevState) => ({
      pendingRecordOffset: (pageNumber - 1) * prevState.recordLimit,
    }), () => this.loadPendingData())

  }
  onChangePendingRecordsLimit = (event) => {
    this.setState({
      pendingRecordLimit: event.target.value && parseInt(event.target.value, 10)
    }, () => this.loadPendingData())
  }

  handleActiveSorting = (column, sortDirection, event) => {
    this.setState({
      activeSortColumn: column.selector,
      activeSortDirection: sortDirection.toUpperCase()
    }, () => this.loadActiveData())


  }
  handleActiveSearch = (text)=>{
    this.setState({
      activeSsearch:text
    }, () => this.loadActiveData())
  }
  handleActivePageChange = (pageNumber) => {
    this.setState((prevState) => ({
      activeRecordOffset: (pageNumber - 1) * prevState.recordLimit,
    }), () => this.loadActiveData())

  }
  onChangeActiveRecordsLimit = (event) => {
    this.setState({
      activeRecordLimit: event.target.value && parseInt(event.target.value, 10)
    }, () => this.loadActiveData())
  }

  handleArchiveSorting = (column, sortDirection, event) => {
    this.setState({
      archiveSortColumn: column.selector,
      archiveSortDirection: sortDirection.toUpperCase()
    }, () => this.loadArchiveData())


  }
  handleArchiveSearch = (text)=>{
    this.setState({
      archiveSearch:text
    }, () => this.loadArchiveData())
  }
  handleArchivePageChange = (pageNumber) => {
    this.setState((prevState) => ({
      archiveRecordOffset: (pageNumber - 1) * prevState.recordLimit,
    }), () => this.loadArchiveData())

  }
  onChangeArchiveRecordsLimit = (event) => {
    this.setState({
      archiveRecordLimit: event.target.value && parseInt(event.target.value, 10)
    }, () => this.loadArchiveData())
  }



  editPendingFeatureRequest = (row) => {
    history.push(`/admin/dashboard/merchant/activeFeatureRequest/${row.id}`)
  }
  viewFeatureRequest = (row) => {
    history.push(`/admin/dashboard/merchant/viewFeatureRequest/${row.id}`)
  }
  numCheck = (num) => {
    
    return num % 1 === 0;
 
}
  render() {
    const archivedColumns = [
      {
        name: "Be Featured ID",
        selector: "id",
        sortable: false,
      },
      {
        name: "Merchant",
        selector: "merchant",
        sortable: false
      },
      {
        name: "Activity",
        selector: "activity",
        sortable: false,

      },
      {
        name: "Letter",
        selector: "letter",
        sortable: false,
      },
      {
        name: "Activity categories",
        selector: "categories",
        sortable: false,
      },
      {
        name: "Date submitted",
        selector: "createdAt",
        sortable: false,
        format: (row) => row.createdAt?moment(row.createdAt).format('DD MMM YYYY'):'',
      },
      {
        name: "Start date",
        selector: "startDate",
        sortable: true,
        format: (row) => row.startDate?moment(row.startDate).format('DD MMM YYYY'):'',
      },
      {
        name: "End date",
        selector: "endDate",
        sortable: true,
        format: (row) => row.endDate?moment(row.endDate).format('DD MMM YYYY'):'',
      },
      {
        name: "Notes",
        selector: "notes",
        sortable: true,
      },
    ];
    const ActiveColumns = [
      {
        name: "Be Featured ID",
        selector: "id",
        sortable: false,
      },
      {
        name: "Merchant",
        selector: "merchant",
        sortable: false
      },
      {
        name: "Activity",
        selector: "activity",
        sortable: false,

      },
      {
        name: "Letter",
        selector: "letter",
        sortable: false,
      },
      {
        name: "Activity categories",
        selector: "categories",
        sortable: false,
      },
      {
        name: "Date submitted",
        selector: "createdAt",
        sortable: false,
        format: (row) => row.createdAt?moment(row.createdAt).format('DD MMM YYYY'):'',
      },
      {
        name: "Start date",
        selector: "startDate",
        sortable: true,
        format: (row) => row.startDate?moment(row.startDate).format('DD MMM YYYY'):'',
      },
      {
        name: "End date",
        selector: "endDate",
        sortable: true,
        format: (row) => row.endDate?moment(row.endDate).format('DD MMM YYYY'):'',
      },
      {
        name: "Unique View",
        selector: "uniqueView",
        sortable: true,
      },
      {
        name: "Conversions",
        selector: "conversions",
        sortable: true,
      },
      {
        name: "Conversion rate",
        selector: "conversionRate",
        sortable: true,
        format: (row)=>row.conversionRate+'%'
      },
      {
        name: "Price",
        selector: "price",
        sortable: true,
        format:(row)=>row.price?(this.numCheck(row.price)? ` ${row.currency} ${row.price}` :`${row.currency} ${(Math.round(row.price * 100) / 100).toFixed(2)}`):'',
        
      },
      {
        name: "Notes",
        selector: "notes",
        sortable: true,
      },

    ];
    const PendingColumns = [
      {
        name: "Be Featured ID",
        selector: "id",
        sortable: false,
      },
      {
        name: "Merchant",
        selector: "merchant",
        sortable: false
      },
      {
        name: "Activity",
        selector: "activity",
        sortable: false,

      },
      {
        name: "Letter",
        selector: "letter",
        sortable: false,
      },
      {
        name: "Activity categories",
        selector: "categories",
        sortable: false,
      },
      {
        name: "Date submitted",
        selector: "createdAt",
        sortable: false,
        format: (row) => row.createdAt?moment(row.createdAt).format('DD MMM YYYY'):'',
      },
      {
        name: "Start date",
        selector: "startDate",
        sortable: true,
        format: (row) => row.startDate?moment(row.startDate).format('DD MMM YYYY'):'',
      },
      {
        name: "End date",
        selector: "endDate",
        sortable: true,
        format: (row) => row.endDate?moment(row.endDate).format('DD MMM YYYY'):'',
      },
      {
        name: "Notes",
        selector: "notes",
        sortable: true,
      },
      

    ];
    return (
      <Tabs variant="pills" className="mb-3" defaultActiveKey="pending/approved" >
        <Tab eventKey="pending/approved" title="Pending/Approved">

          <Card className="box-border">
            <Card.Header>
              <Card.Title as="h5">Pending Feature Requests</Card.Title>
            </Card.Header>
            <Card.Body >
              <RenderDataTable
                columns={PendingColumns}
                onRowClicked={this.editPendingFeatureRequest}
                data={this.props.pendingActivitiesList || []}                
                defaultSortField={this.state.pendingSortColumn}
                filterTextSearch={this.state.pendingSearch}
                sortServer
                totalRecords={this.props.pendingTotalCount}
                dataLength ={this.props.pendingLenght}
                recordLimit={this.state.pendingRecordLimit}
                recordOffset={this.state.pendingRecordOffset}
                onChangeRecordsLimit={this.onChangePendingRecordsLimit}
                handlePageChange={this.handlePendingPageChange}
                onSort={this.handlePendingSorting}
                handleSearch={this.handlePendingSearch}
              />
            </Card.Body>
          </Card>
          <Card className="box-border">
            <Card.Header>
              <Card.Title as="h5">Approved Feature Requests</Card.Title>
            </Card.Header>
            <Card.Body>
              <RenderDataTable
                columns={ActiveColumns}
                onRowClicked={this.viewFeatureRequest}
                data={this.props.activeActivitiesList || []}                
                defaultSortField={this.state.activeSortColumn}
                filterTextSearch={this.state.activeSsearch}
                sortServer
                totalRecords={this.props.activeTotalCount}
                dataLength ={this.props.activeLength}
                recordLimit={this.state.activeRecordLimit}
                recordOffset={this.state.activeRecordOffset}
                onChangeRecordsLimit={this.onChangeActiveRecordsLimit}
                handlePageChange={this.handleActivePageChange}
                onSort={this.handleActiveSorting}
                handleSearch={this.handleActiveSearch}
              />
            </Card.Body>
          </Card>

        </Tab>
        <Tab eventKey="Archived" title="Archived">
          <Card className="box-border">
            <Card.Header>
              <Card.Title as="h5">Archived Featured Merchants</Card.Title>
            </Card.Header>
            <Card.Body>
              <RenderDataTable
                columns={archivedColumns}
                data={this.props.archivedList || []}               
                onRowClicked={this.viewFeatureRequest}               
                defaultSortField={this.state.archiveSortColumn}
                filterTextSearch={this.state.archiveSearch}
                sortServer
                totalRecords={this.props.archiveTotalCount}
                dataLength ={this.props.archiveLength}
                recordLimit={this.state.archiveRecordLimit}
                recordOffset={this.state.archiveRecordOffset}
                onChangeRecordsLimit={this.onChangeArchiveRecordsLimit}
                handlePageChange={this.handleArchivePageChange}
                onSort={this.handleArchiveSorting}
                handleSearch={this.handleArchiveSearch}
              />
            </Card.Body>
          </Card>

        </Tab>
      </Tabs>
    )
  }
}
const mapStateToProps = (state) => {
  console.log(state.admin)
  let pendingTotalCount = 0;
  let pendingLenght = 0
  let activeTotalCount = 0;
  let activeLength =0;
  let archiveTotalCount = 0;
  let archiveLength =0;
  let activeActivityList = state.admin.activeRequestList && state.admin.activeRequestList.data && state.admin.activeRequestList.data.data.map((item, index) => {
    return {
            id: item.featuredId,
            merchant:item.merchantName,
            activity:item.activityName,
            letter: item.letterCollected, 
            categories: item.categories && item.categories.length >0 && item.categories.map((cat, index) => <button key={index} type="btn" disabled className="category-btn" >{capitalizeFirstChar(cat.category_name)}</button>), 
            createdAt: item.createdAt, 
            startDate: item.startDate, 
            endDate: item.endDate,
            uniqueView:0,
            conversions:0,
            conversionRate:0,
            price:item.price && item.price.price?item.price.price:null,
            currency:item.price && item.price.currency?item.price.currency:'SGD',
            notes:item.notes
          }
  })  
  activeLength = state.admin.activeRequestList && state.admin.activeRequestList.data && state.admin.activeRequestList.data.data.length
  activeTotalCount = state.admin.activeRequestList && state.admin.activeRequestList.data && state.admin.activeRequestList.data.totalCount;
  let pendingActivityList = state.admin.pendingRequestList && state.admin.pendingRequestList.data && state.admin.pendingRequestList.data.data.map((item, index) => {
    return {
            id: item.featuredId,
            merchant:item.merchantName,
            activity:item.activityName,
            letter: item.letterCollected, 
            categories: item.categories && item.categories.length >0 && item.categories.map((cat, index) => <button key={index} type="btn" disabled className="category-btn" >{capitalizeFirstChar(cat.category_name)}</button>), 
            createdAt: item.createdAt, 
            startDate: item.startDate, 
            endDate: item.endDate,
            notes:item.notes
          }
  })  
  pendingLenght = state.admin.pendingRequestList && state.admin.pendingRequestList.data && state.admin.pendingRequestList.data.data.length
  pendingTotalCount = state.admin.pendingRequestList && state.admin.pendingRequestList.data && state.admin.pendingRequestList.data.totalCount;
  let archivedList = state.admin.archiveRequestList && state.admin.archiveRequestList.data && state.admin.archiveRequestList.data.data.map((item, index) => {
    return {
            id: item.featuredId,
            merchant:item.merchantName,
            activity:item.activityName,
            letter: item.letterCollected, 
            categories: item.categories && item.categories.length >0 && item.categories.map((cat, index) => <button key={index} type="btn" disabled className="category-btn" >{capitalizeFirstChar(cat.category_name)}</button>), 
            createdAt: item.createdAt, 
            startDate: item.startDate, 
            endDate: item.endDate,
            notes:item.notes
          }
  })  
  archiveLength = state.admin.archiveRequestList && state.admin.archiveRequestList.data && state.admin.archiveRequestList.data.data.length
  archiveTotalCount = state.admin.archiveRequestList && state.admin.archiveRequestList.data && state.admin.archiveRequestList.data.totalCount;
  
  return {
    archivedList: archivedList,
    activeActivitiesList: activeActivityList,
    activeTotalCount:activeTotalCount,
    pendingActivitiesList:pendingActivityList,
    pendingTotalCount:pendingTotalCount,
    pendingLenght:pendingLenght,
    activeLength:activeLength,
    archiveTotalCount:archiveTotalCount,
    archiveLength:archiveLength,
  };
}
export default connect(mapStateToProps, {getActiveRequestList, getPendingRequestList, getArchiveRequestList})(BeFeatured)