
import merchantDashboard from './App/containers/merchant/dashboard';
import Bookings from './App/containers/merchant/bookings';
import Error404 from './Demo/Maintenance/Error';
import BeFeatured  from './App/containers/merchant/beFeatured';
// import profile from './App/containers/merchant/profile';
import Reviews from './App/containers/merchant/reviews';
import SettlementsAll from './App/containers/merchant/settlements-all';
import SettlementsRemittance from './App/containers/merchant/settlements-remittance';
import resetPassword from './App/containers/merchant/resetPassword';
import ChangePassword from './App/containers/merchant/changePassword';
import profile from './App/containers/merchant/profile';
import pramotions from './App/containers/merchant/pramotions';
import bookingDetails from './App/containers/merchant/bookingDetails';
import MerchantConfirmLink from './App/containers/merchant/confirmLink';
import updatePassword from './App/containers/merchant/updatePassword';
import EmailConfirmLink from './App/containers/merchant/EmailConfirmLink';

const routes = [
    { path: '/merchant/dashboard', exact: true, name: "Merchant Dashboard", component: merchantDashboard },
    { path: '/merchant/dashboard/bookings/list', exact: true, name: "Bookings List", component: Bookings },
    // { path: '/merchant/dashboard/profile', exact: true, name: "Profile", component: profile },
    { path: '/merchant/dashboard/reviews', exact: true, name: "Reviews", component: Reviews },
    { path: '/merchant/dashboard/be-featured', exact: true, name: "Featured", component: BeFeatured },
    { path: '/merchant/dashboard/profile', exact: true, name: "View Merchant Profile", component: profile },
    { path: '/merchant/dashboard/settlements-all', exact: true, name: "Settlements-all", component: SettlementsAll },
    { path: '/merchant/dashboard/settlements-remittance', exact: true, name: "Settlements-all", component: SettlementsRemittance },
    { path: '/merchant/dashboard/pramotions', exact: true, name: "Pramotions", component: pramotions },
    { path: '/merchant/dashboard/bookingDetails/:id', exact: true, name: "Booking Details", component: bookingDetails },
    { path: '/merchant/dashboard/updatePassword', exact: true, name: "Update Password", component:updatePassword  },
    { path: '/merchant/reset-password', exact: true, name: "Reset Password", component: resetPassword },
    { path: '/merchant/change-password/:id', exact: true, name: "Change password", component: ChangePassword },
    { path: '/merchant/confirm-link', exact: true, name: "Confirm Link", component: MerchantConfirmLink },
    { path: '/merchant/email-confirm-link/:otp/:mail', exact: true, name:'Email Confirm Link', component: EmailConfirmLink },

    { path: '*', name: 'Error 404', component: Error404 },
  
    // { path: ' /merchant/dashboard/bookings/qrCode', exact: true, name: "QR Code Scanner", component: merchantDashboard},

];
export default routes;