  onSelectFile = (e, index) => {

    if (e.target.files && e.target.files.length > 0) {
      let imT = e.target.files[0].name.split('.');
      imgType = e.target.files[0].type;
      imgSendname = new Date().getTime()+'.'+imT[1];
      const reader = new FileReader();
      reader.addEventListener('load', () => {

        this.setState({ ["src" + index]: reader.result,unSaved:true });
      }
      );
      reader.readAsDataURL(e.target.files[0]);
    }
  };

  // If you setState the crop in here you should return false.
  onImageLoaded = (image, index) => {
    this["imageRef" + index] = image;
  };

  onCropComplete = (crop, index) => {
    this.makeClientCrop(crop, index);
  };

  onCropChange = (crop, percentCrop, index) => {
    // You could also use percentCrop:
    // this.setState({ crop: percentCrop });
    this.setState({ ["crop" + index]: crop });
  };
  makeClientCrop = (crop, index) => {
    if (this["imageRef" + index] && crop.width && crop.height) {
      const croppedImageUrl = this.getCroppedImg(
        this["imageRef" + index],
        crop,
        imgSendname
      );
      this.setState({ ["croppedImageUrl" + index]: croppedImageUrl },
       () => {

      var file = this.dataURLtoFile(this.state["croppedImageUrl" + index],imgSendname);
      console.log(file);
      const formData = new FormData();
      formData.append('logo',file);
         this.props.convertBase64ToUrl(file, `croppedImageUrl${index}`)
        
        });
    }
  }
  dataURLtoFile = (dataurl, filename) =>{
 
    var arr = dataurl.split(','),
        mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]), 
        n = bstr.length, 
        u8arr = new Uint8Array(n);
        
    while(n--){
        u8arr[n] = bstr.charCodeAt(n);
    }
    
    return new File([u8arr], filename, {type:mime});
}
  getCroppedImg(image, crop, fileName) {
    const canvas = document.createElement('canvas');
    const scaleX = image.naturalWidth / image.width;
    const scaleY = image.naturalHeight / image.height;
    canvas.width = crop.width;
    canvas.height = crop.height;
    const ctx = canvas.getContext('2d');

    ctx.drawImage(
      image,
      crop.x * scaleX,
      crop.y * scaleY,
      crop.width * scaleX,
      crop.height * scaleY,
      0,
      0,
      crop.width,
      crop.height
    );
    const base64Image = canvas.toDataURL(imgType);
    return base64Image;
  }

  handleAddDeals = () => {
    this.setState((prevState) => {
      let deals = prevState.deals || [];
      let obj = {
          title: '',
          description: '',
          outlet: '',
          currency:'SGD',
          originalPrice: '',
          discountedPrice: '',
          discountedPercentage: '',
          discountedAmount: '',
          merchantActivityId: '',
          activityId:this.state.activityId
        
      }
      deals.push({ ...obj })
      return {
        deals: deals
      }
    })
  }
  handleDealsOnChange = (input, { field, index }) => {
    let deals = this.state.deals;
    if (field === 'outlets'){
      console.log(input)
      deals[index][field] = input ? (input.target ? input.target.value : input) : [];
      console.log(deals[index][field])
    
    }
    else  {
      deals[index][field] = input ? (input.target ? input.target.value : input) : '';
      if (deals[index] && deals[index] && deals[index].originalPrice) {
        if (input.target.value) {
          if (field === 'discountedPercentage') {
            deals[index]['discountAmount'] = ((parseFloat(input.target.value) / 100) * deals[index].originalPrice).toFixed(2);
            deals[index]['discountedPrice'] = (deals[index].originalPrice - ((parseFloat(input.target.value) / 100) * deals[index].originalPrice)).toFixed(2);
          } else if (field === 'discountedPrice') {
            deals[index]['discountedPercentage'] = (((deals[index].originalPrice - parseFloat(input.target.value)) / deals[index].originalPrice) * 100).toFixed(2);
            deals[index]['discountAmount'] = (deals[index].originalPrice - parseFloat(input.target.value)).toFixed(2);
          } else if (field === 'discountAmount') {
            deals[index]['discountedPercentage'] = (parseFloat(input.target.value) / deals[index].originalPrice * 100).toFixed(2);
            deals[index]['discountedPrice'] = (deals[index].originalPrice - parseFloat(input.target.value)).toFixed(2);
          }
        } else {
          deals[index]['discountedPercentage'] = '';
          deals[index]['discountedPrice'] = '';
          deals[index]['discountAmount'] = '';
        }
      }
    }
    if (field === 'originalPrice' && !input.target.value) {
      deals[index]['discountedPercentage'] = '';
      deals[index]['discountedPrice'] = '';
      deals[index]['discountAmount'] = '';
    }
    this.setState({
      deals: deals,
      unSaved:true
    })
  
  }
