import React from 'react';
import { Tabs, Tab } from 'react-bootstrap';
import { showInfoMsg } from '../../../components/commonUIComponents';
import { Row, Col } from 'react-bootstrap';
import { connect } from 'react-redux';
import Step1 from './step1';
import Step2 from './step2';
import Step3 from './step3'; 
import { saveMerchantDetails,getMerchantSelectList, clearMerchantDetails,changeMerchantStatus, saveMerchantOutlets, getMerchantDetails, getOutletsOfMerchant, getMerchantOutletById, clearMerchantData, updateMerchantDetails, clearOutletDetails, updateMerchantOutlet, deleteOutlet, deleteMerchant, checkExistingMerchantName, getAlreadyAddedActivities, clearAlreadyAddedActivities } from '../actions/index'
import moment from 'moment';
import { maskPhoneNumber,API_URL, MERCHANT_PORT } from '../../../utilActions';
import { geocodeByAddress, getLatLng } from 'react-google-places-autocomplete';
const imgPath = `${API_URL}:${MERCHANT_PORT}`
class AddMerchant extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      merchantLogoSrc: '',
      merchantLogoFile:'',
      gmapsLoaded: false,
      activeKey: 'step1',
      openingHours: [
        {
          day: 'Sun',
          startTime: '',
          endTime: '',
          status:'open'
        },
        {
          day: 'Mon',
          startTime: '',
          endTime: '',
          status:'open'
        },
        {
          day: 'Tue',
          startTime: '',
          endTime: '',
          status:'open'
        }
        ,
        {
          day: 'Wed',
          startTime: '',
          endTime: '',
          status:'open'
        }
        ,
        {
          day: 'Thu',
          startTime: '',
          endTime: '',
          status:'open'
        },
        {
          day: 'Fri',
          startTime: '',
          endTime: '',
          status:'open'
        }
        ,
        {
          day: 'Sat',
          startTime: '',
          endTime: '',
          status:'open'
        },
        {
          day: 'Holiday',
          startTime: '',
          endTime: '',
          status:'open'
        }
      ],

      merchantId: '',
      mobileForSparks: '',
      selectedOutlet: '',
      merchantName: '',
      merchantSignUpEmail: '',
      merchantWebsite: '',
      contactPersonForSparks: '',
      contactEmail: '',

      outletName: '',
      outletEmail: '',
      streetName: '',
      fullAddress: '',
      latitude:'',
      longitude:'',
      postalCode: '',
      outletMobile: '',
      region:'',
      station: '',
      outletCountryCode: '+65',
      existingLogo: '',
      existingMerchant:'',
      isAlreadyExists:true
    }
    // console.log(this.props.match.params.id)
    if (window.location.href.indexOf("merchant/edit") > -1) {
      // console.log(this.props.match.params)
      this.state.merchantId = this.props.match.params.id
      this.state.paramActivityId = this.props.match.params.activityId
    }
  }
  initMap = () => {
    this.setState({
      gmapsLoaded: true,
      fullAddress:this.state.fullAddress
    })
  }

  componentDidMount() {
    window.initMap = this.initMap
    const gmapScriptEl = document.createElement(`script`)
    gmapScriptEl.src = `https://maps.googleapis.com/maps/api/js?key=AIzaSyC6NMl5VVe0jDn5EYrKqrHa6GZxFFk2AoQ&libraries=places&callback=initMap`
    document.querySelector(`body`).insertAdjacentElement(`beforeend`, gmapScriptEl)
    this.props.getMerchantSelectList()
    if (this.state.merchantId) {
      
      this.getMerchantData();
      this.getAllMerchantOutlets();
      this.handleAlreadyAddedActivities();
    }
    if (this.props.isMerchantUpdated) {
      this.setState({
        activeKey: 'step2'
      }, () => this.handleTabChange(this.state.activeKey))
    }

    if (this.props.isNewMerchantActivityAdded) {
      this.setState({
        activeKey: 'step3'
      }, () => this.handleTabChange(this.state.activeKey))
    }
  }
  componentDidUpdate(prevProps, prevState) {
    
    if (this.props.merchantDetails && this.props.merchantDetails.data && this.props.merchantDetails !== prevProps.merchantDetails) {
      let { merchantName, merchantSignUpEmail, merchantWebsite, contactPersonForSparks, contactEmail, notes } = this.props.merchantDetails.data;
      let mobileForSparks = this.props.merchantDetails.data.mobileForSparks && this.props.merchantDetails.data.mobileForSparks.mobile && maskPhoneNumber(this.props.merchantDetails.data.mobileForSparks.mobile);
      let sparksCountryCode = this.props.merchantDetails.data.mobileForSparks && this.props.merchantDetails.data.mobileForSparks.code;
      this.setState({
        merchantName,
        merchantSignUpEmail,
        merchantWebsite: merchantWebsite ? merchantWebsite.split("//").pop() : '',
        contactPersonForSparks,
        contactEmail,
        mobileForSparks,
        notes,
        sparksCountryCode,
        existingLogo: this.props.merchantDetails.data.merchantLogo ? `${this.props.merchantDetails.data.merchantLogo}` : ''

      })
    }
    if (this.props.outletById && this.props.outletById !== prevProps.outletById) {
      
      let { outletName, outletEmail, } = this.props.outletById;
      let outletMobile = this.props.outletById.outletMobile && maskPhoneNumber(this.props.outletById.outletMobile);
      let outletCountryCode = (this.props.outletById.outletExtension && this.props.outletById.outletExtension ==='outlook extension'?'+65':this.props.outletById.outletExtension) || this.state.outletCountryCode;
      let streetName = (this.props.outletById && this.props.outletById.streetAddress) || '';
      let fullAddress = (this.props.outletById && this.props.outletById.address) || '';
      let latitude =  (this.props.outletById && this.props.outletById.mrtLatitude) || '';
      let longitude =  (this.props.outletById && this.props.outletById.mrtLongitude) || '';
      let postalCode = (this.props.outletById && this.props.outletById.postalCode) || '';
      let region = (this.props.outletById && this.props.outletById.region) || '';
      let station = (this.props.outletById && this.props.outletById.trainStations) || '';
      let openingHours = (this.props.outletById && this.props.outletById.openingHours.length > 0  && this.props.outletById.openingHours.map((day) => {

        let startTime = day.startTime ? moment(new Date(day.startTime)).format("HH:mm") : '';
        let endTime = day.endTime ? moment(new Date(day.endTime)).format("HH:mm") : '';
        let status = day.status ? day.status:'open';
        return { ...day, startTime, endTime,status }
      })) || this.state.openingHours;


      this.setState({
        outletName,
        outletEmail,
        streetName,
        fullAddress,
        latitude,
        longitude,
        postalCode,
        outletMobile,
        station,
        region,
        openingHours: [...openingHours],
        outletCountryCode
      })

    }
    // console.log(this.props)
    if (this.props.isOutletUpdated && this.props.isOutletUpdated !== prevProps.isOutletUpdated) {

      this.setState({
        activeKey: 'step3'
      }, () => this.handleTabChange(this.state.activeKey))
    }
    if (this.props.isMerchantUpdated && this.props.isMerchantUpdated !== prevProps.isMerchantUpdated) {
      this.setState({
        activeKey: 'step2'
      }, () => this.handleTabChange(this.state.activeKey))
    }



  }
  componentWillUnmount() {
    this.props.clearMerchantData()
    this.props.clearMerchantDetails();
    this.props.clearAlreadyAddedActivities();

  }
  getMerchantData() { 
    this.props.getMerchantDetails(this.state.merchantId);
  }
  handleAlreadyAddedActivities = () => {
    this.props.getAlreadyAddedActivities(this.state.merchantId)
  }
  getAllMerchantOutlets() {
    this.props.getOutletsOfMerchant(this.state.merchantId);
  }
 

  getOutletData = (input) => {
    if (input && input.value) {
      this.props.getMerchantOutletById(input.value);
    } else {
      this.setState({
        outletName: '',
        outletEmail: '',
        streetName: '',
        fullAddress: '',
        latitude:'',
        longitude:'',
        postalCode: '',
        outletMobile: '',
        station: '',
        outletCountryCode: '+65',
        region: '',
        openingHours: [
          {
            day: 'Sun',
            startTime: '',
            endTime: '',
            status:'open'
          },
          {
            day: 'Mon',
            startTime: '',
            endTime: '',
            status:'open'
          },
          {
            day: 'Tue',
            startTime: '',
            endTime: '',
            status:'open'
          }
          ,
          {
            day: 'Wed',
            startTime: '',
            endTime: '',
            status:'open'
          }
          ,
          {
            day: 'Thu',
            startTime: '',
            endTime: '',
            status:'open'
          },
          {
            day: 'Fri',
            startTime: '',
            endTime: '',
            status:'open'
          }
          ,
          {
            day: 'Sat',
            startTime: '',
            endTime: '',
            status:'open'
          },
          {
            day: 'Holiday',
            startTime: '',
            endTime: '',
            status:'open'
          }
        ],
      }, () => this.props.clearOutletDetails())

    }

    this.setState({
      selectedOutlet: input
    })

  }




  handleLogoField = (event, file) => {
    if (file) {
      let fileReader = new FileReader();
      fileReader.onload = (event) => {
        this.setState({
          merchantLogoSrc: event.target.result,
          merchantLogoFile:file
        })
      }
      fileReader.onerror = function (event) {
      };
      fileReader.readAsDataURL(file)
    }


  }

  handleSubmitStep1 = (event, formData) => {
    event.preventDefault();
    if (!this.props.loading) {
      let postData = {};
      postData.merchantName = formData.merchantName
      postData.merchantSignUpEmail = formData.merchantSignUpEmail
      postData.merchantWebsite = "https://" + formData.merchantWebsite
      postData.contactPersonForSparks = this.state.contactPersonForSparks
      postData.contactEmail = formData.contactEmail
      postData.notes = formData.notes
      postData.merchantLogo = this.props.convertedImageField === 'croppedImageUrl' && this.props.convertedImage ? `${this.props.convertedImage}` : (this.props.merchantDetails && this.props.merchantDetails.data && this.props.merchantDetails.data.merchantLogo) || '';
      postData.mobileForSparks = { code: this.state.sparksCountryCode, mobile: this.state.mobileForSparks && this.state.mobileForSparks.toString().split('-').join('') }
      if (this.props.merchantDetails && this.props.merchantDetails.data) {
        let merchantId = this.props.merchantDetails.data.id;

        this.props.updateMerchantDetails(postData, merchantId)
      } else {
        this.props.saveMerchantDetails(postData,this.state.paramActivityId);
      }
    }

  }

  getCountryCodeForSparks = (countryCode) => {
    this.setState({
      sparksCountryCode: countryCode
    })
  }
  getCountryCodeForOutlet = (countryCode) => {
    this.setState({
      outletCountryCode: countryCode
    })
  }
  handleOnChangeStep1 = (event, input) => {
    
    this.setState({
      [event.target.name]: event.target.value
    })
  }
  handleSubmitStep2 = (event, formData) => {
    event.preventDefault();
    if (!this.props.loading) {
      let postData = {}

      postData.outletname = this.state.outletName
      postData.outletemail = this.state.outletEmail
      postData.outletMobile = this.state.outletMobile && this.state.outletMobile && this.state.outletMobile.toString().split('-').join('')
      // postData.outletExtension = "string";
      postData.outletExtension = this.state.outletCountryCode;
      postData.merchantId = this.state.merchantId;
      postData.openingHours = this.state.openingHours && this.state.openingHours.map((day) => {

        let startTime = day.startTime ? moment(new Date(2020, 0, 1, day.startTime.split(":")[0], day.startTime.split(":")[1])).toISOString() : '';
        let endTime = day.endTime ? moment(new Date(2020, 0, 1, day.endTime.split(":")[0], day.endTime.split(":")[1])).toISOString() : '';

        return { ...day, startTime, endTime };
      })

      let address = {}
      address.streetAddress = this.state.streetName
      address.address = this.state.fullAddress
      address.postalCode = this.state.postalCode
      address.trainStations = this.state.station
      address.region = this.state.region
      address.mrtLatitude = this.state.latitude
      address.mrtLongitude = this.state.longitude
      postData.activity_location = address;
      if (this.props.outletById) {
        postData.outletId = this.props.outletById.id;
        this.props.updateMerchantOutlet(postData, this.props.outletById.id, this.state.merchantId)
      } else {
        this.props.saveMerchantOutlets(postData,this.state.paramActivityId);
      }


    }
  }
  getLocation = (e) => {
    console.log(e)
    geocodeByAddress(e.description)
    .then(results => getLatLng(results[0]))
    .then(({ lat, lng }) =>{
      this.setState({
        fullAddress:e.description,
        latitude:lat,
        longitude:lng,
      })
      }
    );
    

  }
  handleCloseOpen = (e,index,status) => {
    e.preventDefault()
    console.log(e,index,status)
    let openingHours = this.state.openingHours;

    openingHours[index]['endTime'] =  '';
    openingHours[index]['startTime'] =  '';
    openingHours[index]['status'] =  status;
    this.setState({
      openingHours: openingHours
    })
  }
  getTime = (momentObj, index, time) => {

    let openingHours = this.state.openingHours;

    openingHours[index][time] = momentObj._d ? moment(momentObj._d).format("HH:mm") : '';
    this.setState({
      openingHours: openingHours
    })
  }
  handleAddNewOutlet = (event) => {
    this.setState({
      selectedOutlet: ''
    }, () => this.props.clearOutletDetails())
  }
  handleOnChangeStep2 = (event) => {
    this.setState({
      [event.target.name]: event.target.value
    })
  }

  handleRemoveSuggestionConfirm = (event) => {

    event.preventDefault();
    showInfoMsg('Are you Sure To Delete Outlet?', () => this.deleteOutlet(this.state.selectedOutlet.value))
  }
  handleRemoveMerchantConfirm = (event) => {

    event.preventDefault();
    showInfoMsg('Are you Sure To Delete Merchant?', () => this.handleRemoveMerchant())
  }
  handleStatusMerchantConfirm = (event)=>{
    event.preventDefault();
    showInfoMsg('Are you Sure To Update Merchant Status?', () => this.handleStatusUpdateMerchant())
  }
  deleteOutlet = (id) => {
    this.props.deleteOutlet(id, this.state.merchantId,this.state.paramActivityId)
    this.setState({
      selectedOutlet: null
    })
    localStorage.setItem('outletSelectId','')
    this.getOutletData(null)
  }
  handleTabChange = (stepName, event) => {

    // this.props.clearAlreadyAddedSelectedActivityData();
    this.setState({
      activeKey: stepName
    })
    if (this.props.merchantOutlets && stepName === 'step2') {
      if(localStorage.getItem('outletSelectId')){
        for(let item of this.props.merchantOutlets){
          if(parseInt(item.value) === parseInt(localStorage.getItem('outletSelectId'))){
            this.setState({
              selectedOutlet: item
            }, () => {
              if (this.state.selectedOutlet && this.state.selectedOutlet.value) {
                this.props.getMerchantOutletById(this.state.selectedOutlet.value)
              }
            })
          }
        }        
      }else{
        this.setState({
          selectedOutlet: this.props.merchantOutlets[0]
        }, () => {
          if (this.state.selectedOutlet && this.state.selectedOutlet.value) {
            this.props.getMerchantOutletById(this.state.selectedOutlet.value)
          }
        })
      }
      
    }
    if (stepName === 'step3') {
      this.getAllMerchantOutlets();
      // this.handleAlreadyAddedActivities();

    }
  }
  handleRemoveMerchant = () => {
    let id = this.props.merchantDetails && this.props.merchantDetails.data && this.props.merchantDetails.data.id
    let payload = {
      "status":0,
	    "merchantId":id
    }
    this.props.deleteMerchant(payload)
  }
  handleStatusUpdateMerchant = () => {
    // console.log(this.props.merchantDetails.data)
    let status =1;
    let id = this.props.merchantDetails && this.props.merchantDetails.data && this.props.merchantDetails.data.id
    if(this.props.merchantDetails.data.status === 1){
      status = 2;
    }else{
      status = 1;
    }
    let payload = {
      "status":status,
	    "merchantId":id
    }
    this.props.changeMerchantStatus(payload)
  }

  isAlreadyExists = (str) => {
    if (str && str.trim().length) {
      if ((this.props.merchantList || []).findIndex((name) => name.label && name.label.trim().toLowerCase() === str.trim().toLowerCase() && parseInt(name.value, 10) !== parseInt(this.state.merchantId, 10)) < 0) {

        return false
      } else {

        return true
      }
    } else {
      return true
    }

  }
  
  render() {

    return (

      <Tabs activeKey={this.state.activeKey} onSelect={this.handleTabChange}>
        <Tab eventKey="step1" title={
          <div>Step 1
          <br />
            Merchant Details
        </div>}
        >
          <Step1
            existingMerchantLength={this.props.alreadyExistingMerchants && this.props.alreadyExistingMerchants.length}
            state={this.state}            
            isAlreadyExists={this.isAlreadyExists}
            handleOnChangeStep1={this.handleOnChangeStep1}
            handleSubmitStep1={this.handleSubmitStep1}
            merchantLogoSrc={this.state.merchantLogoSrc}
            merchantLogoFile={this.state.merchantLogoFile}
            handleLogoField={this.handleLogoField}
            merchantDetails={this.props.merchantDetails && this.props.merchantDetails.data}
            getCountryCodeForSparks={this.getCountryCodeForSparks}
            handleRemoveMerchantConfirm={this.handleRemoveMerchantConfirm}
            handleStatusMerchantConfirm={this.handleStatusMerchantConfirm}
            loading={this.props.loading}
            merchantStr={this.state.merchantStr}
          />
        </Tab>
        <Tab eventKey="step2" disabled={!this.props.merchantDetails ? true : false} title={


          <Row>
            <Col md="12"><div>
            
            Step 2</div><div>Merchant Outlets</div></Col>
          </Row>
        }  >
          <Step2
            handleSubmitStep2={this.handleSubmitStep2}
            openingHours={this.state.openingHours}
            gmapsLoaded={this.state.gmapsLoaded}
            merchantDetails={this.props.merchantDetails}
            merchantOutlets={this.props.merchantOutlets}
            getOutletData={this.getOutletData}
            getTime={this.getTime}
            state={this.state}
            getCountryCodeForOutlet={this.getCountryCodeForOutlet}
            handleAddNewOutlet={this.handleAddNewOutlet}
            handleOnChangeStep2={this.handleOnChangeStep2}
            outletById={this.props.outletById}
            getLocation={this.getLocation}
            handleCloseOpen={this.handleCloseOpen}
            handleRemoveSuggestionConfirm={this.handleRemoveSuggestionConfirm}
            loading={this.props.loading}

          />
        </Tab>
        <Tab eventKey="step3" disabled={(!this.props.merchantDetails || !this.props.merchantOutlets.length) ? true : false} title={
          <div>Step 3
          <br />
            Activity -Specific Details
        </div>} >
          <Step3
            merchantDetails={this.props.merchantDetails}
          />
        </Tab>
        <Tab>
        </Tab>
      </Tabs>
    );
  }
}
const mapStateToProps = (state) => {
  // console.log(state.admin)
  let merchantOutlets = ((state.admin.merchantOutlets && state.admin.merchantOutlets.data) || []).map((item) => ({
    label: item.outletName,
    value: item.id
  }))

  let merchantList = ((state.admin.getMerchantSelect && state.admin.getMerchantSelect.data) || []).map((item) => ({
    label: item.merchantName,
    value: item.id
  }))
  return {

    merchantList:merchantList,
    alreadyExistingMerchants: state.admin.alreadyExistingMerchants && state.admin.alreadyExistingMerchants.data,
    merchantDetails: state.admin.merchantDetails,
    merchantOutlets: merchantOutlets,
    outletById: state.admin.merchantOutlet && state.admin.merchantOutlet.data,
    savedMerchantOutlet: state.admin.savedMerchantOutlet,
    isMerchantUpdated: state.admin.isMerchantUpdated,
    isOutletUpdated: state.admin.isOutletUpdated,
    convertedImage: state.admin.convertedImage,
    convertedImagePath:state.admin.convertedImagePath,
    convertedImageField: state.admin.convertedImageField,
    loading: state.admin.loading,
    s3BucketDynamicImageUrl: state.admin.s3BucketDynamicImageUrl,
    isNewMerchantActivityAdded: state.admin.isNewMerchantActivityAdded
  }

}

export default connect(mapStateToProps, { saveMerchantDetails,getMerchantSelectList, clearMerchantDetails,changeMerchantStatus, saveMerchantOutlets, getMerchantDetails, getOutletsOfMerchant, getMerchantOutletById, clearMerchantData, updateMerchantDetails, clearOutletDetails, updateMerchantOutlet, deleteOutlet, deleteMerchant, checkExistingMerchantName, getAlreadyAddedActivities, clearAlreadyAddedActivities })(AddMerchant);
