import React from 'react';
import { NavLink } from 'react-router-dom';
import Breadcrumb from "../../../App/layout/AdminLayout/Breadcrumb";
import lock from '../../../assets/images/user/lock.png';
import { RenderTextField } from '../../components/commonUIComponents';
import validator from 'validator';
import { ValidationForm } from 'react-bootstrap4-form-validation';
import { SubmitButton } from '../../components/commonUIComponents';
import { connect } from 'react-redux'
import validations from '../../validations';
import { resetPassword } from './actions/index';
class ChangePassword extends React.Component {
  
  handleSubmit = (event, formValues) => {
    let payload ={
      "id":this.props.match.params.id,
      "password":formValues.password
    }
    event.preventDefault();
    this.props.resetPassword(payload)
  }
  render() {

    return (
      <>
        <Breadcrumb />
        <div className="auth-wrapper">
          <div className="auth-content subscribe">
            <div className="card">
              <div className="row no-gutters">
                <div className="col-md-4 col-lg-6 d-md-flex d-lg-flex theme-bg-new align-items-center justify-content-center">
                  <img src={lock} alt="lock images" className="img-fluid" />
                </div>
                <div className="col-md-8 col-lg-6">
                  <div className="card-body text-center">
                    <div className="row justify-content-center">
                      <div className="col-sm-10">
                        <ValidationForm onSubmit={this.handleSubmit}>
                          <div className="mb-4">
                            <i className="feather icon-mail auth-icon" />
                          </div> 
                          <h3 className="mb-4">Change Password</h3>
                          <div className="input-group mb-3">
                            <RenderTextField
                              name="password"
                              placeholder="New Password"
                              errorMessage={{ validator: validations.required }}
                              minLength={6}
                              validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                            />
                          </div>
                          <div className="input-group mb-3">
                            <RenderTextField
                              name="cpassword"
                              placeholder="Confirm Password"
                              minLength={6}
                              errorMessage={{ validator: validations.required }}
                              validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                            />
                          </div>
                          <SubmitButton disabled={this.props.loading} >Change Password</SubmitButton>
                          <p className="mb-0 text-muted">Back to login <NavLink to="/">Login</NavLink></p>
                        </ValidationForm>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    loading:state.admin.loading,
    initialValues: {
      password:'',
      cpassword: '',
      saveCredentials: false,
    }
  }
}
export default connect(mapStateToProps, { resetPassword })(ChangePassword);