import { merchantActionTypes } from "../constants";

const initialState = {
  loading: false,
  loginUser: null,
  signupUser: null,
  authToken: null,
  accountDetails: null
}
export default (state = initialState, { type, payload }) => {
  switch (type) {
    case merchantActionTypes.login.REQUEST:
      return {
        ...state,
        loading: true
      }
    case merchantActionTypes.login.SUCCESS:
      return {
        ...state,
        loading: false,
        loginUser: payload,
        authToken: payload.token
      }
    case merchantActionTypes.login.FAILURE:
      return {
        ...state,
        loading: false,
        loginUser: null,
        authToken: null,
      }
    case merchantActionTypes.forgot.REQUEST:
      return {
        ...state,
        loading: true
      }
    case merchantActionTypes.forgot.SUCCESS:
      return {
        ...state,
        loading: false,
        loginUser: null,
        authToken: null
      }
    case merchantActionTypes.forgot.FAILURE:
      return {
        ...state,
        loading: false,
        loginUser: null,
        authToken: null,
      }
    case merchantActionTypes.reset.REQUEST:
      return {
        ...state,
        loading: true
      }
    case merchantActionTypes.reset.SUCCESS:
      return {
        ...state,
        loading: false,
        loginUser: null,
        authToken: null
      }
    case merchantActionTypes.reset.FAILURE:
      return {
        ...state,
        loading: false,
        loginUser: null,
        authToken: null,
      }
    case merchantActionTypes.signup.REQUEST:
      return {
        ...state,
        loading: true
      }
    case merchantActionTypes.signup.SUCCESS:
      return {
        ...state,
        loading: false,
        signupUser: payload
      }
    case merchantActionTypes.signup.FAILURE:
      return {
        ...state,
        loading: false,
        signupUser: null
      }
    case merchantActionTypes.updateMerchantPassword.REQUEST:
      return {
        ...state,
        loading: true,
        regularDealsList: null

      }
    case merchantActionTypes.updateMerchantPassword.SUCCESS:
      return {
        ...state,
        loading: false,
        regularDealsList: payload
      }
    case merchantActionTypes.updateMerchantPassword.FAILURE:
      return {
        ...state,
        loading: false,
        regularDealsList: null
      }
    case merchantActionTypes.updateMerchantEmail.REQUEST:
      return {
        ...state,
        loading: true,
        regularDealsList: null

      }
    case merchantActionTypes.updateMerchantEmail.SUCCESS:
      return {
        ...state,
        loading: false,
      }
    case merchantActionTypes.updateMerchantEmail.FAILURE:
      return {
        ...state,
        loading: false,
      }
    case merchantActionTypes.addBankDetails.REQUEST:
      return {
        ...state,
        loading: true
      }
    case merchantActionTypes.addBankDetails.SUCCESS:
      return {
        ...state,
        loading: false,
        bankDetail: payload
      }
    case merchantActionTypes.confirmEmailDone.FAILURE:
      return {
        ...state,
        loading: false,
        confirmEmailDone: payload
      }
    case merchantActionTypes.confirmEmailDone.REQUEST:
      return {
        ...state,
        loading: true
      }
    case merchantActionTypes.confirmEmailDone.SUCCESS:
      return {
        ...state,
        loading: false,
        confirmEmailDone: payload
      }
      
    case merchantActionTypes.addBankDetails.FAILURE:
      return {
        ...state,
        loading: false,
        bankDetail: null
      }
    case merchantActionTypes.createFeaturedActvitityRequest.REQUEST:
      return {
        ...state,
        loading: true,
        createFeaturedActivityDetails:payload
      }
    case merchantActionTypes.createFeaturedActvitityRequest.SUCCESS:
      return {
        ...state,
        loading: false,
        createFeaturedActivityDetails:payload,
      }
    case merchantActionTypes.createFeaturedActvitityRequest.FAILURE:
      return {
        ...state,
        loading: false,
        createFeaturedActivityDetails:payload
      }
    case merchantActionTypes.createPramotionRequest.REQUEST:
      return {
        ...state,
        loading: true,
        createPramotionRequestDetails:payload
      }
    case merchantActionTypes.createPramotionRequest.SUCCESS:
      return {
        ...state,
        loading: false,
        createPramotionRequestDetails:null,
      }
    case merchantActionTypes.createPramotionRequest.FAILURE:
      return {
        ...state,
        loading: false,
        createPramotionRequestDetails:payload
      }
    case merchantActionTypes.getPramotionsDetailsRequest.REQUEST:
      return {
        ...state,
        loading: true
      }
    case merchantActionTypes.getPramotionsDetailsRequest.SUCCESS:
      return {
        ...state,
        loading: false,
        pramotionRequestList: payload
      }
    case merchantActionTypes.getPramotionsDetailsRequest.FAILURE:
      return {
        ...state,
        loading: false,
        pramotionRequestList: null
      }
    case merchantActionTypes.getFeaturedActvitityRequest.REQUEST:
      return {
        ...state,
        loading: true
      }
    case merchantActionTypes.getFeaturedActvitityRequest.SUCCESS:
      return {
        ...state,
        loading: false,
        featuredActivityRequestList: payload
      }
    case merchantActionTypes.getFeaturedActvitityRequest.FAILURE:
      return {
        ...state,
        loading: false,
        featuredActivityRequestList: null
      }
    case merchantActionTypes.deletePaymentAccount.REQUEST:
      return {
        ...state,
        loading: true
      }
    case merchantActionTypes.merchantSignout.REQUEST:
      return {
        ...state,
        loading: true
      }
    case merchantActionTypes.deletePaymentAccount.SUCCESS:
      return {
        ...state,
        loading: false

      }
    case merchantActionTypes.deletePaymentAccount.FAILURE:
      return {
        ...state,
        loading: false
      }
    case merchantActionTypes.getAccountDetails.REQUEST:
      return {
        ...state,
        loading: true
      }
    case merchantActionTypes.getAccountDetails.SUCCESS:
      return {
        ...state,
        loading: false,
        accountDetails: payload

      }
    case merchantActionTypes.getAccountDetails.FAILURE:
      return {
        ...state,
        loading: false,
        accountDetails: null
      }

    case merchantActionTypes.merchantSignout.SUCCESS:
      return {
        ...state,
        loading: false,
        loginUser: null,
        authToken: null,
      }
    case merchantActionTypes.merchantSignout.FAILURE:
      return {
        ...state,
        loading: false,
      }
    case merchantActionTypes.clearStoreAndRedirectToHome.SUCCESS:
      return {
        ...initialState
      }
    case merchantActionTypes.getMerchantProfileDetails.REQUEST:
      return {
        ...state,
        loading: true,

      }
    case merchantActionTypes.getMerchantProfileDetails.SUCCESS:
      return {
        ...state,
        loading: false,
        merchantProfileDetails: payload
      }
    case merchantActionTypes.getMerchantProfileDetails.FAILURE:
      return {
        ...state,
        loading: false,
        merchantProfileDetails: null
      }
    case merchantActionTypes.getAlreadyAddedActivities.REQUEST:
      return {
        ...state,
        loading: true,
        mechantActivitesLoading: true
      }
    case merchantActionTypes.getAlreadyAddedActivities.SUCCESS:
      return {
        ...state,
        loading: false,
        alreadyAddedActivity: payload,
        mechantActivitesLoading: false
      }
    case merchantActionTypes.getAlreadyAddedActivities.FAILURE:
      return {
        ...state,
        loading: false,
        alreadyAddedActivity: null,
        mechantActivitesLoading: false
      }
    case merchantActionTypes.getOutletsByActivityId.REQUEST:
      return {
        ...state,
        loading: true,

      }
    case merchantActionTypes.getOutletsByActivityId.SUCCESS:
      return {
        ...state,
        loading: false,
        activityOutlets: payload
      }
    case merchantActionTypes.getOutletsByActivityId.FAILURE:
      return {
        ...state,
        loading: false,
        activityOutlets: null
      }
    case merchantActionTypes.getMerchantOutletById.REQUEST:
      return {
        ...state,
        loading: true,

      }
    case merchantActionTypes.getMerchantOutletById.SUCCESS:
      return {
        ...state,
        loading: false,
        outletById: payload,
        merchantOutlet: payload
      }
    case merchantActionTypes.getMerchantOutletById.FAILURE:
      return {
        ...state,
        loading: false,
        outletById: null
      }
    case merchantActionTypes.getMerchantActivityDetailsById.REQUEST:
      return {
        ...state,
        loading: true,

      }
    case merchantActionTypes.getMerchantActivityDetailsById.SUCCESS:
      return {
        ...state,
        loading: false,
        activityDetails: payload,
        isNewMerchantActivityAdded: false
      }
    case merchantActionTypes.getMerchantActivityDetailsById.FAILURE:
      return {
        ...state,
        loading: false,
        activityDetails: null
      }
    case merchantActionTypes.getRegularDealsByActivityId.REQUEST:
      return {
        ...state,
        loading: true,
        regularDealsList: null

      }
    case merchantActionTypes.getRegularDealsByActivityId.SUCCESS:
      return {
        ...state,
        loading: false,
        regularDealsList: payload
      }
    case merchantActionTypes.getRegularDealsByActivityId.FAILURE:
      return {
        ...state,
        loading: false,
        regularDealsList: null
      }
    case merchantActionTypes.clearActivityDetails.SUCCESS:
      return {
        ...state,
        loading: false,
        activityDetails: null,
        outletDetailById: null,
        parentActivityDetailById: null,
        activityOutlets: null,
        getMerchantProfileDetails: null
      }
    case merchantActionTypes.clearOutletDetails.SUCCESS:
      return {
        ...state,
        outletById: null,
        outletDetailById: null,
        merchantOutlet:null
      }
    case merchantActionTypes.clearMerchantProfileDetails.SUCCESS:
      return {
        ...state,
        merchantDetails: null
      }
    case merchantActionTypes.clearDealsDetails.SUCCESS:
      return {
        ...state,
        regularDeals: null
      }
    case merchantActionTypes.clearAlreadyAddedActivities.SUCCESS:
      return {
        ...state,
        alreadyAddedActivity: null
      }
    default:
      return state
  }
}