import React from 'react';
import { Row, Col, Card, Form } from 'react-bootstrap';
import { ValidationForm } from 'react-bootstrap4-form-validation';
import { RenderPasswordField, showAlertMsg,RenderEmailField } from '../../components/commonUIComponents';
import { SubmitButton } from '../../components/commonUIComponents';
import { connect } from 'react-redux';
import { updateMerchantPassword, updateMerchantEmail } from './actions/index';
import validator from 'validator';
import validations from '../../validations';
class UpdatePassword extends React.Component {
  constructor(props) {
    super(props);
    

    this.state = {
      currentPassword: '',
      newPassword: '',
      confirmPassword: '',
      password:'',
      currentEmail:'',
      newEmail:''

    }
  }
  
 
  componentDidUpdate(props,prev){
    if(this.props.currentEmail && this.props.currentEmail !== prev.currentEmail){
     this.setState({
       currentEmail:this.props.currentEmail
     })
 
    }
  }

  handleSubmit = (event, formData) => {
    event.preventDefault();
    if (!this.props.loading) {
      let postData = { ...formData };
      if(postData.newPassword !== postData.confirmPassword){
        showAlertMsg('New password and confirm password are not matched');
      }else{
        this.props.updateMerchantPassword( postData);
      }
      
    }
  }
  handleMailSubmit = (event, formData) => {
    event.preventDefault();
    if (!this.props.loading) {
      let postData = { ...formData };
      postData.email = this.state.currentEmail;
      if(postData.newPassword !== postData.confirmPassword){
        showAlertMsg('New password and confirm password are not matched');
      }else{
        this.props.updateMerchantEmail( postData);
      }
      
    }
  }
  
 
  render() {


    return (

        
     
      <Row>
      
        <Col sm="6">
        
      <ValidationForm onSubmit={this.handleMailSubmit}>
          <Card>
            <Card.Header>
              <Card.Title as="h5">Email </Card.Title>
            </Card.Header>
            <Card.Body>
            <Row>
              <Col>
                <Row>
                  <Col md="12">
                    <Form.Group>
                      <Form.Label>Current Email</Form.Label>
                      <RenderEmailField
                        name="currentEmail"
                        value={this.state.currentEmail}
                        disabled={true}
                      />
                    </Form.Group>
                  </Col>
                </Row>
                <Row>
                  <Col md="12">
                    <Form.Group>
                      <Form.Label>New Email</Form.Label>
                      <RenderEmailField
                        placeholder="Enter new Email"
                        name="newEmail"
                        validator={validator.isEmail}
                        errorMessage={{ validator: 'Invalid email' }}
                        value={this.state.newEmail || ''}
                        onChange={(e) => {this.setState({newEmail:e.target.value})}}
                      />
                    </Form.Group>
                  </Col>
                </Row>
                <Row>
                  <Col md="12">
                    <Form.Group>
                      <Form.Label>Verify current password</Form.Label>
                      <RenderPasswordField
                        placeholder="Enter Current Password"
                        name="password"
                        validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                        errorMessage={{ validator: validations.required }}
                        value={this.state.password || ''}
                        minLength={6}
                        onChange={(e) => {this.setState({password:e.target.value})}}
                      />
                    </Form.Group>
                  </Col>
                </Row>
              </Col>
            </Row>
            </Card.Body>
          </Card>
        
          <Row>
          <Col md="6">
            <Row>
              <Col>
                <Form.Group>
                  <SubmitButton disabled={this.props.loading} className="mt-10">
                    Update Email
                  </SubmitButton>
                </Form.Group>
              </Col>
            </Row>
          </Col>
    
        
        </Row>
          </ValidationForm>
        </Col>
        <Col sm="6">
        <ValidationForm onSubmit={this.handleSubmit}>
        <Card>
          <Card.Header>
            <Card.Title as="h5">Password </Card.Title>
          </Card.Header>
          <Card.Body>
          <Row>
            <Col>
                    <Row>
                      <Col md="12">
                        <Form.Group>
                          <Form.Label>Current password</Form.Label>
                          <RenderPasswordField
                            placeholder="Enter Current Password"
                            name="currentPassword"
                            validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                            errorMessage={{ validator: validations.required }}
                            value={this.state.currentPassword || ''}
                            minLength={6}
                            onChange={(e) => {this.setState({currentPassword:e.target.value})}}
                          />
                        </Form.Group>
                      </Col>
                    </Row>
                    <Row>
                      <Col md="12">
                        <Form.Group>
                          <Form.Label>New password</Form.Label>
                          <RenderPasswordField
                            placeholder="Enter new password"
                            name="newPassword"
                            validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                            errorMessage={{ validator: validations.required }}
                            minLength={6}
                            value={this.state.newPassword || ''}
                            onChange={(e) => {this.setState({newPassword:e.target.value})}}
                          />
                        </Form.Group>
                      </Col>
                    </Row>
                    <Row>
                      <Col md="12">
                        <Form.Group>
                          <Form.Label>Confirm password</Form.Label>
                          <RenderPasswordField
                            placeholder="Enter Confirm password"
                            name="confirmPassword"
                            validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                            errorMessage={{ validator: validations.required }}
                            value={this.state.confirmPassword || ''}
                            minLength={6}
                            onChange={(e) => {this.setState({confirmPassword:e.target.value})}}
                          />
                        </Form.Group>
                      </Col>
                    </Row>
            </Col>
          </Row>
          </Card.Body>
        </Card>
        <Row>
        <Col md="6">
          <Row>
            <Col>
              <Form.Group>
                <SubmitButton disabled={this.props.loading} className="mt-10">
                  Update Password
                </SubmitButton>
              </Form.Group>
            </Col>
          </Row>
        </Col>
  
      
      </Row>
        </ValidationForm>
      </Col>

        
                  
      </Row>
    );
  }
}
const mapStateToProps = (state) => {
  console.log(state.merchant.loginUser)
  return {
    loading:state.merchant.loading,
    currentEmail:state.merchant.loginUser && state.merchant.loginUser && state.merchant.loginUser.merchantSignUpEmail,
    initialValues: {
      currentPassword:'',
      newPassword: '',
      newEmail:'',
      currentEmail:'',
      confirmPassword:'',
      saveCredentials: false,
    }
  }
 
}

export default connect(mapStateToProps, {updateMerchantPassword, updateMerchantEmail })(UpdatePassword);
