import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Row, Col, Card, Form } from 'react-bootstrap';
import { ValidationForm } from 'react-bootstrap4-form-validation';
import validator from 'validator';
import RenderDataTable from '../../../components/dataTable'

import { RenderTextField, RenderPhoneField, RenderEmailField, RenderBootstrapSingleSelect } from '../../../components/commonUIComponents';
import Datetime from 'react-datetime';
import validations from '../../../validations';



class PatnerDetails extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isActive: true,
            activeModaltext: "Inactive",
            showModal: false,
            selectedOutlet: null
        }
    }
    handleSubmitTandC = (event) => {
        event.preventDefault();

    }
    handleSubmitOverview = (event) => {
        event.preventDefault();

    }
    handleSubmitDeals = (event) => {
        event.preventDefault();

    }
    toggleHandler = (event) => {
        event.preventDefault();
        this.setState({
            isActive: !this.state.isActive,
            showModal: !this.state.showModal
        }, () => {
            if (this.state.isActive === true) {
                this.setState({
                    activeModaltext: "Inactive"
                })
            } else {
                this.setState({
                    activeModaltext: "Active"
                })
            }
        })
    };

    openModal = () => {
        this.setState({
            showModal: true
        })
    }

    modalClosed = () => {
        this.setState({
            showModal: false
        })
    }
    changeActivity = () => {
        this.setState({
            selectedOutlet: null
        });
    }
    changeOutlet = (input) => {
        this.setState({
            selectedOutlet: input
        })

    }
    handleSubmit = (event) => { event.preventDefault() }
    render() {
        const columns = [
            {
                name: "Firstname",
                selector: "fname",
                sortable: true,
                width: "20%"
            },
            {
                name: "Lastname",
                selector: "lname",
                sortable: true,
                width: "20%"

            },
            {
                name: "Date Linked",
                selector: "datelinked",
                sortable: false

            },
            {
                name: "Date Delinked",
                selector: "datedelinked",
                sortable: false

            },
            {
                name: "Initiated Delinked",
                selector: "initiatedelink",
                sortable: false,


            },
            {
                name: "Diary Entries",
                selector: "diaryentries",
                sortable: true,

            }
        ];


        return (
            <>
                <Card className="box-border">
                    <Card.Header>
                        <Card.Title as="h5"> Personal Overview</Card.Title>
                    </Card.Header>
                    <Card.Body >
                        <ValidationForm onSubmit={this.handleSubmit}>
                            <Row>
                                <Col md={6}>
                                    <Form.Group>
                                        <Form.Label>Name</Form.Label>
                                        <Row>
                                            <Col md={6}>
                                                <RenderTextField
                                                    name="firstname"
                                                    placeholder="First Name"
                                                    maxLength={18}
                                                    errorMessage={{ validator: validations.required }}
                                                    validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                                />
                                            </Col>
                                            <Col md={6}>
                                                <RenderTextField
                                                    name="lastname"
                                                    placeholder="Last Name"
                                                    maxLength={18}
                                                    errorMessage={{ validator: validations.required }}
                                                    validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                                />
                                            </Col>
                                        </Row>

                                    </Form.Group>
                                </Col>
                                <Col md="6">
                                    <Form.Group>
                                        <Form.Label>Country</Form.Label>
                                        <RenderTextField
                                            name="country"
                                            placeholder="Country"
                                            maxLength={18}
                                            errorMessage={{ validator: validations.required }}
                                            validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                        />
                                    </Form.Group>
                                </Col>
                                <Col md="6">
                                    <Form.Group>
                                        <Form.Label>Mobile Number</Form.Label>
                                        <RenderPhoneField
                                            name="contactNumberForSparks"
                                            validator={(value) => ((/[1-9]{3}-[0-9]{4}-[0-9]{1}/).test(value))}
                                            errorMessage={{ validator: "Invalid contact number" }}

                                        />
                                    </Form.Group>
                                </Col>
                                <Col md="6">
                                    <Form.Group>
                                        <Form.Label>DOB</Form.Label>
                                        <Datetime timeFormat={false} dateFormat={"DD MMM YYYY"} inputProps={{ placeholder: 'Select Date' }} />

                                    </Form.Group>
                                </Col>
                                <Col md="6">
                                    <Form.Group>
                                        <Form.Label>Email</Form.Label>
                                        <RenderEmailField
                                            name="contactEmailForSparks"
                                            validator={validator.isEmail}
                                            errorMessage={{ validator: 'Invalid email' }}
                                        />
                                    </Form.Group>
                                </Col>
                                <Col md="6">
                                    <Form.Group>
                                        <Form.Label>Gender</Form.Label>
                                        <RenderBootstrapSingleSelect
                                            name="gender"
                                            options={[
                                                { value: 'male', label: 'Male' },
                                                { value: 'female', label: 'Female' },
                                                { value: 'others', label: 'Others' },
                                            ]}
                                        />
                                    </Form.Group>
                                </Col>

                                <Col md="6">
                                    <Form.Group>
                                        <Form.Label>Status</Form.Label>
                                        <RenderBootstrapSingleSelect
                                            name="status"
                                            options={[
                                                { value: 'active', label: 'Active' },
                                                { value: 'blocked', label: 'Blocked' },
                                            ]}
                                        />
                                    </Form.Group>
                                </Col>
                                <Col md="6">
                                    <Form.Group>
                                        <Form.Label>Created Date</Form.Label>
                                        <Datetime timeFormat={false} dateFormat={"DD MMM YYYY"} inputProps={{ placeholder: 'Select Date' }} />

                                    </Form.Group>
                                </Col>
                                <Col md="6">
                                    <Form.Group>
                                        <Form.Label>Social Account</Form.Label>
                                        {/* <Datetime timeFormat={false} dateFormat={"DD MMM YYYY"} inputProps={{ placeholder: 'Select Date' }} /> */}
                                        <Row>
                                            <Col md="3" className="font-30" >
                                                <i className="feather icon-twitter"></i>
                                            </Col>
                                            <Col md="3" className="font-30">
                                                <i className="feather icon-facebook"></i>
                                            </Col>
                                            <Col md="3" className="font-30">
                                                <i className="feather icon-instagram"></i>
                                            </Col>
                                            <Col md="3" className="font-30">
                                                <i className="feather icon-mail"></i>
                                            </Col>
                                        </Row>
                                    </Form.Group>
                                </Col>
                                <Col md="6">
                                    <Form.Group>
                                        <Form.Label>Last Session</Form.Label>
                                        <Datetime timeFormat={false} dateFormat={"DD MMM YYYY"} inputProps={{ placeholder: 'Select Date' }} />
                                    </Form.Group>
                                </Col>
                            </Row>
                            <Row>
                                {/* <Col md="6">
                    <SubmitButton className="btn btn-primary shadow-2 mt-4">
                      Save
                          </SubmitButton>
                  </Col> */}
                            </Row>
                        </ValidationForm>
                    </Card.Body>
                </Card>
                <Card className="box-border">
                    <Card.Header>
                        <Card.Title as="h5">Previous Patners</Card.Title>
                    </Card.Header>
                    <Card.Body>
                        <RenderDataTable
                            onRowClicked={this.navigateEditActivity}
                            columns={columns}
                            noPagination="false"
                            data={this.props.patnerList || []}
                            defaultSortField="name"
                        />
                    </Card.Body>
                </Card>
            </>
        )
    }
}
const mapStateToProps = () => {
    return {
        patnerList: [
            { fname: "Aishhhhha", lname: 'kaur', datelinked: '29-12-2013', datedelinked: '29-12-2013', initiatedelink: 'Yes', diaryentries: 'Archieved' },
            { fname: "Simar", lname: 'Singh', datelinked: '29-12-2013', datedelinked: '29-12-2013', initiatedelink: 'Yes', diaryentries: 'Archieved' },
            { fname: "John", lname: 'Pauk', datelinked: '29-12-2013', datedelinked: '29-12-2013', initiatedelink: 'No', diaryentries: 'Deleted' },
            { fname: "Smith", lname: 'Paul', datelinked: '29-12-2013', datedelinked: '29-12-2013', initiatedelink: 'Yes', diaryentries: 'Archieved' },

        ]
    };
}


export default connect(mapStateToProps, null)(PatnerDetails)
