import React from 'react';
import { NavLink } from 'react-router-dom';
import Breadcrumb from "../../../App/layout/AdminLayout/Breadcrumb";
import logo from '../../../assets/images/sparks-logo.svg';

import { forgotPassword } from './actions/index';
import { RenderEmailField } from '../../components/commonUIComponents';
import validator from 'validator';
import { ValidationForm } from 'react-bootstrap4-form-validation';
import { connect } from 'react-redux'
import { SubmitButton } from '../../components/commonUIComponents';
class ResetPassword extends React.Component {
  
  handleSubmit = (event, formValues) => {
    console.log(formValues)
    event.preventDefault();
    this.props.forgotPassword(formValues);
  }
  render() {

    return (
      <>
        <Breadcrumb />
        <div className="auth-wrapper">
          <div className="auth-content subscribe">
            <div className="card">
              <div className="row no-gutters">
                <div className="col-md-4 col-lg-6 d-md-flex d-lg-flex theme-bg-new align-items-center justify-content-center">
                  <img src={logo} alt="lock images" className="img-fluid" />
                </div>
                <div className="col-md-8 col-lg-6">
                  <div className="card-body text-center">
                    <div className="row justify-content-center">
                      <div className="col-sm-10">
                        <ValidationForm onSubmit={this.handleSubmit}>
                          <div className="mb-4">
                            <i className="feather icon-mail auth-icon" />
                          </div>
                          <h3 className="mb-4">Reset Password</h3>
                          <div className="input-group mb-3">
                            <RenderEmailField
                              name="email"
                              placeholder="Email"
                              validator={validator.isEmail}
                              errorMessage={{ validator: "Please enter a valid email" }}

                            />
                          </div>
                          <SubmitButton disabled={this.props.loading}>Reset Password</SubmitButton>
                        </ValidationForm>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    
    loading:state.merchant.loading,
    initialValues: {
      email: '',
      saveCredentials: false,
    }
  }
}
export default connect(mapStateToProps, { forgotPassword })(ResetPassword);