import React from 'react';
import { InputGroup, DropdownButton, Dropdown } from 'react-bootstrap';
import MaskedInput from 'react-text-mask';
import { BaseFormControl } from 'react-bootstrap4-form-validation';
import { countries } from 'country-data';
class MaskedPhoneFieldWithValidation extends BaseFormControl {
    constructor(props) {
      super(props);
      this.inputRef = React.createRef();
      let countryOptions = countries.all.reduce((country, item) => {
  
        item.countryCallingCodes.map((subItem) => country.push({ name: item.name, code: subItem }))
        country.sort((a, b) => {
          if (a.name > b.name) {
            return 1
          } else if (a.name <= b.name) {
            return -1
          }
          return 0;
        })
        return country;
      }, []);
      let selectedCode = countryOptions && countryOptions.length && countryOptions[0].code;
      
      if (countryOptions.findIndex((country) => country.code === '+65') >= 0) {
        selectedCode = countryOptions[countryOptions.findIndex((country) => country.code === '+65')].code
      }
  
      this.state = {
        selectedCode: selectedCode,
        countryOptions: countryOptions,
      }
    }
    componentDidMount() {
      if (this.props.getCountryCode) {
        this.props.getCountryCode(this.state.selectedCode)
      }
  
  
    }
    componentDidUpdate(prevProps) {
  
      if (this.props.initialCountryCode && this.props.initialCountryCode !== prevProps.initialCountryCode) {
        this.setState({
          selectedCode: this.props.initialCountryCode
        })
      }
    }
  
    handleOnChange = (event, code) => {
  
      this.setState({
        selectedCode: code
      }, () => {
        if (this.props.getCountryCode) {
          this.props.getCountryCode(this.state.selectedCode)
        }
      })
    }
    //Need to implement this method to get the underying input reference
    getInputRef() {
  
      return this.inputRef.current.inputElement;
    }
  
    handleChange = (e) => {
  
      //Call this.checkError to validate the input
      this.checkError();
      //Pass along the event object for controlled component
      if (this.props.onChange) this.props.onChange(e);
    }
  
    render() {
      let filteredProps = { ...this.filterProps() };
      delete filteredProps.getCountryCode;
      delete filteredProps.initialCountryCode;
      return (
        <React.Fragment>
          <InputGroup className="mb-3 phone-field">
            <InputGroup.Prepend>
              <DropdownButton className="green-bg" title={this.state.selectedCode} id="input-group-dropdown-1">
                {this.state.countryOptions.map((opt, index) => <Dropdown.Item key={index} onClick={(event) => this.handleOnChange(event, opt.code)}>{<div><span>{opt.name} </span><span className="float-right">{opt.code}</span></div>}</Dropdown.Item>)}
  
              </DropdownButton>
            </InputGroup.Prepend>
            <MaskedInput ref={this.inputRef} {...filteredProps} onChange={this.handleChange} />
            {this.displayErrorMessage()}
            {this.displaySuccessMessage()}
          </InputGroup>
  
  
          {/* <MaskedInput ref={this.inputRef} {...this.filterProps()} onChange={this.handleChange} /> */}
  
        </React.Fragment>
      )
    }
  }
  export default MaskedPhoneFieldWithValidation