import React, { Component } from 'react'
import { ValidationForm } from 'react-bootstrap4-form-validation';
import { RenderBootstrapSingleSelect,RenderPrefixedPriceField,RenderAppendTextField, SubmitButton,SuccessButton, DeleteButton,RenderTextField,RenderNumberField, RenderTextArea, RenderSearchableMultiSelect } from './../../components/commonUIComponents';
import AnimatedModal from './../../components/AnimatedModal';
import { Row, Col, Form, Card, Tabs, Tab, } from 'react-bootstrap';
import RenderDataTable from '../../components/dataTable';
import DEMO from '../../../store/constant';
import { connect } from 'react-redux';
import Datetime from 'react-datetime';
import moment from 'moment';
import validator from 'validator';
import validations from '../../validations';
import { getAlreadyAddedActivities,getOutletsByActivityId,createPramotionRequest,PramotionsDetails, } from './actions/';

class Pramotions extends Component {
    constructor(props) {
        super(props);

       

        this.state = {
            "merchantId":'',
            "activityId":'',
            "merchantActivityId":'',
            "startDate":moment().format(),
            "endDate":'',
            "notes":'',
            "outletId":[],
            title:'',
            selectedOutletName: [],
            description:'',
            pramotion_ticket:'',
            startMoment:'',
            originalPrice:'',
            discountedPercentage: '',
            discountedPrice : '',
            discountAmount : '',
            terms:[
                    {point:'Pramotion can only be claimed unitl start date & end date'},
                    {point:'Reservation is subjected to availability. please check availability with merchant before going down for activity'},
                    {point:'Points are non refundable once user unlock the pramotions'}
                  ],

            sortColumn: 'createdAt',
            sortDirection: 'DESC',
            recordLimit: 10,
            recordOffset: 0,
            search:'',
            

        }
    }
    loadData = () => {
        let searchObj = 
        { 
          limit: this.state.recordLimit,
          offset: this.state.recordOffset,
          orderBy: this.state.sortColumn,
          order: this.state.sortDirection,
          search:this.state.search
        }
    
        this.props.PramotionsDetails(searchObj);
      }
    componentDidMount() {
        console.log(this.props.merchantLoginId)
        if (this.props.merchantLoginId) {
          this.getAllActivities(this.props.merchantLoginId)
          this.loadData();
        }
    }
    handleSorting = (column, sortDirection, event) => {
        this.setState({
          sortColumn: column.selector,
          sortDirection: sortDirection.toUpperCase()
        }, () => this.loadData())
    
    
      }
      handleSearch = (text)=>{
        this.setState({
          search:text
        }, () => this.loadData())
      }
      handlePageChange = (pageNumber) => {
        this.setState((prevState) => ({
          recordOffset: (pageNumber - 1) * prevState.recordLimit,
        }), () => this.loadData())
    
      }
      navigateEditActivity = (row) => {
        console.log(row)
      }
      onChangeRecordsLimit = (event) => {
        this.setState({
          recordLimit: event.target.value && parseInt(event.target.value, 10)
        }, () => this.loadData())
      }
       
    getAllActivities = (id) => {
        this.props.getAlreadyAddedActivities(this.props.merchantLoginId)
    
      }
    
    openStatusChangeModal = (event) => {
        this.setState({
            showActivityChangeModal: true,

        })
        this.handleSubmit()
        event.preventDefault();
    }
    modalClosed = () => {
        this.setState({
            showActivityChangeModal: false,
        })
    }
    handleChange = (event) => {
        console.log(event.target.name,event.target.value)
        
        this.setState({
            [event.target.name]: event.target.value
          })
        
            if (event.target.name === 'discountedPercentage') {
            this.setState(
                {
                    discountAmount : ((parseFloat(event.target.value) / 100) * this.state.originalPrice).toFixed(2),
                    discountedPrice : (this.state.originalPrice - ((parseFloat(event.target.value) / 100) * this.state.originalPrice)).toFixed(2),
                    
                }
            )
            
            } else if (event.target.name === 'discountedPrice') {
            this.setState(
                {
                    discountedPercentage : (((this.state.originalPrice - parseFloat(event.target.value)) / this.state.originalPrice) * 100).toFixed(2),
                    discountAmount :(this.state.originalPrice - parseFloat(event.target.value)).toFixed(2),
                    
                },()=>{
                    console.log(this.state.discountedPercentage,this.state.discountAmount)
                }
            )
            
            } else if (event.target.name === 'discountAmount') {
            this.setState(
                {
                    discountedPercentage : (parseFloat(event.target.value) / this.state.originalPrice * 100).toFixed(2),
                    discountedPrice :(this.state.originalPrice - parseFloat(event.target.value)).toFixed(2),
                    
                }
            )
            
        }
        
         
        if (event.target.name === 'originalPrice' && !event.target.value) {
          this.setState({
            discountedPercentage: '',
            discountedPrice : '',
            discountAmount : '',
          })
        }
      }
    handleStartDateChange = (e) =>{
        this.setState({
            startDate:e._d,
            endDate:moment(e._d).add(14, 'days'),
            startMoment:e.isAfter(moment().add(14, 'days'))
        })
    }
    handleEndDateChange = (e) =>{
        this.setState({
            endDate:e._d
        })
    }
    handleActivityChange =  (e) => {
        console.log(e)
        this.setState({
            activityId:e.activityId,
            merchantActivityId:e.value
        })
        this.getActivityOutlets(e.value);
    }
    getActivityOutlets = (id) => {
        this.props.getOutletsByActivityId(id, this.props.merchantLoginId)
    }
    handleOutletOnChange = selectedOutletName => {
        this.setState({ selectedOutletName: selectedOutletName && selectedOutletName.length ? selectedOutletName : [] }
        )
      }
    handleSubmit = (ev, formData) => {
        ev.preventDefault();
    
        let postObj = {
            "adminId":0,
            "merchantId":this.props.merchantLoginId,
            // "activityId":this.state.activityId,
            "merchantActivityId":this.state.merchantActivityId,
            "startDate":moment(this.state.startDate).format('YYYY-MM-DD H:mm:ss'),
            "endDate":moment(this.state.endDate).format('YYYY-MM-DD H:mm:ss'),   
            "outletId":this.state.selectedOutletName,         
            "title":this.state.title,
            "description":this.state.description,
            "originalPrice":this.state.originalPrice,
            "discountedPrice":this.state.discountedPrice,
            "discountedPercentage":this.state.discountedPercentage,
            "discountAmount":this.state.discountAmount,
            "promotionalTerms":this.state.terms,
            "quantity":this.state.pramotion_ticket,
            "quantityPaidFor":0,
        };
        // create new activity
        this.props.createPramotionRequest(postObj);
    
      }
    validationStartDate = (currentDate) => {
        // console.log(currentDate)
        return currentDate.isAfter(moment().add(7, 'days'));
    };
    validationEndDate = (currentDate) => {
        
        return currentDate.isAfter(moment(this.state.startDate).add(13, 'days'));
        
    };

    handleTermsOnChange = (input,index) => {
        let term = this.state.terms;
        term[index].point = input.target.value
        this.setState({
            terms:term
        })
    }
    handleAddTerms = () => {
        let term = this.state.terms;
        term.push({point:''})
        this.setState({
            terms:term
        })
    }
    handleRemoveConfirm = (event, index) => {
    
        event.preventDefault();
        let term = this.state.terms;
        term.splice(index, 1);
        this.setState({
            terms:term
        })
      }

    render() {
        const columns = [
            {
              name: "Pramotion Id",
              selector: "id",
              sortable: true,
            },
            {
              name: "Activity",
              selector: "activity",
              sortable: true,
            },
            {
              name: "Date Submitted",
              selector: "createdAt",
              sortable: true,
              format: (row) => row.createdAt?moment(row.createdAt).format('DD MMM YYYY'):'',
            },
            {
              name: "Start Date",
              selector: "startDate",
              sortable: false,
              format: (row) => row.startDate?moment(row.startDate).format('DD MMM YYYY'):'',
            },
            {
              name: "End Date",
              selector: "endDate",
              sortable: false,
              format: (row) => row.endDate?moment(row.endDate).format('DD MMM YYYY'):'',
            },
            {
                name: "Original price",
                selector: "oprice",
                sortable: false,
            },
            {
                name: "Discounted price",
                selector: "dprice",
                sortable: false,
            },
            {
              name: "Status",
              selector: "status",
              format: (row) => {
                return <a href={DEMO.BLANK_LINK} className={`label d-block ${row.status === 1 ? "green-bg" : 'red-bg'} f-12 text-white`}>{row.status===1 ? "Active" : "Inactive"}</a>
              },
              sortable: true,
            },
            {
              name: "Claimed Tickets",
              selector: "claimedTickets",
              sortable: false, 
              format: (row) => {
                return row.status === 1?`${row.claimedTickets}/${row.totalTicket}`:'N.A.'
              },
            },
          ];

        return (
         
                
                    <Row>
                        <Col>
                        <Tabs defaultActiveKey="request">

                            <Tab eventKey="request" title="Create Pramotions" >
                            <Row>
                                <Col>
                                    {/* <p>Raw denim you probably haven't heard of them jean shorts Austin. Nesciunt tofu stumptown aliqua, retro synth master cleanse. Mustache cliche tempor, williamsburg carles vegan helvetica. Reprehenderit butcher retro keffiyeh dreamcatcher synth. Cosby sweater eu banh mi, qui irure terry richardson ex squid. Aliquip placeat salvia cillum iphone. Seitan aliquip quis cardigan american apparel, butcher voluptate nisi qui.</p> */}
                                    <ValidationForm onSubmit={this.handleSubmit}>
                                        <Row>
                                            <Col md={6}>
                                                <Form.Group>
                                                <Form.Label>Title</Form.Label>
                                                <RenderTextField
                                                    name="title"
                                                    placeholder=""
                                                    maxLength={25}
                                                    onChange={this.handleChange}
                                                    value={this.state.title || ''}
                                                    validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                                    errorMessage={{ validator: validations.required }}
                                                />
                                                </Form.Group>
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col md="6">
                                                <Form.Group>
                                                    <Form.Label>Select Activity</Form.Label>
                                                    <RenderBootstrapSingleSelect
                                                        options={this.props.alreadyAddedActivity}
                                                        placeholder={"Please Select Activity"}
                                                        name="alreadyAddedActivities"
                                                        value={this.state.selectedActivity}
                                                        required
                                                        errorMessage={{ validator: validations.required }}
                                                        onChange={this.handleActivityChange} />
                                                </Form.Group>
                                            </Col>
                                            <Col md={6}>
                                            <Form.Group>
                                                <Form.Label>Outlet Name</Form.Label>
                                                <RenderSearchableMultiSelect
                                                name="outletName"
                                                value={this.state.selectedOutletName}  
                                                onChange={this.handleOutletOnChange}
                                                placeholder="Select outlet name .."
                                                required
                                                options={this.props.activityOutlets}
                                                />
                                            </Form.Group>
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col md={6}>
                                                <Form.Group>
                                                <Form.Label>Pramotion Description</Form.Label>
                                                <RenderTextField
                                                    name="description"
                                                    placeholder=""
                                                    maxLength={25}
                                                    onChange={this.handleChange}
                                                    value={this.state.description || ''}
                                                    validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                                    errorMessage={{ validator: validations.required }}
                                                />
                                                </Form.Group>
                                            </Col>
                                            <Col md={6}>
                                                <Form.Label>
                                                    Number of Pramotion Tickets 
                                                </Form.Label>
                                                <span data-toggle="tooltip" className="InfoTooltip" data-placement="top" title="Number of couples that can redeem the pramotion is equivalent to the number of pramotinal tickets divided by 2">
                                                    <i class="feather icon-alert-circle"></i>
                                                </span>
                                                <RenderNumberField
                                                name="pramotion_ticket"
                                                placeholder=""
                                                validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                                errorMessage={{ validator: 'Invalid hours' }}
                                                onChange={this.handleChange}
                                                value={this.state.pramotion_ticket}
                                                />
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col md={6}>
                                                <Form.Group>
                                                <Form.Label>Original Price</Form.Label>
                                                <RenderPrefixedPriceField
                                                    name="originalPrice"
                                                    placeholder="Original Price"
                                                    required
                                                    prefix="SGD"
                                                    validator={validator.isFloat}
                                                    onChange={this.handleChange}
                                                    value={this.state.originalPrice}
                                                    errorMessage={{ validator: 'Invalid Price' }}
                                                />
                                                </Form.Group>
                                            </Col>
                                            <Col md="6">
                                                <Form.Group>
                                                    <Form.Label>Start Date</Form.Label>
                                                    <Datetime name="startDate" 
                                                     isValidDate={this.validationStartDate}
                                                     errorMessage={{ validator: validations.required }}
                                                     timeFormat={false} onChange={this.handleStartDateChange}  dateFormat={"DD MMM YYYY"} inputProps={{ placeholder: 'Select Date' }} />
                                                </Form.Group>
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col md="6">
                                                <Form.Group>
                                                <Form.Label>Discounted Price</Form.Label>
                                                <RenderPrefixedPriceField
                                                    name="discountedPrice"
                                                    placeholder="Discounted Price"
                                                    required
                                                    prefix="SGD"
                                                    disabled={this.state.originalPrice ? false : true}
                                                    validator={validator.isFloat}
                                                    onChange={this.handleChange}
                                                    value={this.state.discountedPrice}
                                                    errorMessage={{ validator: 'Invalid price' }}
                                                />
                                                </Form.Group>
                                            </Col>
                                            <Col md="6">
                                                <Form.Group>
                                                    <Form.Label>End Date</Form.Label>
                                                    <Datetime name="endDate"                                                    
                                                    validator={(str) => !validator.isEmpty(str)}
                                                    isValidDate={this.validationEndDate}
                                                    errorMessage={{ validator: validations.required }}
                                                    timeFormat={false} onChange={this.handleEndDateChange}
                                                    dateFormat={"DD MMM YYYY"}
                                                    inputProps={{ placeholder: 'Select Date' }}
                                                    value={this.state.endDate} />
                                                </Form.Group>
                                            </Col>
                                        </Row>
                                        <Row>
                                            
                                            <Col md="6">
                                                <Form.Group>
                                                <Form.Label>Discounted Percentage</Form.Label>
                                                <RenderAppendTextField
                                                    name="discountedPercentage"
                                                    placeholder="Discounted %"
                                                    validator={(str) => validator.isFloat(str) && parseFloat(str, 10) <= 100}
                                                    errorMessage={{ validator: 'Invalid percentage' }}
                                                    onChange={this.handleChange}
                                                    disabled={this.state.originalPrice ? false : true}
                                                    value={this.state.discountedPercentage}
                                                    appendText="%" />
                                                </Form.Group>
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col md="6">
                                                <Form.Group>
                                                <Form.Label>Discounted Amount</Form.Label>
                                                <RenderPrefixedPriceField
                                                    name="discountAmount"
                                                    placeholder="Discounted Amount"
                                                    validator={validator.isFloat}
                                                    prefix="SGD"
                                                    disabled={this.state.originalPrice ? false : true}
                                                    onChange={this.handleChange}
                                                    value={(this.state.discountAmount) || ''}
                                                    errorMessage={{ validator: 'Invalid price' }}
                                                />
                                                </Form.Group>
                                            </Col>
                                        </Row>
                                        <Row>
                                        
                                            <Col md="12">
                                                
                                                { (this.state.terms).map((term, index) => 
                                                    <Row>
                                                    <Col md="6">
                                                        <Form.Group>
                                                            <Form.Label>Terms point {index+1}</Form.Label>
                                                            <RenderTextField
                                                            name={`term[${index}]`}
                                                            disabled={index > 2 ? false : true}
                                                            placeholder=""
                                                            onChange={(input) => this.handleTermsOnChange(input, index )}
                                                            value={this.state.terms && this.state.terms[index] &&  this.state.terms[index].point}
                                                            validator={(str) => !validator.isEmpty(str, { ignore_whitespace: true })}
                                                            errorMessage={{ validator: validations.required }}
                                                            />
                                                        </Form.Group>
                                                    </Col>
                                                    <Col md={6}>
                                                        <Form.Group>
                                                            {index > 2 ?
                                                            <DeleteButton onClick={(event) => this.handleRemoveConfirm(event, index)}> Remove term</DeleteButton> : null
                                                            }
                                                            
                                                        </Form.Group>
                                                    </Col>
                                                </Row>
                                                )}
                                                
                                                <SuccessButton onClick={this.handleAddTerms}>Add Terms point</SuccessButton> 
                                            </Col>
                                        </Row>
                                        <hr></hr>
                                        <Row>
                                            <Col md="6">
                                                <Row>
                                                    <Col>
                                                        <Form.Group>
                                                            <SubmitButton className="mt-10" >
                                                                Submit
                                                            </SubmitButton>
                                                        </Form.Group>
                                                    </Col>
                                                    <Col md="6">
                                                        <Form.Group>
                                                            <DeleteButton className="mt-10">
                                                                Cancel
                                                            </DeleteButton>
                                                        </Form.Group>
                                                    </Col>
                                                </Row>

                                            </Col>
                                        </Row>
                                    </ValidationForm>
                                </Col>
                            </Row>
                            
                            </Tab>
                            <Tab eventKey="request-list" title="Pramotions Status">

                                <Row>
                                
                                    <Col>
                                        <RenderDataTable
                                        onRowClicked={this.navigateEditActivity}
                                        columns={columns}
                                        data={this.props.pramotionRequestList}
                                        defaultSortField={this.state.sortColumn}
                                        filterTextSearch={this.state.search}
                                        sortServer
                                        totalRecords={this.props.totalRecords}
                                        dataLength ={this.props.pramotionRequestList.length}
                                        recordLimit={this.state.recordLimit}
                                        recordOffset={this.state.recordOffset}
                                        onChangeRecordsLimit={this.onChangeRecordsLimit}
                                        handlePageChange={this.handlePageChange}
                                        onSort={this.handleSorting}
                                        handleSearch={this.handleSearch} 
                                        />
                                    </Col>
                                </Row>
                                </Tab>
                        </Tabs>
                            <AnimatedModal showActivityChangeModal={this.state.showActivityChangeModal} modalClosed={this.modalClosed}>
                                <div className="text-center">
                                    <h4 className="modal-class">Thanks you for your interest !<br></br> Our team will contact you shortly with our pricing structure.</h4>
                                    <SubmitButton className="modal-button text-center" onClick={this.modalClosed}>OK</SubmitButton>

                                </div>

                            </AnimatedModal>

                        </Col>
                        </Row>
            

        )
    }

}

const mapStateToProps = (state) => {
    console.log(state)
    
  let totalRecords =0;
    let pramotionRequestList = ((state.merchant.pramotionRequestList && state.merchant.pramotionRequestList.data.data) || []).map((item) => ({
        id:item.id,
        activity:item.activityName,
        createdAt:item.createdAt,
        startDate:item.startDate,
        endDate:item.endDate,
        oprice:item.originalPrice,
        dprice:item.discountedPrice,
        status:item.status,
        claimedTickets:item.quantityPaidFor,
        totalTicket:item.quantity

      }))
    let alreadyAddedActivity = ((state.merchant.alreadyAddedActivity && state.merchant.alreadyAddedActivity.data) || []).map((item) => ({
        label: item.activityName,
        value: item.id,
        activityId:item.activityId,
        outletId:item.outletId?item.outletId:[]

      }))
    
    totalRecords = state.merchant.pramotionRequestList && state.merchant.pramotionRequestList.data.totalCount;

    let activityOutlets = ((state.admin.activityOutlets && state.admin.activityOutlets.data ) || []).map((item) => ({
        label: item.outletName,
        value: item.id
      }))
    return {
        alreadyAddedActivity:alreadyAddedActivity,
        totalRecords: totalRecords,
        activityOutlets:activityOutlets,
        pramotionRequestList:pramotionRequestList,
        merchantLoginId: state.merchant.loginUser && state.merchant.loginUser.id,
        activityList: [
            { value: 'a1', label: 'A1' },
            { value: 'a2', label: 'A2' },
            { value: 'a3', label: 'A3' }
        ]
    };

}

export default connect(mapStateToProps, {
    getAlreadyAddedActivities,
    getOutletsByActivityId, 
    createPramotionRequest,
    PramotionsDetails
})(Pramotions)
