import React, { Component } from 'react';
import { Switch, Route } from 'react-router-dom';

import { connect } from 'react-redux'
import '../../node_modules/font-awesome/scss/font-awesome.scss';

import Loader from './layout/Loader'
import ScrollToTop from './layout/ScrollToTop';
import routes from "../route";
import PrivateRouter from '../routers/privateRouter';
import PublicRouter from '../routers/publicRouter';
import merchantLayout from './containers/merchant/layout/merchantLayout/index'
import Error404 from '../Demo/Maintenance/Error';
import AdminLayout from './layout/AdminLayout';

import { StripeProvider, Elements } from 'react-stripe-elements';

const apiKey = "pk_test_8xwh88ubiWno3xfM0BQGNo9E00McTtH4BD"

class App extends Component {
  render() {
    const menu = routes.map((route, index) => {
      return (route.component) ? (
        
        route.isPrivate ?
          (route.isAdmin === true?
            <PrivateRouter key={index} index={index} {...route} loginUser={this.props.admin} />:
            <PrivateRouter key={index} index={index} {...route} loginUser={this.props.merchant} />
          )
          
          :
          (
            route.path === '/merchant'?
            <PublicRouter key={index} index={index}{...route} role={"merchant"} loginUser={ this.props.merchant} />:
            <PublicRouter key={index} index={index}{...route} role={"admin" } loginUser={this.props.admin } />
            )
      ) : (null);
    });

    return (

            
      <ScrollToTop>



        {(this.props.isAdminLoading || this.props.isMerchantLoading) ? <Loader /> : null}
        < StripeProvider apiKey={apiKey}>
        <Elements>
          <Switch>
            {menu}
            <PrivateRouter path="/admin/dashboard" role="admin" exact={false} component={AdminLayout} loginUser={this.props.admin} />
            <PrivateRouter path="/merchant/dashboard" role="merchant" exact={false} component={merchantLayout} loginUser={this.props.merchant} />
            <Route path="*" component={Error404} />
          </Switch>
          </Elements>
        </StripeProvider>

      </ScrollToTop>

    );
  }
}
const mapStateToProps = (state) => {

  return {
    isAdminLoading: state.admin.loading,
    isMerchantLoading: state.merchant.loading,
    admin: state.admin.loginUser,
    merchant: state.merchant.loginUser
  }
}
export default connect(mapStateToProps, null, null, { pure: false })(App);
