import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Card } from 'react-bootstrap';
import RenderDataTable from '../../../components/dataTable'
import { history } from '../../../../history';



class Diary extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isActive: true,
            activeModaltext: "Inactive",
            showModal: false,
            selectedOutlet: null
        }
    }
    navigateEditDiary = () => {
        history.push('/admin/dashboard/users/diary/123');
    }
    handleSubmitTandC = (event) => {
        event.preventDefault();

    }
    handleSubmitOverview = (event) => {
        event.preventDefault();

    }
    handleSubmitDeals = (event) => {
        event.preventDefault();

    }
    toggleHandler = (event) => {
        event.preventDefault();
        this.setState({
            isActive: !this.state.isActive,
            showModal: !this.state.showModal
        }, () => {
            if (this.state.isActive === true) {
                this.setState({
                    activeModaltext: "Inactive"
                })
            } else {
                this.setState({
                    activeModaltext: "Active"
                })
            }
        })
    };

    openModal = () => {
        this.setState({
            showModal: true
        })
    }

    modalClosed = () => {
        this.setState({
            showModal: false
        })
    }
    changeActivity = () => {
        this.setState({
            selectedOutlet: null
        });
    }
    changeOutlet = (input) => {
        this.setState({
            selectedOutlet: input
        })

    }
    handleSubmit = (event) => { event.preventDefault() }
    render() {
        const columns = [
            {
                name: "Month",
                selector: "month",
                sortable: true,
                width: "15%"
            },
            {
                name: "Letter Earned",
                selector: "letter",
                sortable: true,
                width:"8%"
                

            },
            {
                name: "Activity",
                selector: "activity",
                sortable: true,
                width: "15%"

            },
            {
                name: "Merchant",
                selector: "merchant",
                sortable: true,
                width: "20%"

            },
            {
                name: "Privacy",
                selector: "privacy",
                sortable: true,
                width: "10%"


            },
            {
                name: "Status",
                selector: "status",
                sortable: true,
                width:"23%"

            },
            {
                name: "Points Earned",
                selector: "points",
                sortable: true,

            }
        ];


        return (
            <>
                <Card className="box-border">
                    <Card.Header>
                        <Card.Title as="h5"> Diary Entries</Card.Title>
                    </Card.Header>
                    <Card.Body>
                        <RenderDataTable
                            onRowClicked={this.navigateEditDiary}
                            columns={columns}
                            noPagination="false"
                            data={this.props.diaryEntries || []}
                            defaultSortField="name"
                        />
                    </Card.Body>
                </Card>
            </>
        )
    }
}
const mapStateToProps = () => {
    return {
        diaryEntries: [
            { letter:"F",activity: "Horse Riding", merchant: 'Saddle club', month: '29-12-2013', privacy: <i className="feather icon-users"> Friends</i>, status:  <button type="btn" className="label  f-12 text-white status-btn best-seller">Pending Patner Review</button>, diaryentries: 'Archieved',points:30 },
            { letter:"L",activity: "Archery", merchant: 'Salt and light archery', month: '29-12-2013',privacy:<i className="feather icon-lock"> Private</i>, status:  <button type="btn" className="label  f-12 text-white status-btn best-seller">Published</button>, diaryentries: 'Archieved',points:30 },
            { letter:"M",activity: "Kite Flying", merchant: 'Mega Adventure', month: '29-12-2013',privacy: <i className="feather icon-users"> Friends</i>, status:  <button type="btn" className="label  f-12 text-white status-btn best-seller">Hidden</button>, diaryentries: 'Deleted',points:10 },
            { letter:"K",activity: "Base Ball", merchant: 'Base factory', month: '29-12-2013', privacy: <i className="feather icon-users"> Friends</i>, status:  <button type="btn" className="label  f-12 text-white status-btn best-seller">Deleted</button>, diaryentries: 'Archieved',points:50 },

        ]
    };
}


export default connect(mapStateToProps, null)(Diary)
