import React from 'react'
import { Col, Form } from 'react-bootstrap';
// import { SubmitButton } from '../../../components/commonUIComponents';
import { ValidationForm } from 'react-bootstrap4-form-validation';
export default function Overview(props) {
    return (
        <ValidationForm onSubmit={props.handleSubmitOverview}>
            <Form.Group as={Col} sm="12">
                <Form.Label htmlFor="expect">What to Expect</Form.Label>
                <Form.Control plaintext readOnly value={(props.data && props.data.expectations)|| ''} />

            </Form.Group>
            <Form.Group as={Col} sm="12">
                <Form.Label htmlFor="info">Additional Information</Form.Label>
                <Form.Control plaintext readOnly value={(props.data && props.data.additionalInfo) || ''} />

            </Form.Group>

        </ValidationForm>
    )
}
